        <CampDirectoryVue v-show="show.camp_directory"  :teams="teams" :SIGN_CAMP="SIGN_CAMP"/>
        
        <!-- 加载界面 -->
        <LoadingPanelVue v-show="show.loading"/>
    </div>
</template>

<script>
import all_warriors from '../../mixin/warrior/warriors'//获取所有武将
import Warrior from '../../mixin/structure/WarriorBattle'
import skills from '../../mixin/warrior/skills'//获取战法

//导入战报面板
import BattleResultVue from './BattleResult.vue'
//导入战法面面板
import BattleTacticsVue from './BattleTactics.vue'
//导入战报数据结构
import Report from '../../mixin/structure/Report'
//导入战法详情面板
import TacticsInfoVue from './TacticsInfo.vue'
//导入阵营加成板块
import TeamCampVue from './TeamCamp.vue'
//导入阵容目录板块
import CampDirectoryVue from './CampDirectory.vue'
//导入加载板块
import LoadingPanelVue from './LoadingPanel.vue'
export default {
    name : 'SimBattle',
    mixins:[all_warriors,skills],
    components :{
        BattleResultVue,
        BattleTacticsVue,
        TacticsInfoVue,
        TeamCampVue,
        CampDirectoryVue,
        LoadingPanelVue
    },
    data(){
        return{
            show:{
                result : false,
                attribute : false,
                isUnloadWarrior : false,
                tactics : false,
                tactics_info : false,
                camp_directory : false,
                loading : false,
            },
            teams:[
                //我的队伍
                {} =  [
                    {} = new Warrior(2,0,this),
                    {} = new Warrior(1,0,this),
                    {} = new Warrior(0,0,this),//前锋
                ],
                //敌方的队伍
                {} = [
                    {} = new Warrior(2,1,this),//前锋
                    {} = new Warrior(1,1,this),
                    {} = new Warrior(0,1,this),
                ]
            ],
            //外部加成表
            EXTERNAL_ADDITION_TABLE:{
                barracks : 20,//兵营等级
                //四大营等级
                plus_ad :10,//尚无营
                plus_de : 10,//铁壁营
                plus_speed : 10,//疾风营
                plus_tact : 10,//军机营
            },

            //对局结果表
            RESULT_TABLE:{
                winner : 0,//获胜者的队伍下标
                all_turns : 0,//对局的总持续回合数
            },
            //战报堆
            REPORT_HEAP:[
                {} = new Report()
            ],
            //索引
            index:{
                team : 0,//队伍数组下标
                warrior:0,//武将的数组下标
            },

            SIGN_CAMP : 0,
            SIGN_WARRIOR_INDEX :0,//标记当前索引，表示配置的武将放置位置

            refresh_img : true,

            game_time:1,//游戏回合数
            //安装战法时的武将
            LoadTactics : {
                t_index : 0,
                w_index : 0,
            },

            report_review  : false,

            //当前选择的战法
            current_tactics :{
                name : '攻其不备',
                quality : 'S',
                main_type : 'command',//战法主要类型
                second_type : '',//第二战法类型
                third_type : '',//第三类型
                info : '战斗开始后前3回合，使我军群体受到攻击和策略攻击时的伤害降低30.0%（受谋略属性影响）',
                odd : 100,//战法发动几率
                att : 5,//战法距离
                target_type : '',//目标类型
                target_number : 2,//作用目标数量
                arms : ['骑','弓','步'],//战法兵种范围
                wait:0,//战法等待时间
                statistics : {
                    damage : 0,
                    cure : 0,
                    times : 0,
                },//战法统计
                //战法释放函数
                action(self,vue){
                    
                    //发动战法，统计修改
                    this.statistics.times += 1;
                }
            },
            current_tactics_id : 0,

           
        }
    },
    methods:{
        //打开战法界面配置战法
        ConfigTacticsPanel(unload,id,t_index,w_index,target){
            this.LoadTactics.t_index = t_index;
            this.LoadTactics.w_index = w_index;
            if(unload){
                this.current_tactics = target;
                this.current_tactics_id = id;
                //说明该战法是武将的主战法，不可以卸载
                this.TacticsInfoPanel(true);

            }else{
                if(this.teams[this.LoadTactics.t_index][this.LoadTactics.w_index].TACTICS_STACK.length < 3){
                    //确定安装的武将
                    this.show.tactics = true;
                }
            }
        },  
        //切换兵种
        SwitchAdvancedArms(index){
            this.teams[this.index.team][this.index.warrior].current_advanced = this.teams[this.index.team][this.index.warrior].advanced_arms[index];
        },
        //调整武将的属性点
        VueAdjustAttrIndex(type,value,empty){
            this.teams[this.index.team][this.index.warrior].DistributeAttrIndex(type,value,empty);
        },
        //调整武将红度
        AdjustWarriorStar(value){
            if(value){
                if(this.teams[this.index.team][this.index.warrior].red < 5){
                    this.teams[this.index.team][this.index.warrior].red ++;
                }else{
                    return;
                }   
            }else{
                if(this.teams[this.index.team][this.index.warrior].red > 0){
                    this.teams[this.index.team][this.index.warrior].red --;
                }else{
                    return;
                }
            }
            //武将的属性点变化更新
            this.teams[this.index.team][this.index.warrior].CountAttrIndex();
            this.teams[this.index.team][this.index.warrior].DistributeAttrIndex('ad',0);
        },
        //调整武将等级
        AdjustWarriorLevel(up){
            if(up){
                if(this.teams[this.index.team][this.index.warrior].level < 50){
                    this.teams[this.index.team][this.index.warrior].level += 10
                }else{
                    return;
                }
            }else{
                if(this.teams[this.index.team][this.index.warrior].level > 20){
                    this.teams[this.index.team][this.index.warrior].level -= 10
                }else{
                    return;
                }
            }
            //等级变化
            this.teams[this.index.team][this.index.warrior].AdjustWarriorLevel(this.teams[this.index.team][this.index.warrior].level);
            //武将的属性点变化更新
            this.teams[this.index.team][this.index.warrior].CountAttrIndex();
            this.teams[this.index.team][this.index.warrior].DistributeAttrIndex('ad',0);
        },
        //切换队伍
        SwitchTeam(item){
            if(item == 'my'){
                this.$refs.select_my_team.classList.add('current');
                this.$refs.select_enemy_team.classList.remove('current');
                this.$refs.visual_container.style.left = 0;
                this.SIGN_CAMP = 0;
            }else{
                this.$refs.select_my_team.classList.remove('current');
                this.$refs.select_enemy_team.classList.add('current');
                this.$refs.visual_container.style.left = -100 + 'vw';
                this.SIGN_CAMP = 1;
            }
        },
        
        //配置武将面板
        ConfigWarrior(item,index,t_index){
            if(item.name == ''){
                this.$refs.warrior_config_panel.classList.remove('active');
                this.SIGN_WARRIOR_INDEX = index
            }else{
                //修改索引对象
                this.index = {
                    team : t_index,
                    warrior : index
                }
                //打开详细设置面板
                this.show.attribute = true;
            }
        },
        
        //配置战法
        ConfigSkills(){
            
        },
        //打印队伍状态
        OptStatus(){
            for(let i =0;i<2;i++){
                for(let j=0;j<3;j++){
                    if(this.teams[i][j].name == ''){
                        //说明有武将美装上去
                        this.$bus.$emit('alert','队伍没有配置完全');
                        return;
                    }else{
                        
                    }
                }
            }
        },
        //打开或者关闭战法面板
        OpenCloseTacticsPanel(status){
            if(status){
                this.show.tactics = true;
            }else{
                this.show.tactics = false;
            }
        },

        //将武将配置到对应的空槽中
        SelectThisWarrior(item){
            //在我的队伍的对应位置上添加武将
            this.teams[this.SIGN_CAMP][this.SIGN_WARRIOR_INDEX].GetAttribution(item);
            //初始化武将等级50
            this.teams[this.SIGN_CAMP][this.SIGN_WARRIOR_INDEX].AdjustWarriorLevel(50);
            //武将的属性点变化
            this.teams[this.SIGN_CAMP][this.SIGN_WARRIOR_INDEX].CountAttrIndex();
            this.teams[this.SIGN_CAMP][this.SIGN_WARRIOR_INDEX].DistributeAttrIndex('ad',0);
            //装填武将主战法
            this.teams[this.SIGN_CAMP][this.SIGN_WARRIOR_INDEX].LoadingMainTactics();
            
            setTimeout(() => {
                this.refresh_img = false;
                this.refresh_img = true;
                
            },10);
            this.$refs.warrior_config_panel.classList.add('active');
        },
        //卸载武将出队伍面板
        UnloadWarrior(){
            this.teams[this.index.team][this.index.warrior].ClearWarriorData(this.teams[this.index.team][this.index.warrior].position,this.teams[this.index.team][this.index.warrior].belong);
            this.show.isUnloadWarrior = false;
            this.show.attribute = false;
            this.report_review = false;

        },
        //卸载一队的武将
        UnloadTeamAllWarriors(team){
            for(let i=0;i<3;i++){
                this.teams[team][i].ClearWarriorData(this.teams[team][i].position,this.teams[team][i].belong);
            }
            this.report_review = false;
        },
        //武将安装额外战法
        LoadTacticsIntoWarrior(t){
            if(this.teams[this.LoadTactics.t_index][this.LoadTactics.w_index].TACTICS_STACK.length < 3){
                this.teams[this.LoadTactics.t_index][this.LoadTactics.w_index].TACTICS_STACK.push(t);
            }else{
                this.$bus.$emit('alert',"战法已配置满")
            }
        },
        //卸载战法
        UnloadTactics(){
            if(this.current_tactics_id == 1){
                if(this.teams[this.LoadTactics.t_index][this.LoadTactics.w_index].TACTICS_STACK.length == 2){
                    this.teams[this.LoadTactics.t_index][this.LoadTactics.w_index].TACTICS_STACK.pop();
                }
                else{
                    let temp = this.teams[this.LoadTactics.t_index][this.LoadTactics.w_index].TACTICS_STACK[2];
                    for(let i=0;i<2;i++){
                        this.teams[this.LoadTactics.t_index][this.LoadTactics.w_index].TACTICS_STACK.pop();
                    }
                    this.teams[this.LoadTactics.t_index][this.LoadTactics.w_index].TACTICS_STACK.push(temp);
                }
            }
            else if(this.current_tactics_id == 2){
                this.teams[this.LoadTactics.t_index][this.LoadTactics.w_index].TACTICS_STACK.pop();
            }
        },
        //打开或关闭战法详情面板
        TacticsInfoPanel(open){
            if(open){
                this.show.tactics_info = true;
            }
            else{
                this.show.tactics_info = false;
            }
        },
        //战报回顾
        ReportReview(){
            if(this.report_review){
                this.show.result = true;
            }
            else{
                this.$bus.$emit('alert','无战报回顾')
            }
        },
        //打开或关闭阵容目录面板
        CampDirectoryPanel(open){
            if(open){
                this.show.camp_directory = true;
            }
            else{
                this.show.camp_directory = false;
            }
        },  
        //导入阵容
        ImportDefinitionCamp(camp){
            //red_arr是红度数组

            for(let i=0;i<3;i++){
                let item = "";
                // console.log("w",this.all_warriors);
                //找到对应的武将在武将表中的位置
                for(let j=0;j<this.all_warriors.length;j++){
                    if(camp[i].id == this.all_warriors[j].id){
                        item = this.all_warriors[j];
                        break;
                    }
                }
                this.teams[this.SIGN_CAMP][i].ClearWarriorData(this.teams[this.SIGN_CAMP][i].position,this.teams[this.SIGN_CAMP][i].belong)
                this.teams[this.SIGN_CAMP][i].GetAttribution(item);
                this.teams[this.SIGN_CAMP][i].AdjustWarriorLevel(50);
                this.teams[this.SIGN_CAMP][i].CountAttrIndex();
                this.teams[this.SIGN_CAMP][i].DistributeAttrIndex('ad',0);
                this.teams[this.SIGN_CAMP][i].LoadingMainTactics();
                for(let j=1;j<3;j++){
                    let tactics = "";
                    if(camp[i].TACTICS_STACK[j].type == "command"){
                        for(let k=0;k<this.command_skills.length;k++){
                            if(camp[i].TACTICS_STACK[j].name == this.command_skills[k].name){
                                tactics = this.command_skills[k].tactics;
                                break;
                            }
                        }
                    }
                    else if(camp[i].TACTICS_STACK[j].type == "passive"){
                        for(let k=0;k<this.passive_skills.length;k++){
                            if(camp[i].TACTICS_STACK[j].name == this.passive_skills[k].name){
                                tactics = this.passive_skills[k].tactics;
                                break;
                            }
                        }
                    }
                    else if(camp[i].TACTICS_STACK[j].type == "active"){
                        for(let k=0;k<this.active_skills.length;k++){
                            if(camp[i].TACTICS_STACK[j].name == this.active_skills[k].name){
                                tactics = this.active_skills[k].tactics;
                                break;
                            }
                        }
                    }
                    else if(camp[i].TACTICS_STACK[j].type == "combo"){
                        for(let k=0;k<this.combo_skills.length;k++){
                            if(camp[i].TACTICS_STACK[j].name == this.combo_skills[k].name){
                                tactics = this.combo_skills[k].tactics;
                                break;
                            }
                        }
                    }
                    this.teams[this.SIGN_CAMP][i].TACTICS_STACK.push(tactics);
                    
                }
                
            }

            setTimeout(() => {
                this.refresh_img = false;
                this.refresh_img = true;
                
            },10);
            this.CampDirectoryPanel(false);
        },
        //记载武将的红度
        UpdateWarriorsRed(red_arr){
            for(let i=0;i<this.teams[this.SIGN_CAMP].length;i++){
                this.teams[this.SIGN_CAMP][i].red = red_arr[i];
            }
        },
        
        

        //----------------------------------------------战斗start----------------------------------------------
        //开始战斗函数
        BeginFight(){
            this.RemakeWarriorStatus(false);
            this.report_review = true;
            let res = this.InitWarriors();//战前初始化
            
            if(!res){
                this.$bus.$emit('alert','队伍没有配置完全');
            }else{
                this.show.loading = true;
                setTimeout(() => {
                    this.show.loading = false;
                }, 5000);
                //战斗即将开始，武将的基础面板数据均被加载到它自己身上，接下来依次执行武将的被动战法，指挥战法的准备
                setTimeout(()=>{
                    this.ExecuteWholeGame();
                    
                },100);
            }
        },
        //对局初始化，对武将进行一次初始化
        InitWarriors(){
            //战报生成
            let report = new Report();
            report.InitSysMsg('sys',"双方阵容")
            this.REPORT_HEAP.push(report);
            for(let i =0;i<2;i++){
                for(let j=0;j<3;j++){
                    //初始化四大营设置
                    this.teams[i][j].InitExternalAddition(this.EXTERNAL_ADDITION_TABLE);
                    //初始化属性点分配，加载到武将上
                    this.teams[i][j].LoadingPlusAttr();
                    
                    
                    
                    

                    //初始化武将属性战报
                    let report = new Report();
                    let position_str = '';
                    if(this.teams[i][j].position == 0){
                        position_str = '前锋'
                    }else if(this.teams[i][j].position == 1){
                        position_str = '中军'
                    }else{
                        position_str = '大营'
                    }
                    report.InitVersaReport(this.teams[i][j],0,`${position_str}【${this.teams[i][j].name}】等级${this.teams[i][j].level}，兵力${this.teams[i][j].current_attr.hp}`);
                    this.REPORT_HEAP.push(report);

                    if(this.teams[i][j].name == ''){
                        //说明有武将没装上去
                        return false;
                    }
                }
            }
            this.teams[0][0].InitExternalFunction();
            //阵营加成
            for(let i=0;i<2;i++){
                for(let j=0;j<3;j++){
                    
                    //加载阵营
                    this.teams[i][j].InitCampAddition();
                    //加载兵种加成
                    this.teams[i][j].InitArmsAddition();
                    //称号加成
                    if(j == 0){
                        this.teams[i][j].InitDesignation(false);
                    }
                    //加载兵种的特性
                    this.teams[i][j].InitArmsBaseAttr();

                    //载入武将初始化后的原始数据
                    this.teams[i][j].InitOriginAttr();


                }
            }

            //被动战法生效战报
            for(let k=0;k<2;k++){
                for(let j = 0;j<3;j++){
                    //填充战报
                    for(let i=0;i<this.teams[k][j].TACTICS_STACK.length;i++){
                        //如果该战法的类型为passive，那么就执行action函数
                        if(this.teams[k][j].TACTICS_STACK[i].main_type == 'passive'){
                            let report_1 = new Report();
                            report_1.InitVersaReport(this.teams[k][j],0,`的战法【${this.teams[k][j].TACTICS_STACK[i].name}】生效！`);
                            this.REPORT_HEAP.push(report_1);
                            //执行武将的被动战法
                            this.teams[k][j].TACTICS_STACK[i].action(this.teams[k][j],this);
                        }
                    }
                }
            }

            //指挥战法生效
            this.CommandTacticsState();
            return true;
        },
        //指挥战法读取阶段
        CommandTacticsState(){
            let arr = [];
            for(let i=0;i<2;i++){
                for(let j = 0;j<3;j++){
                    //遍历到武将的战法栈
                    for(let k=0;k<this.teams[i][j].TACTICS_STACK.length;k++){
                        if(this.teams[i][j].TACTICS_STACK[k].main_type == 'command'){
                            arr.push(this.teams[i][j]);//将这些有指挥武将统一放到一个数组里
                            break;
                        }
                    }
                }
            }
            if(arr.length == 0){
                //说明没有带有指挥战法的武将
                return;
            }
            //系统战报
            let report = new Report();
            report.InitSysMsg('sys',"准备阶段")
            this.REPORT_HEAP.push(report);

            let flag = 0;
            while(flag < 8 && arr.length > 0){
                let fast = -1;//最快速度值
                let fast_id = -1;//对应的武将的数组下标
                for(let i =0;i<arr.length;i++){
                    if(arr[i].origin_attr.speed > fast){
                        //该武将速度快过最快数值
                        fast_id = i;
                        fast = arr[i].origin_attr.speed;
                    }else if(arr[i].origin_attr.speed == fast){
                        let rand = Math.floor(Math.random()*2);
                        if(rand == 1){
                            fast_id = i;
                            fast = arr[i].origin_attr.speed;
                        }
                    }
                }
                for(let i=0;i<arr[fast_id].TACTICS_STACK.length;i++){
                    if(arr[fast_id].TACTICS_STACK[i].main_type == 'command'){
                        let report = new Report();
                        report.InitVersaReport(this.teams[arr[fast_id].belong][Math.abs(arr[fast_id].position - 2)],0,`发动【${arr[fast_id].TACTICS_STACK[i].name}】！`)
                        this.REPORT_HEAP.push(report);
                        //执行指挥战法
                        this.teams[arr[fast_id].belong][Math.abs(arr[fast_id].position - 2)].TACTICS_STACK[i].action(this.teams[arr[fast_id].belong][Math.abs(arr[fast_id].position - 2)],this);
                    }
                }
                arr.splice(fast_id,1);//删除这个已经触发过指挥战法的对象
                flag++;//while循环保护锁
            }
            
        },
        //执行一场对局
        ExecuteWholeGame(){
            let res = true;
            while(this.game_time < 9 && res){
                //填充战报
                let report = new Report();
                report.InitSysMsg('sys',`第 ${this.game_time} 回合开始`)
                this.REPORT_HEAP.push(report);
                
                //执行一回合
                this.ExecuteOneTurn();
                let over = this.CheckTeamsStatus();
                if(over.over){
                    this.RESULT_TABLE = {
                        winner : over.winner,
                        all_turns : this.game_time,
                    }
                    res = false;
                }
                this.game_time++;
                // res = this.CheckResult();//如果有一方大营阵亡，那么res=false，while循环停止，游戏结束
                
            }
            
            this.FinalResult();
            //打开结果面板
            let report = new Report();
            report.InitSysMsg('sys',`游戏结束`)
            this.REPORT_HEAP.push(report);

            this.show.result = true;
            this.$bus.$emit('LoadingBloodTape');

        },
        //执行一个系统回合
        ExecuteOneTurn(){
            let time = 0;
            let flag = true;
            //读每个武将身在的SYS_BEGIN_HEAP堆区
            this.teams[0][0].ConsumeSYS_BEGIN_HEAP();
            while(time < 6 && flag){
                
                //选出速度最快的武将，开始它的回合
                let object = this.FastestWarrior();
                //status为true表示找到最快武将，false表示没有武将符合条件，本回合结束
                if(object.status){
                    let target = this.teams[object.team_id][object.warrior_id]
                    //武将开始行动
                    target.StartAction();
                    //就要检测行动者有没有死亡
                    let over1 = this.IsInitiatorDead(target);
                    //行动者阵亡,如果行动者是大营武将，则回合直接结束，游戏结束
                    if(over1 && target.position == 2){
                        let report = new Report();
                        report.InitVersaReport(target,1,"兵力不足，无法再战");
                        this.REPORT_HEAP.push(report);
                        flag = false;
                    }
                    else if(over1){
                        let report = new Report();
                        report.InitVersaReport(target,1,"兵力不足，无法再战");
                        this.REPORT_HEAP.push(report);
                        target.isAttacked = true;
                    }
                    //没有阵亡
                    else{
                        let confusion = this.teams[object.team_id][object.warrior_id].IsConfusion();
                        //没有被混乱
                        if(!confusion || this.teams[object.team_id][object.warrior_id].IsInsight()){
                            if(this.teams[object.team_id][object.warrior_id].IsInsight() && confusion){
                                let report = new Report();
                                report.InitVersaReport(target,1,"处于洞察状态，免疫混乱");
                                this.REPORT_HEAP.push(report);
                            }
                            //发动主动战法
                            let hesitate = target.IsHesitate();
                            if(!hesitate || target.IsInsight()){
                                if(target.IsInsight() && hesitate){
                                    let report = new Report();
                                    report.InitVersaReport(target,1,"处于洞察状态，免疫犹豫");
                                    this.REPORT_HEAP.push(report);
                                }
                                //正式发动
                                target.LaunchActiveTactics();
                                //发动技能后检查对局是否结束
                                let over2 = this.IsOverGame();
                                if(over2){
                                    return true;
                                }

                            }else{
                                let report = new Report();
                                report.InitVersaReport(target,1,"处于犹豫状态，无法施放主动战法");
                                this.REPORT_HEAP.push(report);
                            }
                            
                            let un_attack = target.IsNoneAttack();
                            //没被怯战
                            if(!un_attack || target.IsInsight()){
                                if(target.IsInsight() && un_attack){
                                    let report = new Report();
                                    report.InitVersaReport(target,1,"处于洞察状态，免疫怯战");
                                    this.REPORT_HEAP.push(report);
                                }
                                //武将发动攻击,可能造成敌方大营阵亡
                                let over = target.LaunchAnAttack();
                                if(over){
                                    flag = false;
                                    return true;//大营死亡，返回false
                                }
                                
                                let double = this.teams[object.team_id][object.warrior_id].IsDoubleAttack()
                                //发动连击
                                if(double){
                                    let report_1 = new Report();
                                    report_1.InitVersaReport(this.teams[object.team_id][object.warrior_id],1,`执行两次攻击`);
                                    this.REPORT_HEAP.push(report_1);
                                    let over = this.teams[object.team_id][object.warrior_id].LaunchAnAttack();
                                    if(over){
                                        flag = false;
                                        return true;//大营死亡，返回false
                                    }
                                }
                            }
                            //被怯战
                            else{
                                this.teams[object.team_id][object.warrior_id].isAttacked = true;
                                let report = new Report();
                                report.InitVersaReport(this.teams[object.team_id][object.warrior_id],1,"处于怯战状态，无法进行普通攻击");
                                this.REPORT_HEAP.push(report);
                            }
                        }
                        else{
                            this.teams[object.team_id][object.warrior_id].isAttacked = true;
                            let report = new Report();
                            report.InitVersaReport(this.teams[object.team_id][object.warrior_id],1,"处于混乱状态");
                            this.REPORT_HEAP.push(report);
                        }
                    }
                    time ++;
                }else{
                    flag = false;
                }
            }
            //回合结束
            this.TurnOver();
            return false;//正常结束，返回true
        },
        //返回当前本回合最快且还没有进行过攻击的武将的队伍index和位置index
        FastestWarrior(){
            let fastest_belong = 0;//选出本回合内存活武将中速度最快的武将的归属
            let fastest_index = 0;//选出本回合内存活武将中速度最快的武将的索引
            let fastest_number = -1;
            for(let i=0;i<2;i++){
                for(let j=0;j<3;j++){
                    if(this.teams[i][j].IsFirstHead()){
                        this.teams[i][j].current_attr.speed += 10000;
                    }
                }
            }
            //选出最快的武将
            for(let j =0;j < 2;j++){
                for(let i = 0;i<3;i++){
                    if(this.teams[j][i].exist && this.teams[j][i].isAttacked == false){
                        
                        if(fastest_number == -1){
                            fastest_number = this.teams[j][i].current_attr.speed;//将最快速度初始值赋值给第一个武将
                            fastest_index = i;//目标数组下标
                            fastest_belong = j;//目标所属队伍
                        }
                        
                        if(this.teams[j][i].current_attr.speed > fastest_number){
                            fastest_number = this.teams[j][i].current_attr.speed;
                            fastest_index = i;
                            fastest_belong = j;
                        }else if(this.teams[j][i].current_attr.speed == fastest_number){
                            //速度相同的，则有50%的几率替换掉，保证不是左手方先手
                            let rand = Math.floor(Math.random()*2);
                            if(rand == 1){
                                fastest_number = this.teams[j][i].current_attr.speed;
                                fastest_index = i;
                                fastest_belong = j;
                            }
                        }
                    }
                }
            }
            for(let i=0;i<2;i++){
                for(let j=0;j<3;j++){
                    if(this.teams[i][j].IsFirstHead()){
                        this.teams[i][j].current_attr.speed -= 10000;
                    }
                }
            }
            if(fastest_number == -1){
                //说明遍历了所有武将都没有遍历到存活并且没有发动攻击的武将
                let object = {
                    status : false,
                    team_id : 0,
                    warrior_id : 0,
                }
                return object;
            }else{
                let object = {
                    status : true,
                    team_id : fastest_belong,
                    warrior_id : fastest_index,
                }
                return object;
            }
        },
        //回合结束
        TurnOver(){
            for(let i=0;i<2;i++){
                for(let j=0;j<3;j++){
                    //重置武将攻击状态
                    if(this.teams[i][j].exist){
                        this.teams[i][j].isAttacked = false;
                        this.teams[i][j].tacticsTimes = 0;
                    }
                }
            }
        },
        //查看当前队伍状态，返回res对象，over为true表示游戏结束，winner记录获胜方
        CheckTeamsStatus(){
            if(!this.teams[0][0].exist){
                let res = {
                    over : true,
                    winner  : 0,
                }
                return res;
            }
            if(!this.teams[1][0].exist){
                let res = {
                    over : true,
                    winner  : 1,
                }
                return res;
            }
            let res = {
                over : false,
                winner  : -1,
            }
            return res;
        },
        //检查行动者有没有死亡
        IsInitiatorDead(initiator){
            if(initiator.current_attr.hp <= 0){
                return true;
            }
            return false;
        },
        //对局是否结束
        IsOverGame(){
            if(!this.teams[0][0].exist || !this.teams[1][0].exist){
                return true;
            }
            return false;
        },
        //调整最后结果
        FinalResult(){
            for(let i=0;i<2;i++){
                for(let j=0;j<3;j++){
                    if(this.teams[i][j].current_attr.hp < 0){
                        this.teams[i][j].current_attr.hp = 0;
                        this.teams[i][j].exist = false;
                    }
                }
            }
            if(!this.teams[0][0].exist){
                //对局结果表
                this.RESULT_TABLE = {
                    winner : 1,//获胜者的队伍下标
                    all_turns : this.game_time - 1,//对局的总持续回合数
                }
            }
            else if(!this.teams[1][0].exist){
                //对局结果表
                this.RESULT_TABLE = {
                    winner : 0,//获胜者的队伍下标
                    all_turns : this.game_time - 1,//对局的总持续回合数
                }
            }
            else{
                //对局结果表
                this.RESULT_TABLE = {
                    winner : -1,//获胜者的队伍下标
                    all_turns : this.game_time - 1,//对局的总持续回合数
                }
            }
            
        },
        //----------------------------------------------战斗end----------------------------------------------


        //重置武将状态和其他控制
        RemakeWarriorStatus(save_hp){
            //save_hp表示除了兵力不重置，系统战斗相关数据不重置，其他重置
            //属性回溯
            for(let i=0;i<2;i++){
                for(let j=0;j<3;j++){
                    this.teams[i][j].RemakeWarriorAttr(save_hp);
                }
            }
            if(!save_hp){
                //战斗回合数重置
                this.game_time = 1;
                this.RESULT_TABLE = {
                    winner : 0,
                    all_turns :0,
                }
                //清除战报堆
                this.REPORT_HEAP = [
                    {} = new Report()
                ]
            }
        },
        //关闭战报详情页
        CloseBattleResultPage(){
            this.show.result = false;
        },
        //关闭加载效果
        CloseLoadingPanel(){
            this.show.loading = false;
        },
        //把我的配将数据加载到界面上
        LoadMyTeam(camp,red_arr){
            this.ImportDefinitionCamp(camp);
            this.UpdateWarriorsRed(red_arr);
            
        }
    },
    mounted(){
        

        //
        this.$bus.$on('RemakeWarriorStatus',this.RemakeWarriorStatus);

        //关闭战报详情页
        this.$bus.$on('CloseBattleResultPage',this.CloseBattleResultPage);
        //关闭战法面板
        this.$bus.$on('OpenCloseTacticsPanel',this.OpenCloseTacticsPanel);
        //安装战法
        this.$bus.$on('LoadTacticsIntoWarrior',this.LoadTacticsIntoWarrior);
        //战法详情面板
        this.$bus.$on('TacticsInfoPanel',this.TacticsInfoPanel);
        //卸载战法
        this.$bus.$on("UnloadTactics",this.UnloadTactics)
        //卸载一队
        this.$bus.$on('UnloadTeamAllWarriors',this.UnloadTeamAllWarriors);
        //
        this.$bus.$on("CampDirectoryPanel",this.CampDirectoryPanel)
        //导入阵容目录里的阵容
        this.$bus.$on("ImportDefinitionCamp",this.ImportDefinitionCamp);
        //关闭加载效果
        this.$bus.$on("CloseLoadingPanel",this.CloseLoadingPanel);
        //加载我的配将的队伍
        this.$bus.$on('LoadMyTeam',this.LoadMyTeam);

        let w = new Warrior();
        console.log(w);
        
        
    }
}
</script>

