<template>
    <div class="team_camp_addition">
        <div class="camp_tape">
            <div class="none cover bg"></div>
            <div class="bg_wei cover bg" v-if="Show('wei',false,false)"></div>
            <div class="bg_shu cover bg" v-if="Show('shu',false,false)"></div>
            <div class="bg_wu cover bg" v-if="Show('wu',false,false)"></div>
            <div class="bg_qun cover bg" v-if="Show('qun',false,false)"></div>
            <div class="bg_han cover bg" v-if="Show('han',false,false)"></div>
            <div class="bg_jin cover bg" v-if="Show('jin',false,false)"></div>
            
            <!-- 阵 -->
            <div class="box">阵
                <div class="bg_wei cover circle meet" v-if="Show('wei',false,false)">魏</div>
                <div class="bg_shu cover circle meet" v-if="Show('shu',false,false)">蜀</div>
                <div class="bg_wu cover circle meet" v-if="Show('wu',false,false)">吴</div>
                <div class="bg_qun cover circle meet" v-if="Show('qun',false,false)">群</div>
                <div class="bg_han cover circle meet" v-if="Show('han',false,false)">汉</div>
                <div class="bg_jin cover circle meet" v-if="Show('jin',false,false)">晋</div>
            </div>
            <div class="box">
                号
                <div class="meet cover circle" v-if="Show(false,true,false)">号</div>

            </div>
            <div class="box">
                兵
                <div class="meet cover circle" v-if="Show(false,false,'弓')">兵</div>
                <div class="meet cover circle" v-if="Show(false,false,'步')">兵</div>
                <div class="meet cover circle" v-if="Show(false,false,'骑')">兵</div>
                
            </div>
        </div>

        <!-- 清空队伍按钮 -->
        <div class="clear_teams_btn" ref="clear_teams_btn"
        @touchstart="$refs.clear_teams_btn.classList.add('click')"
        @touchend="$refs.clear_teams_btn.classList.remove('click')"
        @click="show.clear = true">清空队伍</div>
        <!-- 自动加点按钮 -->
        <div class="auto_addition_btn" ref="auto_addition_btn"
        @touchstart="$refs.auto_addition_btn.classList.add('click')"
        @touchend="$refs.auto_addition_btn.classList.remove('click')">智能加点</div>
        <!-- 保存阵容 -->
        <div class="save_camp_btn" ref="save_camp_btn"
        @touchstart="$refs.save_camp_btn.classList.add('click')"
        @touchend="$refs.save_camp_btn.classList.remove('click')"
        @click="SaveTeamCamp">保存阵容</div>

        <!-- 卸载武将提示面板 -->
        <div class="alert_panel" v-show="show.clear">
            <div class="mask cover" @click="show.clear = false"></div>
            <transition name="bubble">
            <div class="container" v-show="show.clear">
                <div class="close_btn" ref="close_btn0909" @touchstart="$refs.close_btn0909.classList.add('click')" 
                @touchend="$refs.close_btn0909.classList.remove('click')" @click="show.clear = false"></div>
                <div class="info">
                    卸下 <span style="color:aqua" v-if="SIGN_CAMP == 0">我方</span><span style="color:red" v-if="SIGN_CAMP == 1">敌方</span> 队伍的所有武将
                </div>
                <div class="confirm_btn" ref="confirm_btn" @touchstart="$refs.confirm_btn.classList.add('click')" 
                @touchend="$refs.confirm_btn.classList.remove('click')"
                @click="ClearTeamWarrior">确定</div>
            </div>
            </transition>
        </div>

        <!-- 保存队伍提示面板 -->
        <div class="alert_panel" v-show="show.camp">
            <div class="mask cover" @click="show.camp = false"></div>
            <transition name="bubble">
            <div class="container" v-show="show.camp">
                <div class="close_btn" ref="close_btn0909" @touchstart="$refs.close_btn0909.classList.add('click')" 
                @touchend="$refs.close_btn0909.classList.remove('click')" @click="show.camp = false"></div>
                <div class="info">
                    保存阵容到阵容目录里
                </div>
                <div class="confirm_btn" ref="confirm_btn" @touchstart="$refs.confirm_btn.classList.add('click')" 
                @touchend="$refs.confirm_btn.classList.remove('click')"
                @click="ConfirmSaveCamp">确定</div>
            </div>
            </transition>
        </div>

        <!-- 战报回看 -->
        <!-- <div class="report_review">战报回顾</div> -->
    </div>
</template>

<script>

export default {
    name : 'TeamCamp',
    props : ['teams',"SIGN_CAMP"],
    data(){
        return{
            show : {
                clear : false,
                auto : false,
                camp : false,
            },

            camp_record:[]
        }
    },
    methods:{
        Show(camp,name,arms){
            if(!name && !arms){
                if(camp == "wei"){
                    let num = 0;
                    for(let i= 0;i<3;i++){
                        if(this.teams[this.SIGN_CAMP][i].camp == '魏'){
                            num += 1;
                        }
                    }
                    if(num > 1){
                        return true;
                    }
                    return false;
                }
                if(camp == "shu"){
                    let num = 0;
                    for(let i= 0;i<3;i++){
                        if(this.teams[this.SIGN_CAMP][i].camp == '蜀'){
                            num += 1;
                        }
                    }
                    if(num > 1){
                        return true;
                    }
                    return false;
                }
                if(camp == "wu"){
                    let num = 0;
                    for(let i= 0;i<3;i++){
                        if(this.teams[this.SIGN_CAMP][i].camp == '吴'){
                            num += 1;
                        }
                    }
                    if(num > 1){
                        return true;
                    }
                    return false;
                }
                if(camp == "qun"){
                    let num = 0;
                    for(let i= 0;i<3;i++){
                        if(this.teams[this.SIGN_CAMP][i].camp == '群'){
                            num += 1;
                        }
                    }
                    if(num > 1){
                        return true;
                    }
                    return false;
                }
                if(camp == "han"){
                    let num = 0;
                    for(let i= 0;i<3;i++){
                        if(this.teams[this.SIGN_CAMP][i].camp == '汉'){
                            num += 1;
                        }
                    }
                    if(num > 1){
                        return true;
                    }
                    return false;
                }
                if(camp == "jin"){
                    let num = 0;
                    for(let i= 0;i<3;i++){
                        if(this.teams[this.SIGN_CAMP][i].camp == '晋'){
                            num += 1;
                        }
                    }
                    if(num > 1){
                        return true;
                    }
                    return false;
                }
            }
            if(!camp && !name){
                if(arms == '骑'){
                    let num = 0;
                    for(let i= 0;i<3;i++){
                        let str = this.teams[this.SIGN_CAMP][i].current_advanced.substring(0,1);
                        if(str == 'q'){
                            num += 1;
                        }
                    }
                    if(num > 1){
                        return true;
                    }
                    return false;
                }
                if(arms == '弓'){
                    let num = 0;
                    for(let i= 0;i<3;i++){
                        let str = this.teams[this.SIGN_CAMP][i].current_advanced.substring(0,1);
                        if(str == 'g'){
                            num += 1;
                        }
                    }
                    if(num > 1){
                        return true;
                    }
                    return false;
                }
                if(arms == '步'){
                    let num = 0;
                    for(let i= 0;i<3;i++){
                        let str = this.teams[this.SIGN_CAMP][i].current_advanced.substring(0,1);
                        if(str == 'b'){
                            num += 1;
                        }
                    }
                    if(num > 1){
                        return true;
                    }
                    return false;
                }
            }
            //称号
            if(!camp && !arms){
                if(this.teams[this.SIGN_CAMP][0].name != ""){
                    return this.teams[this.SIGN_CAMP][0].InitDesignation(true);
                }
            }
        },
        //确定清理队伍
        ClearTeamWarrior(){
            this.$bus.$emit('UnloadTeamAllWarriors',this.SIGN_CAMP);
            this.show.clear = false;
        },
        //保存阵容到阵容目录里
        SaveTeamCamp(){
            for(let i=0;i<3;i++){
                if(this.teams[this.SIGN_CAMP][i].name == ""){
                    this.$bus.$emit('alert',"武将配置不完整");
                    return;
                }
                else{
                    if(this.teams[this.SIGN_CAMP][i].TACTICS_STACK.length < 3){
                        this.$bus.$emit('alert',"战法配置不完整");
                        return;
                    }
                }
            }
            this.show.camp = true;
        },
        //确定保存阵容
        ConfirmSaveCamp(){
            this.show.camp = false;
            this.$bus.$emit('CampDirectoryPanel',true);
            this.$bus.$emit('AddNewRecord');
        }
    },
    mounted(){

    }

}
</script>

<style lang="less">
.team_camp_addition{
    position: absolute; left: 0;top: 12vh;
    transform: translateY(83vw);
    width: 100vw;height: 15vw;
    background: url('../../assets/ui/ui (148).png')rgba(24, 26, 26, 0.73)center no-repeat;
    background-size: 250% 100%;
    z-index: 1;
    .camp_tape{
        left: 2.5vw; top: 2.5vw;
        width: 30vw;height: 10vw;
        
        // background: rgb(227, 227, 227);
        // border-radius: 1vw;
        .bg{
            border-radius: 2vw;
            box-shadow: 0 0 5px rgb(39, 38, 38);
        }
        .bg.none{
            background: rgb(33, 32, 32);
        }
        .box{
            float: left;position: relative;
            margin: 1.5vw 0 0 2vw;
            width: 7vw;height: 7vw;
            line-height: 6vw;
            border-radius: 50%;
            font-weight: bold;
            // background: rgba(0, 0, 0, 0.8);
            border: .5vw solid rgb(124, 120, 120);
            box-sizing: border-box;
            color: rgb(170, 170, 170);
        
        .circle{
            border-radius: 50%;
            font-weight: bolder;
            
            // box-shadow: 0 0 5px rgb(40, 40, 40);
        }
        .meet{
            left: -.5vw;top: -.5vw;
            // box-sizing: border-box;
            // background:linear-gradient(to right bottom,black,rgb(164, 66, 4));
            color: rgb(227, 227, 227);
            border: .5vw solid rgb(221, 221, 221);
            // line-height: 8vw;
        }
    }
    }
    .clear_teams_btn,.auto_addition_btn,.save_camp_btn{
        right: 5vw; bottom: -10vw;
        height: 6vw;width: 20vw;
        line-height: 6vw;
        font-weight: bold;
        background: rgb(226, 226, 226);
        color: black;
    }
    .auto_addition_btn{
        right: 29vw;
    }
    .save_camp_btn{
        right: 77vw;
    }
    .clear_teams_btn.click,.auto_addition_btn.click,.save_camp_btn.click{
        transform: scale(.9);
    }
    .alert_panel{
        // z-index: 100;
        .mask{
            width: 100vw;height: 100vh;
            top: -12vh;
            margin: -83vw 0 0 0;
            
        }
    }
    
    
}
</style>