<template>
    <div class="directory_panel">
        <div class="mask cover" @click="Close"></div>
        
        <div class="container">
            <div class="texture"></div>
            <div class="team_container">
                <div class="box" v-for="(r,index) in camp_record" :key="r.index" @click="SelectThisCamp(r,index)">
                    <div class="warrior" v-for="w in r" :key="w.index">
                        <div class="bg_han cover circle" v-if="w.camp == '汉'"></div>
                        <div class="bg_qun cover circle" v-if="w.camp == '群'"></div>
                        <div class="bg_wei cover circle" v-if="w.camp == '魏'"></div>
                        <div class="bg_wu cover circle" v-if="w.camp == '吴'"></div>
                        <div class="bg_shu cover circle" v-if="w.camp == '蜀'"></div>
                        <div class="bg_jin cover circle" v-if="w.camp == '晋'"></div>
                        <img :src="w.img.head" alt="" class="head">
                        <div class="tactics" v-for="t in w.TACTICS_STACK" :key="t.index">
                            <div class="command cover" v-if="t.type == 'command'">{{t.name}}</div>
                            <div class="active cover" v-if="t.type == 'active'">{{t.name}}</div>
                            <div class="combo cover" v-if="t.type == 'combo'">{{t.name}}</div>
                            <div class="passive cover" v-if="t.type == 'passive'">{{t.name}}</div>
                            <div class="quality" v-if="t.quality == 'S'">S</div>
                            <div class="quality" v-if="t.quality == 'A'">A</div>
                            <div class="quality" v-if="t.quality == 'B'">B</div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- 随机应用按钮 -->
            <div class="random_apply_btn" ref="random_apply_btn"
            @touchstart="$refs.random_apply_btn.classList.add('click')"
            @touchend="$refs.random_apply_btn.classList.remove('click')"
            @click="RandomApply">随机应用</div>
            <!-- 清空按钮 -->
            <div class="clear_list_btn" ref="clear_list_btn"
            @touchstart="$refs.clear_list_btn.classList.add('click')"
            @touchend="$refs.clear_list_btn.classList.remove('click')"
            @click="PreClearList">清空列表</div>
        </div>


        <!-- 更多操作 -->
        <div class="confirm_panel" v-show="detail_panel">
            <div class="mask cover" @click="detail_panel = false"></div>
            <div class="panel">
                <div class="close_btn" ref="close_btn2" @touchstart="$refs.close_btn2.classList.add('click')" 
                @touchend="$refs.close_btn2.classList.remove('click')" @click="detail_panel = false"></div>
                <div class="texture">阵 容 预 览</div>
                <div class="warrior_member_assemble">
                    <li v-for="c in current" :key="c.index">
                        <img :src="c.img.big" alt="">
                        <img src="../../assets/background/bg (37).png" alt="" class="cover">
                        <div class="tactics" v-for="t in c.TACTICS_STACK" :key="t.index">
                            <div class="command cover" v-if="t.type == 'command'">{{t.name}}</div>
                            <div class="active cover" v-if="t.type == 'active'">{{t.name}}</div>
                            <div class="combo cover" v-if="t.type == 'combo'">{{t.name}}</div>
                            <div class="passive cover" v-if="t.type == 'passive'">{{t.name}}</div>
                            <div class="quality" v-if="t.quality == 'S'">S</div>
                            <div class="quality" v-if="t.quality == 'A'">A</div>
                            <div class="quality" v-if="t.quality == 'B'">B</div>
                        </div>
                    </li>
                </div>
                <!-- 应用 -->
                <div class="application_btn" ref="application_btn"
                @touchstart="$refs.application_btn.classList.add('click')"
                @touchend="$refs.application_btn.classList.remove('click')"
                @click="ApplyThisCamp">应 用</div>
                <!-- 删除 -->
                <div class="delete_btn" ref="delete_btn"
                @touchstart="$refs.delete_btn.classList.add('click')"
                @touchend="$refs.delete_btn.classList.remove('click')"
                @click="PreDeleteCamp">删 除</div>
            </div>
        </div>

        <!-- 保存队伍提示面板 -->
        <div class="alert_panel" v-show="alert.clear">
            <div class="mask cover" @click="alert.clear = false"></div>
            <transition name="bubble">
            <div class="container" v-show="alert.clear">
                <div class="close_btn" ref="close_btn0909" @touchstart="$refs.close_btn0909.classList.add('click')" 
                @touchend="$refs.close_btn0909.classList.remove('click')" @click="alert.clear = false"></div>
                <div class="info">
                    清空阵容列表数据？
                </div>
                <div class="confirm_btn" ref="confirm_btn" 
                @touchstart="$refs.confirm_btn.classList.add('click')" 
                @touchend="$refs.confirm_btn.classList.remove('click')"
                @click="ClearList">确定</div>
            </div>
            </transition>
        </div>

        <!-- 删除一条记录提示 -->
        <div class="alert_panel" style="z-index: 13;" v-show="alert.delete">
            <div class="mask cover" @click="alert.delete = false"></div>
            <transition name="bubble">
            <div class="container" v-show="alert.delete">
                <div class="close_btn" ref="close_btn34" @touchstart="$refs.close_btn34.classList.add('click')" 
                @touchend="$refs.close_btn34.classList.remove('click')" @click="alert.delete = false"></div>
                <div class="info">
                    删除这条阵容记录？
                </div>
                <div class="confirm_btn" ref="confirm_btn123" 
                @touchstart="$refs.confirm_btn123.classList.add('click')" 
                @touchend="$refs.confirm_btn123.classList.remove('click')"
                @click="DeleteCamp">确定</div>
            </div>
            </transition>
        </div>
    </div>
</template>

<script>
import BattleFormation from '../../mixin/structure/BattleFormation';
export default {
    name : 'CampDirectory',
    props : ['teams',"SIGN_CAMP"],
    data(){
        return{
            alert : {
                clear : false,
                delete : false,
            },
            detail_panel : false,
            camp_record : [],
            current : [
                {} = new BattleFormation(),
                {} = new BattleFormation(),
                {} = new BattleFormation(),
            ],
            current_id : 0,
        }
    },
    methods:{
        Close(){
            this.$bus.$emit('CampDirectoryPanel',false);
        },
        //添加新的记录
        AddNewRecord(){
            let record = [];
            for(let i=0;i<3;i++){
                let target = this.teams[this.SIGN_CAMP][i];
                let warrior = new BattleFormation();
                warrior.GetInfo(target);
                record.push(warrior);
            }
            this.camp_record.unshift(record);
            localStorage.setItem('InfiniteBorderAppCampRecord',JSON.stringify(this.camp_record));
            
        },
        //选择这个阵容进行更多操作
        SelectThisCamp(r,id){
            this.detail_panel = true;
            this.current_id = id;
            for(let i=0;i<3;i++){
                this.current[i].CopyInfo(r[i]);
            }
        },
        //应用这个阵容
        ApplyThisCamp(){
            this.$bus.$emit("ImportDefinitionCamp",this.current);
            this.detail_panel = false;
            this.$bus.$emit('alert',"阵容导入成功！")
        },
        //删除这条队伍记录的提示面板
        PreDeleteCamp(){
            this.alert.delete = true;
        },
        //删除这条队伍记录
        DeleteCamp(){
            this.camp_record.splice(this.current_id,1);
            this.current_id = 0;
            this.alert.delete = false;
            this.detail_panel = false;
            localStorage.setItem('InfiniteBorderAppCampRecord',JSON.stringify(this.camp_record));
        },
        //把数据加载到面板上
        UpdateDataIntoPanel(){
            if(localStorage.getItem('InfiniteBorderAppCampRecord')){
                this.camp_record = JSON.parse(localStorage.getItem('InfiniteBorderAppCampRecord'))
            }
        },
        //随机应用
        RandomApply(){
            let rand = Math.floor(Math.random() * this.camp_record.length);
            this.$bus.$emit("ImportDefinitionCamp",this.camp_record[rand]);
            this.$bus.$emit('alert',"阵容导入成功！")
        },
        //打开清空列表提示面板
        PreClearList(){
            this.alert.clear = true;
        },
        //清空列表
        ClearList(){
            this.camp_record = [];
            localStorage.setItem('InfiniteBorderAppCampRecord',JSON.stringify(this.camp_record));
            this.alert.clear = false;
            this.$bus.$emit('alert',"记录已清空");
        }
    },
    mounted(){
        this.UpdateDataIntoPanel();
        //添加新的记录事件
        this.$bus.$on('AddNewRecord',this.AddNewRecord);

    }
}
</script>

<style lang="less">
.directory_panel{
    left: 0; top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 10;
    .mask{
        background: rgba(26, 26, 26, 0.8);
    }
    .container{
        right: 0; top: 0;
        width: 65vw;height: 100vh;
        background: url('../../assets/background/bg (15).png')white top right no-repeat;
        background-size: cover;
        .texture{
            position: absolute; left: 0;top: 8vh;
            width: 50vw;height: 10vw;
            background: url('../../assets/background/bg (5).png')no-repeat;
            background-size: 100% 100%;
        }
        .team_container{
            left: 0vw; top: 15vh;
            height: 70vh;width: 100%;
            overflow: scroll;
            // background: #000;
            .box{
                position: relative;
                float: left;
                width: 55vw;height: 40vw;
                margin:2vw 0 0 5vw;
                background: rgba(0, 0, 0, 0.15);
                .warrior{
                    position: relative; float: left;
                    width: 15vw;height: 15vw;
                    margin:2vw 0 0 2.5vw;
                    border-radius: 50%;
                    .circle{
                        border-radius: 50%;
                    }
                    .head{
                        position: absolute; left: 50%; top : 50%;
                        transform : translate(-50%,-50%);
                        width: 85%;height: 85%;
                        border-radius: 50%;
                    }
                    .tactics{
                        position: relative;float: left;
                        width: 100%;height: 5vw;
                        
                        
                        color: white;
                        line-height: 5vw;
                        margin:1vw 0 0 0;
                        .quality{
                            right: -1vw; top: -1vw;
                            width: 3vw;height: 3vw;
                            line-height: 3vw;
                            font-weight: bold;
                            font-style: italic;
                            text-shadow: 0 0 .5vw black;
                            color: rgb(254, 242, 204);
                        }
                        .command,.active,.combo,.passive{
                            background: rgb(2, 95, 53);
                            font-size: 2.5vw;
                        }
                        .active{
                            background: rgb(107, 0, 0);
                        }
                        .combo{
                            background: rgb(84, 2, 87);
                        }
                        .passive{
                            background: rgb(5, 54, 100);
                        }
                    }
                    .tactics:nth-of-type(2){
                        margin:17vw 0 0 0;
                    }
                }
            }
        }
        .team_container::-webkit-scrollbar{
            display: none;
        }

        .random_apply_btn{
            bottom: 5vw; left: 8vw;
            width: 25vw;height: 25vw;
            line-height: 25vw;
            color: white;
            background: url('../../assets/ui/ui (1).png')no-repeat;
            background-size: 100% 100%;
        }
        .clear_list_btn{
            bottom: 10vw; left: 34vw;
            width: 22vw;height: 22vw;
            line-height: 22vw;
            background: url('../../assets/ui/ui (2).png')no-repeat;
            background-size: 100% 100%;
            color: white;
        }
        .random_apply_btn.click,.clear_list_btn.click{
            transform: scale(1);
            opacity: .5;
        }
    }

    .confirm_panel{
        top: 0; left: 0;
        width: 100vw;height: 100vh;
        z-index: 11;
        .mask{
            background: rgba(0, 0, 0, 0.85);
        }
        .panel{
            position: absolute; left: 50%; top : 50%;
            transform : translate(-50%,-50%);
            width: 100vw;height: 100vw;
            background: url('../../assets/background/bg (7).png')#434548 bottom right no-repeat;
            background-size: cover;
            .close_btn{
                right: 5vw; top: 6vw;
                width: 7vw;height: 7vw;
                background: url('../../assets/ui/ui (85).png')no-repeat;
                background-size: 100% 100%;
            }
            .texture{
                position: absolute; left: 0;top: 10vw;
                width: 50vw;height: 8vw;
                background: url('../../assets/background/bg (5).png')no-repeat;
                background-size: 100% 100%;
                text-indent: -20vw;
                line-height: 10vw;
                font-size: 3.3vw;
                color: rgb(254, 242, 204);
            }
            .warrior_member_assemble{
                position: absolute; left: 10vw; top : 18vw;
                width: 80vw;height: 40vw;
                // background: #000;
                li{
                    float: left;position: relative;
                    width: 20vw;height: 30vw;
                    margin: 5vw 0 0 5vw;
                    img{
                        position: absolute; left: 0; top : 0;
                        width : 100%; height : 100%;
                    }
                    .tactics{
                        position: relative;
                        width: 20vw; height:5vw;
                        margin: 1.5vw 0 0 0;
                        // background: #000;
                        .quality{
                            right: -1vw; top: -1vw;
                            width: 3vw;height: 3vw;
                            line-height: 3vw;
                            font-weight: bold;
                            font-style: italic;
                            text-shadow: 0 0 .5vw black;
                            color: rgb(254, 242, 204);
                        }
                        .command,.active,.combo,.passive{
                            position: absolute; left: 0; top : 0;
                            width : 100%; height : 100%;
                            line-height: 5vw;
                            background: rgb(0, 73, 40);
                            font-size: 3vw;
                            box-shadow: 0 0 .2vw black;
                        }
                        .active{
                            background: rgb(85, 1, 1);
                        }
                        .combo{
                            background: rgb(67, 0, 70);
                        }
                        .passive{
                            background: rgb(1, 43, 82);
                        }
                    }
                    .tactics:nth-of-type(1){
                        margin: 35vw 0 0 0;
                        
                    }
                }
            }
            .application_btn,.delete_btn{
                bottom: 10vw;left: 25vw;
                width: 20vw;height: 5vw;
                line-height: 5vw;
                background: white;
                color: black;
                font-weight: bold;
            }
            .application_btn{
                left: 55vw;
            }
            
        }
    }
}
</style>