                        <img :src="w.img.big" alt="" class="cover filter_gray" v-show="w.current_attr.hp == 0">
                        <img src="../../assets/ui/ui (60).png" alt="" class="cover" >
                        <ul class="star_list" v-show="w.current_attr.hp != 0">
