                        </div>

                        <!-- 武将位置 -->
                        <div class="warrior_position">
                            <span v-if="w_index == 0">大营</span>
                            <span v-if="w_index == 1">中军</span>
                            <span v-if="w_index == 2">前锋</span>
                        </div>
                    </div>
                    
                </div>
                <div class="result_icon">
                    <!-- <div class="game_time">战斗{{RESULT_TABLE.all_turns}}回合</div> -->
                    <div class="bg_icon1"></div>
                    <div class="text_icon">
                        <img src="../../assets/ui/ui (119).png" alt="" class="cover" v-show="RESULT_TABLE.winner == 0">
                        <img src="../../assets/ui/ui (120).png" alt="" class="cover" v-show="RESULT_TABLE.winner == -1">
                        <img src="../../assets/ui/ui (131).png" alt="" class="cover" v-show="RESULT_TABLE.winner == 1">
                    </div>
                </div>
                <!-- 敌方武将 -->
                <div class="team_container">
                    <div class="troops_groove_right">
                        <div class="blood_red cover" ref="blood_red"></div>
                        <div class="troops_number_red">{{teams[1][0].current_attr.hp + teams[1][1].current_attr.hp + teams[1][2].current_attr.hp}}/
                        <span style="color: gray;">{{teams[1][0].origin_attr.hp + teams[1][1].origin_attr.hp + teams[1][2].origin_attr.hp}}</span></div>
                    </div>
                    <div class="warrior_card_red" v-for="(w,w_index) in teams[1]" :key="w.index">
                        <img :src="w.img.big" alt="" class="cover" v-show="w.current_attr.hp != 0">
                        <img :src="w.img.big" alt="" class="cover filter_gray" v-show="w.current_attr.hp == 0">

                        <img src="../../assets/ui/ui (60).png" alt="" class="cover" >
                        <ul class="star_list" v-show="w.current_attr.hp != 0">
                            <li v-for="s in w.red" :key="s.index"><img src="../../assets/ui/ui (74).png" alt="" class="cover"></li>
                            <li v-for="s in (5-w.red)" :key="s.index"><img src="../../assets/ui/ui (76).png" alt="" class="cover"></li>
                        </ul>
                        <ul class="star_list filter_gray" v-show="w.current_attr.hp == 0">
                            <li v-for="s in w.red" :key="s.index"><img src="../../assets/ui/ui (74).png" alt="" class="cover"></li>
                            <li v-for="s in (5-w.red)" :key="s.index"><img src="../../assets/ui/ui (76).png" alt="" class="cover"></li>
                        </ul>
                        <!-- 觉醒 -->
                        <img src="../../assets/ui/ui (65).png" alt="" class="up_mark" v-show="w.isUp && w.current_attr.hp != 0">
                        <img src="../../assets/ui/ui (84).png" alt="" class="up_mark" v-show="w.isUpWork && w.current_attr.hp != 0">
                        <img src="../../assets/ui/ui (73).png" alt="" class="up_mark" v-show="w.isUp && w.isUpWork && w.current_attr.hp != 0">
                        <img src="../../assets/ui/ui (65).png" alt="" class="up_mark filter_gray" v-show="w.isUp && w.current_attr.hp == 0">
                        <img src="../../assets/ui/ui (84).png" alt="" class="up_mark filter_gray" v-show="w.isUpWork && w.current_attr.hp == 0">
                        <img src="../../assets/ui/ui (73).png" alt="" class="up_mark filter_gray" v-show="w.isUp && w.isUpWork && w.current_attr.hp == 0">
                        
                        <!-- 兵种 -->
                        <div class="arm">
                            <img src="../../assets/ui/ui (81).png" alt="" v-show="!w.isUpArm" class="cover">
                            <img src="../../assets/ui/ui (82).png" alt="" v-show="w.isUpArms" class="cover">
                            <!-- <img src="../../assets/ui/ui (79).png" alt="" v-show="w.arms == '步' && !w.isUpArms" class="icon">
                            <img src="../../assets/ui/ui (70).png" alt="" v-show="w.arms == '骑' && !w.isUpArms" class="icon">
                            <img src="../../assets/ui/ui (62).png" alt="" v-show="w.arms == '弓' && !w.isUpArms" class="icon">

                            <img src="../../assets/ui/ui (71).png" alt="" v-show="w.arms == '骑' && w.isUpArms" class="icon">
                            <img src="../../assets/ui/ui (80).png" alt="" v-show="w.arms == '步' && w.isUpArms" class="icon">
                            <img src="../../assets/ui/ui (63).png" alt="" v-show="w.arms == '弓' && w.isUpArms" class="icon"> -->
                            <img :src="require(`../../assets/arms/${w.current_advanced}.png`)" alt="" v-if="w.name != ''" class="icon">
                            <div class="arm_num">{{w.att}}</div>
                        </div>

                        <div class="camp" v-show="w.camp == '蜀'"><img src="../../assets/ui/ui (75).png" alt="" class="cover"></div>
                        <div class="camp" v-show="w.camp == '吴'"><img src="../../assets/ui/ui (78).png" alt="" class="cover"></div>
                        <div class="camp" v-show="w.camp == '魏'"><img src="../../assets/ui/ui (77).png" alt="" class="cover"></div>
                        <div class="camp" v-show="w.camp == '群'"><img src="../../assets/ui/ui (72).png" alt="" class="cover"></div>
                        <div class="camp" v-show="w.camp == '汉'"><img src="../../assets/ui/ui (64).png" alt="" class="cover"></div>
                        <div class="camp" v-show="w.camp == '晋'"><img src="../../assets/ui/ui (150).png" alt="" class="cover"></div>
                        
                        
                        <div class="cost_img">{{w.cost}}</div>
                        <div class="level">Lv.<span style="color:gainsboro;font-size: 1.9vw;">{{w.level}}</span></div>
                        <div class="name">{{w.name}}</div>

                        <!-- 武将标志 -->
                        <div class="warrior_sign">
                            <img src="../../assets/ui/ui (66).png" alt="" v-show="w.sign == 's2'" class="cover">
                            <img src="../../assets/ui/ui (67).png" alt="" v-show="w.sign == 's3'" class="cover">
                            <img src="../../assets/ui/ui (68).png" alt="" v-show="w.sign == 'sp'" class="cover">
                            <img src="../../assets/ui/ui (69).png" alt="" v-show="w.sign == 'xp'" class="cover">
                        </div>
                        <div class="hp_num">{{w.current_attr.hp}}</div>

                        <!-- 武将位置 -->
                        <div class="warrior_position">
                            <span v-if="w_index == 0">大营</span>
                            <span v-if="w_index == 1">中军</span>
                            <span v-if="w_index == 2">前锋</span>
                        </div>
                    </div>
                    
                </div>

                <!-- 统计按钮 -->
                <div class="statistics_btn" ref="statistics_btn" @touchstart="$refs.statistics_btn.classList.add('click')"
