            </div>
            <!-- 战报堆区 -->
            <div class="report_text">
                <div class="report" v-for="r in REPORT_HEAP" :key="r.index" ref="report">
                    <div class="left_padding" v-show="r.level > 0 && r.level < 3"></div>
                    <div class="left_padding1" v-show="r.level > 1 && r.level < 3"></div>
                    <!-- 系统战报 -->
                    <div class="span_box" v-if="r.type == 'sys'"></div>
                    <div class="sys_text" v-if="r.type == 'sys'">{{r.text1}}</div>
                    <div class="span_box" v-if="r.type == 'sys'"></div>
                    <!-- 初始化属性战报 -->
                    <div class="warrior_head" v-if="r.type == 'info'">
                        <div class="blue_loop" v-show="r.belong == 0"></div>
                        <div class="red_loop" v-show="r.belong == 1"></div>
                        <img :src="r.attacker_img" alt="" class="cover circle">
                    </div>
                    <div class="text" v-if="r.type == 'info'">{{r.text1}}</div>
                    
                    
                    <!-- 普通攻击战报 -->
                    <div class="warrior_head" v-if="r.type == 'attack'">
                        <div class="blue_loop" v-show="r.belong == 0"></div>
                        <div class="red_loop" v-show="r.belong == 1"></div>
                        <img :src="r.attacker_img" alt="" class="cover circle">
                        
                    </div>
                    <!-- 暴走的战报 -->
                    <div class="warrior_head" v-if="r.type == 'ballistic_attack'">
                        <div class="blue_loop" v-show="r.belong == 0"></div>
                        <div class="red_loop" v-show="r.belong == 1"></div>
                        <img :src="r.attacker_img" alt="" class="cover circle">
                        
                    </div>
                    <div class="text" v-if="r.type == 'attack' || r.type == 'ballistic_attack'">对</div>
                    <div class="warrior_head" v-if="r.type == 'attack'">
                        <div class="blue_loop" v-show="r.belong == 1"></div>
                        <div class="red_loop" v-show="r.belong == 0"></div>
                        <img :src="r.receiver_img" alt="" class="cover circle">
                    </div>
                    <!-- 暴走的战报 -->
                    <div class="warrior_head" v-if="r.type == 'ballistic_attack'">
                        <div class="blue_loop" v-show="r.belong == 0"></div>
                        <div class="red_loop" v-show="r.belong == 1"></div>
                        <img :src="r.receiver_img" alt="" class="cover circle">
                    </div>
                    <div class="text" v-if="r.type == 'attack' || r.type == 'ballistic_attack'">发动一次普通攻击</div>
                    <div class="text" v-if="r.type == 'attack' || r.type == 'ballistic_attack'">{{r.text1}}</div>
                    <div class="left_padding" v-if="r.type == 'attack' || r.type == 'ballistic_attack'" style="clear:both;"></div>
                    <div class="warrior_head" v-if="r.type == 'attack'" >
                        <div class="blue_loop" v-show="r.belong == 1"></div>
                        <div class="red_loop" v-show="r.belong == 0"></div>
                        <img :src="r.receiver_img" alt="" class="cover circle">
                        
                    </div>
                    <div class="warrior_head" v-if="r.type == 'ballistic_attack'" >
                        <div class="blue_loop" v-show="r.belong == 0"></div>
                        <div class="red_loop" v-show="r.belong == 1"></div>
                        <img :src="r.receiver_img" alt="" class="cover circle">
                        
                    </div>
                    <div class="text" v-if="r.type == 'attack' || r.type == 'ballistic_attack'">{{r.text2}}</div>
                    
                
                
                </div>
            </div>
        </div>

        

        <!-- 导航栏按钮 -->
        <div class="navigation_lift_btn" ref="navigation_lift_btn"
        @touchstart="$refs.navigation_lift_btn.classList.add('click')"
        @touchend="$refs.navigation_lift_btn.classList.remove('click')"
        @click="NavigateList">{{TransferChineseWords(RESULT_TABLE.all_turns)}}回合</div>

        <!-- 导航栏 -->
        <div class="navigation_panel" v-show="show.navigation">
            <div class="mask cover" @click="CloseNavigateList"></div>
            <div class="navigation_list">
                <li v-for="(l,index) in NAV_HEAP" :key="l.index" ref="navigation_list"
                @touchstart="$refs.navigation_list[index].classList.add('click')"
                @touchend="$refs.navigation_list[index].classList.remove('click')"
                @click="NavLift(index)">
                    <span v-if="index == 0"  >顶 部</span> 
                    <span v-if="index == 1"  >准备阶段</span> 
                    <!-- {{TransferChineseWords(index)}}回合 -->
                    <span v-if="index > 1 && index < 10 && index != NAV_HEAP.length-1">第{{TransferChineseWords(index - 1)}}回合</span>
                    <span v-if="index == NAV_HEAP.length-1">底 部</span>
                </li>
            </div>
        </div>

        <!-- 统计面板 -->
        <StatisticsVue v-show="show.statistics" :teams="teams" />
        

    </div>
</template>

<script>
// import Warrior from '../../mixin/structure/WarriorBattle'
import StatisticsVue from './Statistics.vue';
export default {
    name :'BattleResult',
    props : ['teams','REPORT_HEAP','RESULT_TABLE'],
    components : {
        StatisticsVue
    },
    data(){
        return{
            show : {
                statistics : false,
                navigation : false,
            },
            NAV_HEAP : [],
            view_height : window.innerHeight/100,//对应1vw的px
            view_width : window.innerWidth/100,//对应1vw的px
            // teams:[
            //     //我的队伍
            //     {} =  [
            //         {} = new Warrior(2,0),
            //         {} = new Warrior(1,0),
            //         {} = new Warrior(0,0),//前锋
            //     ],
            //     //敌方的队伍
            //     {} = [
            //         {} = new Warrior(2,1),//前锋
            //         {} = new Warrior(1,1),
            //         {} = new Warrior(0,1),
            //     ]
            // ],
        }
    },
    beforeCreate(){

    },
    methods:{
        //获得对应的战报层数
        GetReportHeight(){
            this.NAV_HEAP = [];
            for(let i=0;i<this.REPORT_HEAP.length;i++){
                if(this.REPORT_HEAP[i].type == 'sys'){
                    this.NAV_HEAP.push(i);
                }
            }
        },
        //导航电梯
        NavLift(position){
            if(position == this.NAV_HEAP.length - 1){
                this.$refs.result_warrior_container.scrollTo(0,1000000);
                this.CloseNavigateList();
            }
            else if(position == 0){
                this.$refs.result_warrior_container.scrollTo(0,0);
                this.CloseNavigateList();
            }
            else{
                
                let distance = this.$refs.report[this.NAV_HEAP[position]].offsetTop;
                distance += 150 * this.view_width;
                // let distance = this.$refs.report[0].offsetTop;
                // let distance = this.$refs.statistics_btn.offsetTop;
                // let distance = 80 * this.view_height
                // console.log("dis",distance);
                // console.log("span",this.view_height)
                // distance += 15 * this.view_height
                
                // let distance = 185 *  * 8 * this.view_width;
                // let distance = (136 + this.NAV_HEAP[position + 3] * 8) * this.view_width;
                this.$refs.result_warrior_container.scrollTo(0,distance);
                this.CloseNavigateList();
            }
        },
        //回合导航
        NavigateList(){
            this.show.navigation = true;
            this.$refs.result_warrior_container.classList.add('filter');
        },
        CloseNavigateList(){
            this.show.navigation = false;
            this.$refs.result_warrior_container.classList.remove('filter');
        },
        //汉子文字
        TransferChineseWords(number){
            if(number == 1){
                return "一"
            }
            else if(number == 2){
                return "二"
            }
            else if(number == 3){
                return "三"
            }
            else if(number == 4){
                return "四"
            }
            else if(number == 5){
                return "五"
            }
            else if(number == 6){
                return "六"
            }
            else if(number == 7){
                return "七"
            }
            else if(number == 8){
                return "八"
            }
        },
        //关闭本界面
        Close(){
            // this.$refs.blood_blue.style.width = 50 + 'vw';
            // this.$refs.blood_red.style.width = 50 + 'vw';
            this.$bus.$emit('RemakeWarriorStatus',true);
            this.$bus.$emit('CloseBattleResultPage');
        },
        LoadingTeamsData(){
            // this.teams = this.$store.state.teams;
            // console.log()
        },
        //初始化血量
        LoadingBloodTape(){
            this.$bus.$emit("CloseLoadingPanel");
            this.GetReportHeight();
            let blue_w = Math.floor(((this.teams[0][0].current_attr.hp + this.teams[0][1].current_attr.hp + this.teams[0][2].current_attr.hp) / (this.teams[0][0].origin_attr.hp + this.teams[0][1].origin_attr.hp + this.teams[0][2].origin_attr.hp)) * 100);
            let red_w = Math.floor(((this.teams[1][0].current_attr.hp + this.teams[1][1].current_attr.hp + this.teams[1][2].current_attr.hp) / (this.teams[1][0].origin_attr.hp + this.teams[1][1].origin_attr.hp + this.teams[1][2].origin_attr.hp)) * 100);
            if(blue_w < 0){
                blue_w = 0
            }else if(blue_w > 100){
                blue_w = 100;
            }
            if(red_w < 0){
                red_w = 0;
            }else if(red_w > 100){
                red_w = 100;
            }
            this.$refs.blood_blue.style.width = (blue_w/100) * 50 + 'vw';
            this.$refs.blood_red.style.width = (red_w/100) * 50 + 'vw';
            setTimeout(()=>{
                this.$refs.result_warrior_container.scrollTo(0,0);
            },20)
            
        },
        //打开统计面板
        OpenStatisticsPanel(status){
            if(status){
                this.show.statistics = true;
            }else{
                this.show.statistics = false;
            }
        }   
    },
    mounted(){
        // this.view_height = ; 
        this.$bus.$on('LoadingTeamsData',this.LoadingTeamsData);

        //初始化血量
        this.$bus.$on('LoadingBloodTape',this.LoadingBloodTape);
        //关闭统计界面
        this.$bus.$on('CloseStatisticsPanel',this.OpenStatisticsPanel)
    }
}
</script>

<style lang="less">
.battle_result_panel{
    position: absolute; left: 0; top : 0;
    width : 100%; height : 100%;
    background: url('../../assets/background/bg (1).jpg') right bottom no-repeat;
    background-size: cover;
    z-index: 10;
    //变灰
    .filter_gray{
        filter: grayscale(1);
    }
    //蓝红环
    .blue_loop,.red_loop{
        position: absolute; left: 50%; top : 50%;
        transform : translate(-50%,-50%);
        width: 110%;height: 110%;
        background: rgb(0, 162, 194);
        border-radius: 50%;
    }
    .red_loop{
        background: rgb(194, 0, 0);
    }
    //关闭阿牛
    .close_result{
        right: 5vw; top: 7vh;
        width: 8vw;height: 8vw;
        background: url('../../assets/ui/ui (85).png')no-repeat;
        background-size: 100% 100%;
    }
    .texture_branch{
        left: 0; top: 7vh;
