<template>
    <div class="account_management_panel">
        <div class="texture_branch">我 的 存 档</div>
        <div class="container" ref="container">
            <div class="item" v-for="(i,index) in InfiniteBorderApp.account" :key="i.index" >
                <div class="current_mask cover" v-if="i.id == InfiniteBorderApp.current_id"></div>
                <div class="current_icon" v-if="i.id == InfiniteBorderApp.current_id"></div>
                <div class="delete_btn" @click="DeleteThisSave(index)" v-show="index != 0"></div>
                <div class="name">{{i.name}}</div>
                <div class="warrior_number">
                    <div class="icon"></div>
                    武将数量:<span> {{i.warriors.length}}</span>
                </div>
                <div class="tactics_number">
                    <div class="icon"></div>
                    战法数量:<span> {{i.tactics.length}}</span>
                </div>
                <div class="info">{{i.info}}</div>
                <div class="edit_btn" ref="edit_btn_new" 
                @click="EditSava(i,index)"></div>
                
            </div>
        </div>
        <div class="add_new_account_btn" ref="add_new_account_btn"
        @touchstart="$refs.add_new_account_btn.classList.add('click')"
        @touchend="$refs.add_new_account_btn.classList.remove('click')"
        @click="AddNewSave">添加新存档</div>


        <!-- 存档信息面板 -->
        <div class="save_info_panel" ref="new_save_panel" v-show="new_panel">
            <!-- <div class="head">存档</div> -->
            <div class="texture_branch1">我 的 存 档</div>
            <div class="close" ref="close_edit_panel"
            @touchstart="$refs.close_edit_panel.classList.add('click')"
            @touchend="$refs.close_edit_panel.classList.remove('click')"
            @click="new_panel = false"
            ></div>

            <input type="text" class="user_name" placeholder="存档名字" ref="user_name" :value="current.name">
            <input type="text" class="user_info" placeholder="备注" ref="user_info" :value="current.info">

            <!-- 存档 -->
            
            <div class="exchange_save_btn" ref="exchange_save_btn"
            @touchstart="$refs.exchange_save_btn.classList.add('click')"
            @touchend="$refs.exchange_save_btn.classList.remove('click')"
            @click="ExchangeSave"
            v-if="InfiniteBorderApp.current_id != current.id">切换到该存档</div>

            <div class="exchange_save_btn dark" v-if="InfiniteBorderApp.current_id == current.id">切换到该存档</div>
            <div class="edit_name_btn" ref="edit_name_btn"
            @touchstart="$refs.edit_name_btn.classList.add('click')"
            @touchend="$refs.edit_name_btn.classList.remove('click')"
            @click="SaveNewName">保存新的名字和介绍</div>




            
            
        </div>

        <!-- 删除存档提示面板 -->
        <!-- 删除存档提示面板 -->
        <div class="import_or_delete_mask" v-show="isAlert">
            <div class="mask cover" @click="isAlert = false"></div>
            <div class="alert_container">
                <div class="close" @click="isAlert = false"></div>
                <div class="info">确认删除存档【{{current.name}}】吗</div>
                <div class="confirm_btn" ref="confirm_select_save" 
                @click="DeleteThisSaveConfirm"  
                @touchstart="$refs.confirm_select_save.classList.add('click')" 
                @touchend="$refs.confirm_select_save.classList.remove('click')">
                确 定</div>


            </div>
            
        </div>
    </div>
</template>

<script>
export default {
    name : 'Account',
    props:['InfiniteBorderApp'],
    data(){
        return{
            new_panel : false,
            isAlert : false,
            current_index : 0,
            current:{
                id : 0,
                name : '',
                warriors : [],
                tactics : [],
                info  :'',
            }
        }
    },
    methods:{
        //添加新存档
        AddNewSave(){
            let app = JSON.parse(localStorage.getItem('InfiniteBorderApp'));
            if(app.account.length < 6){
                let time = new Date();
                let time_str = `${time.getFullYear()}/${time.getMonth()}/${time.getDate()}`;
                let new_save = {
                    name  :'新账号',//账号名称
                    id : Math.floor(Math.random()*9999999),//账号ID
                    creation_time  :time_str,//账号创建时间
                    update_time : '',//账号最近一次更新时间
                    info :'空存档',//账号备注
                    warriors : [],//武将
                    tactics : [],//战法
                    quality :'',//账号等级，根据武将池和战法池来评定，需要手动更新，
                }
                
                app.account.push(new_save);
                localStorage.setItem('InfiniteBorderApp',JSON.stringify(app));
                this.$bus.$emit('UpdateLocalData');

                setTimeout(()=>{
                    this.$refs.container.scrollTo(0,100000);
                },20)
            }else{
                this.$bus.$emit('alert','存档最多六个')
            }
        },
        //删除这个存档
        DeleteThisSave(index){
            this.current_index = index;
            this.current = this.InfiniteBorderApp.account[index];
            this.isAlert = true
        },
        //确定删除
        DeleteThisSaveConfirm(){
            let app = JSON.parse(localStorage.getItem('InfiniteBorderApp'));
            app.account.splice(this.current_index,1);
            if(app.current_id == this.current.id){
                app.current_id = 0;
            }
            
            localStorage.setItem('InfiniteBorderApp',JSON.stringify(app));
            this.$bus.$emit('UpdateLocalData');
            this.isAlert = false;
        },
        //编辑存档
        EditSava(i,index){
            this.current_index = index;
            this.current = i;
            this.new_panel = true;
        },
        //切换存档
        ExchangeSave(){
            let app = JSON.parse(localStorage.getItem('InfiniteBorderApp'));
            app.current_id = this.current.id;
            localStorage.setItem('InfiniteBorderApp',JSON.stringify(app));
            this.$bus.$emit('UpdateLocalData');
            //更新战法界面
            // this.$bus.$emit('UpDataSkillsInterface');
            this.$bus.$emit('alert','切换成功！')
            //刷新界面
            window.location.reload()
            this.new_panel = false;
        },
        //保存新的名字
        SaveNewName(){
            if(this.$refs.user_name.value.length > 8){
                this.$bus.$emit('alert',"名字不能超过8个字符")
                return;
            }
            
            let app = JSON.parse(localStorage.getItem('InfiniteBorderApp'));
            app.account[this.current_index].name = this.$refs.user_name.value;
            app.account[this.current_index].info = this.$refs.user_info.value;
            localStorage.setItem('InfiniteBorderApp',JSON.stringify(app));
            this.$bus.$emit('UpdateLocalData');
            this.$bus.$emit('alert','修改成功！')
            this.new_panel = false;
            // if(this.$refs.user_name.value.length > 10){

            // }
        }
    },
    mounted(){
        setTimeout(()=>{
            console.log(this.InfiniteBorderApp);
        },1000)
    }
}
</script>

<style lang="less">
.account_management_panel{
    position: absolute; left: 0; top : 0;
    width : 100%; height : 100%;
    background: url('../../assets/background/bg (1).jpg')bottom right no-repeat;
    background-size: cover;
    .texture_branch{
        
        left: 0; top: 15vh;
        width: 55vw;height: 12vw;
        line-height: 10vw;
        background: url('../../assets/background/bg (5).png')no-repeat;
        background-size: 100% 100%;
        font-size: 4vw;
        color: rgb(182, 182, 99);
        text-shadow: 0 0 0 2px black;
        text-indent: -20vw;
        transition: all .3s;
    }
    .texture_branch1{
        left: 0; top: 10vh;
        width: 55vw;height: 12vw;
        line-height: 10vw;
        background: url('../../assets/background/bg (5).png')no-repeat;
        background-size: 100% 100%;
        font-size: 4vw;
        color: rgb(182, 182, 99);
        text-shadow: 0 0 0 2px black;
        text-indent: -20vw;
        transition: all .3s;
    }
    
    .import_or_delete_mask{
        position: absolute; left: 0; top : 0;
        width : 100%; height : 100%;
        background: rgba(221, 221, 221, 0.6);
        z-index: 5;
        .alert_container{
            position: absolute; left: 0; top : 50%;
            margin-top: -30vw;
            width : 100%; height : 60vw;
            background: url('../../assets/background/bg (3).png')no-repeat;
            background-size: 100% 100%;
            animation: alert_container .1s ease-out 1;
            @keyframes alert_container {
               0%{
                  transform: scale(.9);
               }
               100%{
                  transform: scale(1);
               }
            }
            .close{
                right: 4vw;top: 15vw;
                width: 6vw; height: 6vw;
                background: url('../../assets/ui/ui (85).png')no-repeat;
                background-size: 100% 100%;
            }
            .info{
                position: absolute; left: 50%; top : 50%;
                transform : translate(-50%,-50%);
                width: 60vw;
                height: 30vw;
                font-size: 4vw;
                text-align: center;
                line-height: 30vw;
                color: rgb(222, 222, 222);
            }
            .confirm_btn,.cancel_btn{
                position: absolute; left: 50%; bottom : 10%;
                margin: 0 0 0 -15vw;
                width: 30vw;
                height: 6vw;
                background: rgb(215, 215, 215);
                line-height: 6vw;
                text-align: center;
                font-size: 2.5vw;
                color: black;
                transition: all .1s;
            }
            .confirm_btn.click,.cancel_btn.click{
                transform: scale(.9);
            }
            .cancel_btn{
                margin: 0 0 0 5vw;
            }
        }
    }
    .container{
        left: 0; top: 15vh;
        margin: 15vw 0 0 0;
        width: 100vw;height: 120vw;
        padding: 2vw 0;
        // background: white;
        overflow-y: scroll;
        overflow-x: hidden;
        .item{
            position: relative;
            margin: 0 0 2vw 5vw;
            width: 90vw;height: 28vw;
            background: url('../../assets/ui/ui\ \(137\).png')no-repeat;
            background-size: 100% 100%;
            box-shadow: 0 0 5px rgb(49, 49, 49);
            animation: emerge .2s linear 1;
            .name{
                left: 1vw; top: 1.3vw;
                width: 40vw; height: 8vw;
                line-height: 8vw;
                // background: rgb(58, 58, 58);
                background: url('../../assets/ui/ui\ \(140\).png')no-repeat;
                background-size: 100% 100%;
            }
            .warrior_number,.tactics_number{
                left: 1vw; bottom: 1.5vw;
                width: 50vw;height: 8vw;
                line-height: 8vw;
                text-align: left;
                text-indent: 9vw;
                // background: #000;
                .icon{
                    left: 0; top: 0;
                    width: 8vw;height: 8vw;
                    background: url('../../assets/ui/ui (138).png')no-repeat;
                    background-size: cover;
                    
                }
                span{
                    color: white;
                }
            }   
            .tactics_number{
                left: 32vw;
                .icon{
                    background: url('../../assets/ui/ui (139).png')no-repeat;
                    background-size: cover;
                }
            }
            .info{
                left: 50%; top: 10vw;
                margin: 0 0 0 -40vw;
                width: 80vw;
                height: 8vw;
                line-height: 8vw;
                overflow: scroll;
            }
            .info::-webkit-scrollbar{
                display: none;
            }
            .edit_btn{
                right: 2vw; bottom: 2vw;
                width: 10vw;height: 10vw;
                background: url('../../assets/ui/ui (143).png')no-repeat;
                background-size: 100% 100%;
            }
            .edit_btn.click{
                transform: scale(.9);
            }
            .delete_btn{
                right: 2vw; top: 2vw;
                width: 5vw;height: 5vw;
                background: url('../../assets/ui/ui (141).png')no-repeat;
                background-size: 100% 100%;
                opacity: .7;
            }
            .current_icon{
                right: 15vw; top: 1vw;
                width: 8vw;height: 11vw;
                background: url('../../assets/ui/ui (142).png')no-repeat;
                background-size: 100% 100%;
            }
            .current_mask{
                box-sizing: border-box;
                border: 1vw solid rgb(255, 255, 140);
                box-shadow: 0 0 10px rgb(255, 255, 140);
            }
        }
        
    }
    .container::-webkit-scrollbar{
        display: none;
    }
    .add_new_account_btn{
        left: 50%; top: 15vh;
        margin: 145vw 0 0 -15vw;
        width: 30vw;height: 8vw;
        line-height: 8vw;
        font-weight: bold;
        background: url('../../assets/ui/ui (10).png')no-repeat;
        background-size: 100% 100%;
        color: black;
    }
    .add_new_account_btn.click{
        transform: scale(.8);
    }
    .save_info_panel{
        left: 0; top: 0;
        width: 100vw;
        height: 100vh;
        min-height: 200vw;
        background: url('../../assets/background/bg (1).jpg')bottom no-repeat;
        background-size: cover;
        z-index: 10;
        animation:save_info_panel .1s linear 1 ;
        @keyframes save_info_panel {
           0%{
              opacity: 0;
           }
           100%{
              opacity: 1;
           }
        }
        .close{
            right: 6vw;top: 10vh;
            width: 7vw; height: 7vw;
            background: url('../../assets/ui/ui (85).png')no-repeat;
            background-size: 100% 100%;
        }
        .close.click{
            transform: scale(.8);
        }
        .user_name,.user_info{
            position: absolute; left: 10vw; top: 45vw;
            width: 80vw;
            height: 10vw;
            background: rgb(67, 63, 63);
            color: white;
            border: 0;
        }
        .user_info{
            top: 60vw;
        }
        .exchange_save_btn,.edit_name_btn{
            position: absolute; left: 10vw; top: 80vw;
            width: 35vw;
            height: 8vw;
            text-align: center;
            font-size: 3vw;
            font-weight: bold;
            line-height: 8vw;
            background: rgb(234, 234, 234);
            color: rgb(51, 48, 48);
        }
        .exchange_save_btn.dark{
            opacity: .5;
        }
        .edit_name_btn{
            left: 55vw;
        }
        .exchange_save_btn.click,.edit_name_btn.click{
            transform: scale(.9);
        }
        

    }
    .new_save_panel.show{
        display: block;
    }
}
</style>