<template>
    <div class="recruit active" ref="recruit">

        <!-- 返回赛季面板选择 -->
        <div class="recruit_return" ref="return_select_packet" @touchstart="$refs.return_select_packet.classList.add('click')"
        @touchend="$refs.return_select_packet.classList.remove('click')" @click="show.recruit = false;frequency = 0;" v-show="show.recruit">返回卡包选择</div>

        <!-- 特效开关 -->
        <div class="special_switch" ref="special_switch" @touchstart="$refs.special_switch.classList.add('click')"
        @touchend="$refs.special_switch.classList.remove('click')" @click="special = !special;$bus.$emit('alert',special?'抽卡效果开启':'抽卡效果关闭')" v-show="!show.recruit">
            <span v-show="!special">开启抽卡特效</span>
            <span v-show="special">关闭抽卡特效</span>
        </div>


        <!-- 赛季选择面板 -->
        <div class="season" v-show="show.recruit == false">

            <!-- 赛季选择 -->
            <div class="season_tape_container" v-show="current_season == '选择赛季'">
                <div class="season_tape_container_item" v-for="(s,s_index) in all_season" :key="s.index" ref="season_tape_container_item"
                @touchstart="$refs.season_tape_container_item[s_index].classList.add('click')"
                @touchend="$refs.season_tape_container_item[s_index].classList.remove('click')"
                @click="SelectThisSeason(s.name,s_index)">
                    <img :src="s.post" alt="">
                    <img :src="s.title" alt="" class="font">
                </div>
            </div>

            <!-- 卡包选择 -->
            <div class="season_tape_container" v-show="current_season != '选择赛季'">
                <div class="card_packets" v-for="(s,s_index) in all_season[current_season_index].card_bags" ref="card_packets" :key="s.index"
                @touchstart="$refs.card_packets[s_index].classList.add('click')"
                @touchend="$refs.card_packets[s_index].classList.remove('click')"
                @click="SelectThisPacket(s_index,s.name)">
                    <img :src="s.post" alt="" class="cover">
                    <img src="../../assets/ui/ui (12).png" alt="" class="name_tape">
                    <div class="name">{{s.name}}</div>
                </div>
            </div>
            <div class="title">
                <div class="topic">{{current_season}}</div>
                <div class="back" ref="return_season_select" @touchstart="$refs.return_season_select.classList.add('click')"
                @touchend="$refs.return_season_select.classList.remove('click')" @click="BackSeasonSelect"></div>
            </div>
        </div>

        <!-- 招募结果面板 -->
        <div class="warrior_box" v-show="show.recruit && recruit_flag">
            <div class="warrior_card" ref="warrior_card" v-for="w in recruit_result" :key="w.index" v-show="show_queue.length == 0">
                <img :src="w.img" alt="" class="warrior_img">
                <img src="../../assets/ui/ui (60).png" alt="" class="warrior_img_5" v-show="w.star == 5">
                <img src="../../assets/ui/ui (59).png" alt="" class="warrior_img_4" v-show="w.star == 4">
                <img src="../../assets/ui/ui (58).png" alt="" class="warrior_img" v-show="w.star == 3">

                <div class="star_list">
                    <div class="star" v-for="s in w.star" :key="s.index">
                        <img src="../../assets/ui/ui (76).png" alt="">
                    </div>
                </div>
                <!-- 兵种 -->
                <div class="arm">
                    <img src="../../assets/ui/ui (81).png" alt="">
                    <img src="../../assets/ui/ui (79).png" alt="" v-show="w.arms == '步'">
                    <img src="../../assets/ui/ui (70).png" alt="" v-show="w.arms == '骑'">
                    <img src="../../assets/ui/ui (62).png" alt="" v-show="w.arms == '弓'">
                </div>
                <div class="camp" v-show="w.camp == '蜀'"><img src="../../assets/ui/ui (75).png" alt="" class="cover"></div>
                <div class="camp" v-show="w.camp == '吴'"><img src="../../assets/ui/ui (78).png" alt="" class="cover"></div>
                <div class="camp" v-show="w.camp == '魏'"><img src="../../assets/ui/ui (77).png" alt="" class="cover"></div>
                <div class="camp" v-show="w.camp == '群'"><img src="../../assets/ui/ui (72).png" alt="" class="cover"></div>
                <div class="camp" v-show="w.camp == '汉'"><img src="../../assets/ui/ui (64).png" alt="" class="cover"></div>

                <img src="../../assets/ui/ui (61).png" alt="" class="cost_img">
                <div class="name">{{w.name}}</div>
            </div>
        </div>

        <!-- 招募按钮 -->
        <div class="recruit_btn" ref="recruit_btn" @touchstart="$refs.recruit_btn.classList.add('click')"
        @touchend="$refs.recruit_btn.classList.remove('click')" @click="Recruit" v-show="show.recruit">招募5次</div>

        <!-- 底部 -->
        <div class="recruit_bottom">
            <!-- 玉符 -->
            <div class="amulet">
                <div class="amulet_icon"></div>
                <div class="add_amulet" @click="AddMyMoneyPanel">+</div>
                {{amulet}}
            </div>
            <!-- 次数 -->
            <div class="recruit_time">招募次数: {{frequency}}</div>
            <!-- 查看招募结果 -->
            <div class="warrior_preview" ref="warrior_preview" @touchstart="$refs.warrior_preview.classList.add('click')"
            @touchend="$refs.warrior_preview.classList.remove('click')" @click="WarriorPreview">武将预览</div>
        </div>

        <!-- 卡池面板 -->
        <div class="card_pool_panel" v-show="show.card_pool">
            <div class="packet_name">
                <div class="text">武将预览</div>
            </div>
            
            <div class="card_pool_panel_container">
                <div class="card" v-for="c in INDEX_TABLE.all_index" :key="c.index">
                    <img :src="all_warriors[c.id].img.normal" alt="" class="cover">
                    <img src="../../assets/ui/ui (56).png" alt="" class="cover">
                    <!-- <div class="warrior_name">{{all_warriors[c.id].name}}</div> -->
                    <div class="times" v-show="c.time > 0">{{c.time}}</div>
                    <!-- <div class="times" v-show="c.time > 0" style="color:rgb(28, 219, 25)">{{c.time}}</div> -->
                </div>
            </div>
            <!-- <div class="consume_money">&nbsp;&nbsp;&nbsp;消耗玉符:{{INDEX_TABLE.use_money}}</div> -->

            <div class="back_btn"
                ref="card_pool_panel_back_btn"
                @touchstart="$refs.card_pool_panel_back_btn.classList.add('click');"
                @touchend="$refs.card_pool_panel_back_btn.classList.remove('click');"
                @click="ReturnRecruit">返 回</div>
        </div>

        <!-- 炫光特效 -->
        <div class="light_animation" v-show="special && show_queue.length != 0" @click="ConfirmOne">
            <div class="shadow3" v-for="(w,w_index) in show_queue" :key="w.w_index"  v-show="w_index == show_queue.length-1 && w.star == 4"></div>
            <div class="shadow4" v-for="(w,w_index) in show_queue" :key="w.w_index"  v-show="w_index == show_queue.length-1 && w.star == 5"></div>
            <div class="card_shadow1" v-for="(w,w_index) in show_queue" :key="w.w_index"  v-show="w_index == show_queue.length-1 && w.star == 4"></div>
            <div class="card_shadow2" v-for="(w,w_index) in show_queue" :key="w.w_index"  v-show="w_index == show_queue.length-1 && w.star == 5"></div>
            <div class="big_warrior_card" v-for="(w,w_index) in show_queue" :key="w.w_index"  v-show="w_index == show_queue.length-1">
                <!-- <div class="shadow3" v-show="w.star == 4"></div>
                <div class="shadow4" v-show="w.star == 5"></div> -->
                <div class="shadow6" v-for="s in 10" :key="s.index" v-show="w.star == 5"></div>
                <div class="shadow7" v-for="s in 10" :key="s.index" v-show="w.star == 4"></div>
                
                <img :src="w.img" alt="" class="warrior_img">
                <img src="../../assets/ui/ui (60).png" alt="" class="warrior_img_5" v-show="w.star == 5">
                <img src="../../assets/ui/ui (59).png" alt="" class="warrior_img_4" v-show="w.star == 4">
                <img src="../../assets/ui/ui (58).png" alt="" class="warrior_img" v-show="w.star == 3">
                <div class="star_list">
                    <div class="star3" v-for="s in w.star" :key="s.index">
                        <img src="../../assets/ui/ui (76).png" alt="">
                    </div>
                </div>
                <!-- 兵种 -->
                <div class="arm">
                    <img src="../../assets/ui/ui (81).png" alt="">
                    <img src="../../assets/ui/ui (79).png" alt="" v-show="w.arms == '步'">
                    <img src="../../assets/ui/ui (70).png" alt="" v-show="w.arms == '骑'">
                    <img src="../../assets/ui/ui (62).png" alt="" v-show="w.arms == '弓'">
                </div>
                <img src="../../assets/ui/ui (61).png" alt="" class="cost_img">
                <div class="camp" v-show="w.camp == '蜀'"><img :src="require('../../assets/ui/ui (75).png')" alt=""></div>
                <div class="camp" v-show="w.camp == '吴'"><img :src="require('../../assets/ui/ui (78).png')" alt=""></div>
                <div class="camp" v-show="w.camp == '魏'"><img :src="require('../../assets/ui/ui (77).png')" alt=""></div>
                <div class="camp" v-show="w.camp == '群'"><img :src="require('../../assets/ui/ui (72).png')" alt=""></div>
                <div class="camp" v-show="w.camp == '汉'"><img :src="require('../../assets/ui/ui (64).png')" alt=""></div>
            
                <div class="name">{{w.name}}</div>
            
            </div>
            
            <div class="shadow5" v-for="(w,w_index) in show_queue" :key="w.w_index"  v-show=" w_index == show_queue.length-1"></div>

            <div class="shadow1" v-for="(w,w_index) in show_queue" :key="w.w_index"  v-show=" w_index == show_queue.length-1"></div>
            <div class="shadow2" v-for="(w,w_index) in show_queue" :key="w.w_index"  v-show=" w_index == show_queue.length-1"></div>
            
            
        </div>

        
            
        <!-- 清除玉符按钮 -->
        <!-- <div class="clear_money_btn" ref="clear_money_btn" @touchstart="$refs.clear_money_btn.classList.add('click')"
            @touchend="$refs.clear_money_btn.classList.remove('click')" @click="ClearMyMoney">清除玉符</div> -->

        <!-- 充值面板 -->
        <div class="recharge" v-show="show.recharge">
            <!-- 充值盒子 -->
            <div class="add_money_panel_container">
                <div class="recharge_box" v-for="(c,c_index) in RECHARGE_TABLE" :key="c.index" ref="recharge_box"
                @touchstart="$refs.recharge_box[c_index].classList.add('click')"
                @touchend="$refs.recharge_box[c_index].classList.remove('click')"
                @click="AddMoney(c.add)">
                    <img src="../../assets/ui/ui (16).png" alt="" v-show="c_index%6 == 0">
                    <img src="../../assets/ui/ui (18).png"  alt="" v-show="c_index%6 == 1">
                    <img src="../../assets/ui/ui (17).png"  alt="" v-show="c_index%6 == 2">
                    <img src="../../assets/ui/ui (19).png"  alt="" v-show="c_index%6 == 3">
                    <img src="../../assets/ui/ui (20).png"  alt="" v-show="c_index%6 == 4">
                    <img src="../../assets/ui/ui (22).png"  alt="" v-show="c_index%6 == 5">
                    <!-- <img src="" alt=""> -->
                    <div class="first_charge" v-show="c.first">首冲</div>
                    <div class="name">{{c.name}}</div>
                </div>
            </div>
            <!-- 返回按钮 -->
            <div class="return_btn" ref="return_btn" @touchstart="$refs.return_btn.classList.add('click')"
                @touchend="$refs.return_btn.classList.remove('click')" @click="show.recharge = false">返&nbsp;&nbsp;&nbsp;回
            </div>
            <!-- 清除玉符按钮 -->
            <div class="clear_amulet_btn" ref="clear_amulet_btn" @touchstart="$refs.clear_amulet_btn.classList.add('click')"
                @touchend="$refs.clear_amulet_btn.classList.remove('click')" @click="ClearMyMoney">清空玉符</div>
        </div>
        
        



        <!-- 音频控件 -->
        <audio src="../../assets/mp4/click.mp3" preload="preload" ref="click_mp3"></audio>
        <audio src="../../assets/mp4/recruitfive.mp3" preload="preload" ref="recruit_five_audio"></audio>
        <audio src="../../assets/mp4/recruitfour.mp3" preload="preload" ref="recruit_four_audio"></audio>

        
    </div>
</template>

<script>
import warriors from '../../mixin/warrior/warriors5'//导入武将数据
import warriors4 from '../../mixin/warrior/warriors4'//导入武将数据
import warriors3 from '../../mixin/warrior/warriors3'//导入武将数据
import all_season from '../../mixin/season/season'
export default {
    name : 'Recruit',
    mixins:[
        all_season,
        warriors,
        warriors4,
        warriors3,],
    data(){
        return{
            amulet : 100000,//玉符
            frequency:0,//招募次数

            current_season:'选择赛季',
            current_packet_name:'',
            current_season_index : 0,
            special : false,
            recruit_flag : true,
            //保底次数
            GUARANTEE_TIME:45,
            
            show : {
                recharge  : false,
                recruit:false,
                card_pool:false,
            },
            //重置表
            RECHARGE_TABLE:[
                {
                    name : '¥648',
                    first:false,
                    add : 8100,
                },
                {
                    name : '¥328',
                    first:false,
                    add : 4100,
                },
                
                {
                    name : '¥198',
                    first:false,
                    add : 2475,
                },
                
                {
                    name : '¥98',
                    first:false,
                    add : 1225,
                },
                
                {
                    name : '¥30',
                    first:false,
                    add : 300,
                },
                {
                    name : '¥6',
                    first:false,
                    add : 60,
                },
                {
                    name : '首冲 ¥648',
                    first:false,
                    add : 12960,
                },
                {
                    name : '首冲 ¥328',
                    first:false,
                    add : 6560,
                },
                
                {
                    name : '首冲 ¥198',
                    first:false,
                    add : 2960,
                },
                
                {
                    name : '首冲 ¥98',
                    first:false,
                    add : 1960,
                },
                
                {
                    name : '首冲 ¥30',
                    first:false,
                    add : 600,
                },
                {
                    name : '首冲 ¥6',
                    first:false,
                    add : 120,
                },

            ],

            INDEX_TABLE:{
                index_sum:0,
                guarantee_times:this.GUARANTEE_TIME,
                use_money:0,
                all_index:[],//该卡包所以武将的索引
                level1:[],
                level2:[],
                level3:[],

                level1_start_index:-1,
                level2_start_index:-1,
                level3_start_index:-1,
            },
            recruit_result:[],
            show_queue:[],

            current_warrior_pool:{
                name : '',
            },
        }
    },
    methods:{
        //展示或关闭本界面
        OpenOrClose(flag){
            // this.$nextTick(()=>{
            //     //flag为真，表示打开本界面
            //     if(flag){
            //         this.$refs.recruit.classList.remove('active');
            //     }else{
            //         this.$refs.recruit.classList.add('active');
            //     }
            // })
            //flag为真，表示打开本界面
            if(flag){
                this.$refs.recruit.classList.remove('active');
            }else{
                this.$refs.recruit.classList.add('active');
            }
        },
        //招募面板确认接收该武将
        ConfirmOne(){
            this.show_queue.pop();
            if(this.show_queue[this.show_queue.length-1]){
                if(this.show_queue[this.show_queue.length-1].star == 5){
                    //this.$refs.recruit_five_audio.play();
                    this.$refs.recruit_five_audio.load();
                    this.$refs.recruit_five_audio.play();
                }else if(this.show_queue[this.show_queue.length-1].star == 4){
                    this.$refs.recruit_four_audio.load();
                    this.$refs.recruit_four_audio.play();
                }
            }

        },
        //清除玉符
        ClearMyMoney(){
            this.amulet = 0;
            this.$bus.$emit('alert','玉符清除成功');
            localStorage.setItem('brawer_my_money',JSON.stringify(this.amulet));
        },
        //从武将预览返回招募界面
        ReturnRecruit(){
            this.show.card_pool = false;
            
        },
        
        //选择这个赛季
        SelectThisSeason(name,index){
            this.current_season = name;
            this.current_season_index = index;
        },
        //选择这个烤包抽卡
        SelectThisPacket(index,name){
            this.recruit_result = [];
            this.current_packet_name = name;

            this.current_warrior_pool = this.all_season[this.current_season_index].card_bags[index];
            //获取武将索引表

            this.INDEX_TABLE ={
                index_sum:0,
                use_money:0,
                all_index:[],
                guarantee_times:this.GUARANTEE_TIME,
                level1:[],
                level2:[],
                level3:[],

                level1_start_index:-1,
                level2_start_index:-1,
                level3_start_index:-1,
            }
            let start = 0;
            for(let i=0;i < this.all_warriors.length;i++){
                if(start >= this.current_warrior_pool.pool.length){
                    i = this.all_warriors.length;
                }
                for(let j = start;j < this.current_warrior_pool.pool.length;j++){
                    if(this.all_warriors[i].id == this.current_warrior_pool.pool[j].id){

                        this.INDEX_TABLE.index_sum += this.all_warriors[i].level;//表的index指数
                        
                        let item = {
                            id : i,
                            time:0
                        }
                        this.INDEX_TABLE.all_index.push(item);//放入武将的索引

                        if(this.all_warriors[i].level == 1){
                            let ob = {
                                id : i,
                                all_index_table_id : -1
                            }
                            this.INDEX_TABLE.level1.push(ob);
                        }else if(this.all_warriors[i].level == 2){
                            let ob = {
                                id : i,
                                all_index_table_id : -1
                            }
                            this.INDEX_TABLE.level2.push(ob);
                        }else{
                            let ob = {
                                id : i,
                                all_index_table_id : -1
                            }
                            this.INDEX_TABLE.level3.push(ob);
                        }
                        start++;
                        break;
                    }
                }
            }
            //修改索引表的数据
            this.INDEX_TABLE.level1_start_index = this.INDEX_TABLE.level1.length * 1 -1;
            this.INDEX_TABLE.level2_start_index = this.INDEX_TABLE.level1.length * 1 + this.INDEX_TABLE.level2.length * 2 -1;
            this.INDEX_TABLE.level3_start_index = this.INDEX_TABLE.level1.length * 1 + this.INDEX_TABLE.level2.length * 2 + this.INDEX_TABLE.level3.length * 3 -1;
            //建立all_index表对应的索引
            for(let i = 0;i < this.INDEX_TABLE.level1.length;i++){
                for(let j=0;j < this.INDEX_TABLE.all_index.length;j++){
                    if(this.INDEX_TABLE.level1[i].id == this.INDEX_TABLE.all_index[j].id){
                        this.INDEX_TABLE.level1[i].all_index_table_id = j;
                    }
                }
            }
            for(let i = 0;i < this.INDEX_TABLE.level2.length;i++){
                for(let j=0;j < this.INDEX_TABLE.all_index.length;j++){
                    if(this.INDEX_TABLE.level2[i].id == this.INDEX_TABLE.all_index[j].id){
                        this.INDEX_TABLE.level2[i].all_index_table_id = j;
                    }
                }
            }
            for(let i = 0;i < this.INDEX_TABLE.level3.length;i++){
                for(let j=0;j < this.INDEX_TABLE.all_index.length;j++){
                    if(this.INDEX_TABLE.level3[i].id == this.INDEX_TABLE.all_index[j].id){
                        this.INDEX_TABLE.level3[i].all_index_table_id = j;
                    }
                }
            }

            this.show.recruit = true;
        },
        //返回赛季选择
        BackSeasonSelect(){
            this.current_season = '选择赛季';
        },
        //武将预览
        WarriorPreview(){
            // this.$refs.check_result.classList.remove('click');
            this.show.card_pool = true;
        },
        //招募
        Recruit(){
            
            this.$refs.click_mp3.play();
            if(this.amulet < 950){
                this.$bus.$emit('alert',`玉符不足`)
                return;
            }else{
                this.amulet -= 950;
                this.recruit_flag = false;
                setTimeout(()=>{
                    this.recruit_flag = true;
                },10);
                this.INDEX_TABLE.use_money += 950;
                //生成5张卡
                this.recruit_result = [];
                for(let i =0;i<5;i++){
                    this.DrawOneCard();
                }
                //如果开启抽卡特效
                if(this.special){
                    setTimeout(()=>{
                        if(this.show_queue[this.show_queue.length-1]){
                            if(this.show_queue[this.show_queue.length-1].star == 5){
                                this.$refs.recruit_five_audio.load();
                                this.$refs.recruit_five_audio.play();
                            }else if(this.show_queue[this.show_queue.length-1].star == 4){
                                this.$refs.recruit_four_audio.load();
                                this.$refs.recruit_four_audio.play();
                            }
                        }
                    },5)
                }else{
                    this.show_queue = [];
                }
                
                this.frequency ++;
                
                localStorage.setItem('brawer_my_money',JSON.stringify(this.amulet));
            }
            
            
        },
        
        //抽一张
        DrawOneCard(){
            if(this.INDEX_TABLE.guarantee_times == 0){
                // console.log('触发保底');
                //5星卡池
                // 选择武将级别
                let rand_level_index= Math.floor(Math.random()*this.INDEX_TABLE.index_sum);
                if(rand_level_index <= this.INDEX_TABLE.level1_start_index){
                    //说明抽出1指数武将
                    let rand = Math.floor(Math.random()*this.INDEX_TABLE.level1.length);
                    let new_warrior = {
                        star : 5,
                        name : this.all_warriors[this.INDEX_TABLE.level1[rand].id].name,
                        camp : this.all_warriors[this.INDEX_TABLE.level1[rand].id].camp,
                        arms : this.all_warriors[this.INDEX_TABLE.level1[rand].id].arms,
                        img : this.all_warriors[this.INDEX_TABLE.level1[rand].id].img.normal
                    }
                    //加次数
                    this.INDEX_TABLE.all_index[this.INDEX_TABLE.level1[rand].all_index_table_id].time++;
                    this.recruit_result.push(new_warrior);
                    this.show_queue.unshift(new_warrior);
                }else if(rand_level_index > this.INDEX_TABLE.level1_start_index &&rand_level_index <= this.INDEX_TABLE.level2_start_index){
                    //说明抽出2指数武将
                    let rand = Math.floor(Math.random()*this.INDEX_TABLE.level2.length);
                    let new_warrior = {
                        star : 5,
                        name : this.all_warriors[this.INDEX_TABLE.level2[rand].id].name,
                        camp : this.all_warriors[this.INDEX_TABLE.level2[rand].id].camp,
                        arms : this.all_warriors[this.INDEX_TABLE.level2[rand].id].arms,
                        img : this.all_warriors[this.INDEX_TABLE.level2[rand].id].img.normal
                    }
                    //加次数
                    this.INDEX_TABLE.all_index[this.INDEX_TABLE.level2[rand].all_index_table_id].time++;
                    this.recruit_result.push(new_warrior);
                    this.show_queue.unshift(new_warrior);
                }else{
                    //说明抽出3指数武将
                    let rand = Math.floor(Math.random()*this.INDEX_TABLE.level3.length);
                    let new_warrior = {
                        star : 5,
                        name : this.all_warriors[this.INDEX_TABLE.level3[rand].id].name,
                        camp : this.all_warriors[this.INDEX_TABLE.level3[rand].id].camp,
                        arms : this.all_warriors[this.INDEX_TABLE.level3[rand].id].arms,
                        img : this.all_warriors[this.INDEX_TABLE.level3[rand].id].img.normal
                    }
                    //加次数
                    this.INDEX_TABLE.all_index[this.INDEX_TABLE.level3[rand].all_index_table_id].time++;
                    this.recruit_result.push(new_warrior);
                    this.show_queue.unshift(new_warrior);
                }
                this.show.result = true;
                this.INDEX_TABLE.guarantee_times = this.GUARANTEE_TIME;//每出一张五星卡就重置保底次数
                return 5;
            }
            let random_select = Math.floor(Math.random()*25);
            if(random_select == 0){
                //5星卡池
                // 选择武将级别
                let rand_level_index= Math.floor(Math.random()*this.INDEX_TABLE.index_sum);
                if(rand_level_index <= this.INDEX_TABLE.level1_start_index){
                    //说明抽出1指数武将
                    let rand = Math.floor(Math.random()*this.INDEX_TABLE.level1.length);
                    let new_warrior = {
                        star : 5,
                        name : this.all_warriors[this.INDEX_TABLE.level1[rand].id].name,
                        camp : this.all_warriors[this.INDEX_TABLE.level1[rand].id].camp,
                        arms : this.all_warriors[this.INDEX_TABLE.level1[rand].id].arms,
                        img : this.all_warriors[this.INDEX_TABLE.level1[rand].id].img.normal
                    }
                    //加次数
                    this.INDEX_TABLE.all_index[this.INDEX_TABLE.level1[rand].all_index_table_id].time++;
                    this.recruit_result.push(new_warrior);
                    this.show_queue.unshift(new_warrior);
                    return 5;//表示抽到5
                }else if(rand_level_index > this.INDEX_TABLE.level1_start_index &&rand_level_index <= this.INDEX_TABLE.level2_start_index){
                    //说明抽出2指数武将
                    let rand = Math.floor(Math.random()*this.INDEX_TABLE.level2.length);
                    let new_warrior = {
                        star : 5,
                        name : this.all_warriors[this.INDEX_TABLE.level2[rand].id].name,
                        camp : this.all_warriors[this.INDEX_TABLE.level2[rand].id].camp,
                        arms : this.all_warriors[this.INDEX_TABLE.level2[rand].id].arms,
                        img : this.all_warriors[this.INDEX_TABLE.level2[rand].id].img.normal
                    }
                    //加次数
                    this.INDEX_TABLE.all_index[this.INDEX_TABLE.level2[rand].all_index_table_id].time++;
                    this.recruit_result.push(new_warrior);
                    this.show_queue.unshift(new_warrior);
                    return 5;//表示抽到5
                }else{
                    //说明抽出3指数武将
                    let rand = Math.floor(Math.random()*this.INDEX_TABLE.level3.length);
                    let new_warrior = {
                        star : 5,
                        name : this.all_warriors[this.INDEX_TABLE.level3[rand].id].name,
                        camp : this.all_warriors[this.INDEX_TABLE.level3[rand].id].camp,
                        arms : this.all_warriors[this.INDEX_TABLE.level3[rand].id].arms,
                        img : this.all_warriors[this.INDEX_TABLE.level3[rand].id].img.normal
                    }
                    //加次数
                    this.INDEX_TABLE.all_index[this.INDEX_TABLE.level3[rand].all_index_table_id].time++;
                    this.recruit_result.push(new_warrior);
                    this.show_queue.unshift(new_warrior);
                }
                this.show.result = true;
                this.INDEX_TABLE.guarantee_times = this.GUARANTEE_TIME;//每出一张五星卡就重置保底次数
                return 5;//表示抽到5
            }else if(random_select >0 && random_select < 7){
                //4星卡池
                let random4 = Math.floor(Math.random()*20);
                let new_warrior = {
                    star:4,
                    camp : this.all_warrior4[random4].camp,
                    name : this.all_warrior4[random4].name,
                    arms : this.all_warrior4[random4].arms,
                    img : this.all_warrior4[random4].img
                }
                this.recruit_result.push(new_warrior);
                this.show_queue.unshift(new_warrior);
                this.INDEX_TABLE.guarantee_times -= 1;
                return 4;//表示抽到4
            }else{
                //3星卡池
                let random3 = Math.floor(Math.random()*11);
                let new_warrior = {
                    star:3,
                    camp : this.all_warrior3[random3].camp,
                    name : this.all_warrior3[random3].name,
                    arms : this.all_warrior3[random3].arms,
                    img : this.all_warrior3[random3].img
                }
                this.recruit_result.push(new_warrior);
                this.INDEX_TABLE.guarantee_times -= 1;
                return 3;//表示抽到3
            }
        },
        //增加玉符
        AddMoney(value){
            this.amulet += value;
            // this.show.recharge = false;
            localStorage.setItem('brawer_my_money',JSON.stringify(this.amulet));
            this.$bus.$emit('alert',`玉符增加+${value}`)
        },
        //打开充值面板
        AddMyMoneyPanel(){
            this.show.recharge = true;
        },
        

    },
    mounted(){
        this.$bus.$on('EnterRecruit',this.OpenOrClose);
        if(localStorage.getItem('brawer_my_money')){
            this.amulet = JSON.parse(localStorage.getItem('brawer_my_money'));
        }
    }
}
</script>

