<template>
    <div class="team_info active" ref="team_info">
        

            
        
    </div>
</template>

<script>
export default {
    name : 'TeamInfo',
    props:['all_warriors'],
    data(){
        return{
            team : [],
            warrior_index : -1,//选中的武将卡槽索引
        }
    },
    methods:{
        //关闭本页面
        Close(){
            this.$bus.$emit('CloseEditPanel');
        },
        //展示或关闭本界面
        OpenOrClose(flag,t){
            //flag为真，表示打开本界面
            if(flag){
                this.team = t;
                this.$refs.team_info.classList.remove('active');
            }else{
                this.$refs.team_info.classList.add('active');
            }
        },
        //选择武将
        SelectWarrior(){
            //let warrior = new TeamWarrior();
            // this.status.isSelectWarriors = true;
            // this.current_warrior = w_index;//把序号赋值给current对象

            // this.$refs.select_warrior_container_mask.classList.add('active');
            // this.$refs.warrior_pool.classList.add('active');
            // this.$refs.select_warrior_container.classList.add('active')
            
        },
        //配置武将
        ConfigWarrior(w,index){
            if(w.name == ''){
                //说明这个地方没有配置武将
                this.warrior_index = index;
                this.$refs.select_warrior_container.classList.add('active');
            }else{
                //对已经配备武将的格子，采用卸载武将策略
            }
            
        },
        //将武将池的武将放入对应武将卡槽
        InsertThisWarrior(target){
            let i = this.team;
            this.team = [];
            this.team.warriors[this.warrior_index] = target;
            this.team = i;
            
            this.$refs.select_warrior_container.classList.remove('active');
            // this.$bus.$emit('IptThisWarrior',target,0,this.warrior_index);
        },
        
        //更新武将队列并关闭武将选择卡池面板
        CloseCloseWarriorSelectPool(){
            this.$refs.select_warrior_container.classList.remove('active');
        }

    },
    mounted(){
        //关闭武将选择卡池
        this.$bus.$on('CloseWarriorSelectPool',this.CloseCloseWarriorSelectPool);


        this.$bus.$on('EditTeam',this.OpenOrClose);
    }
}
</script>

