<template>
    <div class="main">
        <!-- logo -->
        <div class="logo"></div>
        <!-- <img :src="require(`../assets/background/bg (${index+1}).png`)" v-for="(i,index) in 36" :key="i.id" alt="" class="preloading"> -->
        <!-- 导航区 -->
        <div class="main_navigation">
            <ul>
                <!-- 我的武将板块 -->
                <li ref="my_warrior" @touchstart="$refs.my_warrior.classList.add('click')"
                @touchend="$refs.my_warrior.classList.remove('click')" @click="EnterMyWarrior">我的武将</li>
                <!-- 我的战法板块 -->
                <li ref="my_tactics" @touchstart="$refs.my_tactics.classList.add('click')"
                @touchend="$refs.my_tactics.classList.remove('click')" @click="EnterTactics">我的战法</li>
                <!-- 我的队伍板块 -->
                <li ref="my_team" @touchstart="$refs.my_team.classList.add('click')"
                @touchend="$refs.my_team.classList.remove('click')" @click="EnterMyTeam">我的部队</li>
                <!-- 模拟对战 -->
                <li ref="mock_fight" @touchstart="$refs.mock_fight.classList.add('click')"
                @touchend="$refs.mock_fight.classList.remove('click')" @click="EnterSimBattle">模拟对战</li>
            </ul>
        </div>

        <!-- 招募武将入口 -->
        <div class="main_recruit" ref="main_recruit" @touchstart="$refs.main_recruit.classList.add('click')"
        @touchend="$refs.main_recruit.classList.remove('click')"
        @click="EnterRecruit">
            <div class="logo1"></div>
            <div class="text">招 募</div>
        </div>
        <!-- 配置入口 -->
        <div class="settings_entrance" ref="settings_entrance"
        @touchstart="$refs.settings_entrance.classList.add('click')"
        @touchend="$refs.settings_entrance.classList.remove('click')"
        ></div>
        <!-- 账号入口 -->
        <div class="account_btn" ref="account_btn" 
        @touchstart="$refs.account_btn.classList.add('click')"
        @touchend="$refs.account_btn.classList.remove('click')"
        @click="OpenOrCloseAccountPanel(true)"></div>

        



        <!-- 招募界面组件 -->
        <Recruit/>
        <!-- 我的武将界面组件 -->
        <MyWarrior :InfiniteBorderApp="InfiniteBorderApp" />
        <!-- 我的战法组件 -->
        <Tactics v-show="show.tactics"/>
        <!-- 配置组件 -->
        <Settings v-show="show.settings"/>
        <!-- 模拟对战板块 -->
        <SimBattleVue v-show="show.battle"/>
        <!-- 账号管理 -->
        <AccountVue v-show="show.account" :InfiniteBorderApp="InfiniteBorderApp"/>
        

        <!-- 顶部导航栏 -->
        <transition name="down">
        <div class="top_navigation" v-show="current_interface != 'home'">
            <div class="logo2"></div>
            <div class="back_home" ref="back_home" @touchstart="$refs.back_home.classList.add('click')"
            @touchend="$refs.back_home.classList.remove('click')"
            @click="BackHome"></div>
        </div>
        </transition>
        
       
    </div>
</template>

<script>
//导入招募界面组件
import Recruit from './recruit/Recruit.vue'
//导入我的武将组件
import MyWarrior from './warrior/MyWarrior.vue'
//导入我的战法组件
import Tactics from './tactics/Tactics.vue'
//导入配置组件
import Settings from './setting/Settings.vue'
//导入模拟对战模块
import SimBattleVue from './SimBattle/SimBattle.vue'
//导入账号管理板块
import AccountVue from './setting/Account.vue'

export default {
    name : 'Main',
    components:{
    Recruit,
    MyWarrior,
    Tactics,
    Settings,
    SimBattleVue,
    AccountVue
},
    data(){
        return{
            //当前界面
            current_interface : 'home',
            show : {
                tactics : false,
                settings : false,
                battle : false,
                account : false,
            },


            //账户
            InfiniteBorderApp : '',
        }
    },
    methods:{
        
        //返回主界面
        BackHome(){
            this.$nextTick(()=>{
                if(this.current_interface == 'recruit'){
                    this.$bus.$emit('EnterRecruit',false);
                }else if(this.current_interface == 'my_warrior'){
                    this.$bus.$emit('EnterMyWarrior',false);
                    //保存数据,调用MyWarrior组件的保存函数
                    this.$bus.$emit('SaveWarriorData');
                }else if(this.current_interface == 'my_team'){
                    this.$bus.$emit('EnterMyTeam',false);
                    //保存我的队伍
                    this.$bus.$emit('SaveTeamData');
                }else if(this.current_interface == 'my_tactics'){
                    this.show.tactics = false;
                    //保存战法数据到数据库
                    this.$bus.$emit('SaveMyTacticsData');
                }else if(this.current_interface == 'my_settings'){
                    this.show.settings = false;
                }else if(this.current_interface == 'sim_battle'){
                    this.show.battle = false;
                }else if(this.current_interface == 'my_account'){
                    this.show.account = false;
                }
                this.current_interface = 'home'
            })
            
        },
        //进入招募界面
        EnterRecruit(){
            this.$bus.$emit('EnterRecruit',true);
            this.current_interface = 'recruit';
        },
        //进入我的武将界面
        EnterMyWarrior(){
            this.$bus.$emit('EnterMyWarrior',true);
            this.current_interface = 'my_warrior';
        },
        //进入我的队伍界面
        EnterMyTeam(){
            this.$bus.$emit('EnterMyTeam',true);
            this.current_interface = 'my_team';
        },
        //进入战法界面
        EnterTactics(){
            this.show.tactics = true;
            this.current_interface = 'my_tactics'
        },
        //进入配置界面
        EnterSettings(){
            this.show.settings = true;
            this.current_interface = 'my_settings';
        },
        //进入模拟对战界面
        EnterSimBattle(){
            this.show.battle = true;
            this.current_interface = 'sim_battle';
        },
        //进入账号管理界面
        OpenOrCloseAccountPanel(open){
            if(open){
                this.show.account = true;
                this.current_interface = 'my_account'
            }
        },

        //更新local数据
        UpdateLocalData(){
            if(localStorage.getItem('InfiniteBorderApp')){
                this.InfiniteBorderApp = JSON.parse(localStorage.getItem('InfiniteBorderApp'));
            }
        },
        //从我的配将板块跳转到模拟对战
        EnterSimBattleFromMyTeam(){
            this.EnterSimBattle();
        }
        //创建一个存档基础
    },
    
    


    beforeMount(){
        // let count = 0;
        // let imgs = [];//用require的方式添加图片地址，直接添加图片地址的话，在build打包之后会查找不到图片，因为打包之后的图片名称会有一个加密的字符串
        // //预加载背景
        // for(let i=0;i<37;i++){
        //     let img_str = `require("../assets/background/bg (${i}).png")`
        //     imgs.push(img_str);
        // }
        // //预加载武将大图
        // for(let i=1;i<144;i++){
        //     let img_str = `require("../assets/warrior_cards_big/warrior (${i}).jpg")`
        //     imgs.push(img_str);
        // }
        // for (let img of imgs) {
        //     let image = new Image();
        //     image.onload = () => {
        //         count++;
        //     };
        //     image.src = img;
        // }
        if(!localStorage.getItem('InfiniteBorderApp')){
            //如果没有该应用的数据库，那么就立即创建一个
            let InfiniteBorderApp  = {
                current_id : 0,//当前用户的id
                account : [],//账号列表
            }
            //一个默认账户实例
            let default_user = {
                name  :'默认账户',//账号名称
                id : 0,//账号ID
                creation_time  :'----',//账号创建时间
                update_time  :'',//账号最近一次更新时间
                info :'系统默认添加的账户，不可删除',//账号备注
                warriors : [],//武将
                tactics : [],//战法
                quality :'',//账号等级，根据武将池和战法池来评定，需要手动更新，

            }
            InfiniteBorderApp.account.push(default_user);
            localStorage.setItem('InfiniteBorderApp',JSON.stringify(InfiniteBorderApp));
        }
    },
    mounted(){
        //将账户数据读取到app
        if(localStorage.getItem('InfiniteBorderApp')){
            this.InfiniteBorderApp = JSON.parse(localStorage.getItem('InfiniteBorderApp'));
        }
        //更新本地数据
        this.$bus.$on('UpdateLocalData',this.UpdateLocalData);

        this.$bus.$on('EnterSimBattle',this.EnterSimBattleFromMyTeam)
    }
}
</script>

<style lang="less">
.main{
    position: absolute; left: 0; top : 0;
    width : 100vw; height : 100vh;
    background: url('../assets/background/bg (2).jpg')rgb(246, 253, 255) top no-repeat;
    background-size: 100% 100%;
    // cursor: pointer;
    .logo{
        left: 50%; top : 25vh;
        transform : translate(-50%,-50%);
        width: 50vw;height: 17vw;
        background: url('../assets/background/logo.webp')no-repeat;
        background-size: 100% 100%;
    }
    .preloading{
        position: absolute; left: 120vw; top: 0;
        width: 10px;height: 10px;
    }

    //主导航
    .main_navigation{
        left: 0; bottom: 8vw;
        width: 40vw;height: 56vw;
        // background: #000;
        ul{
            position:absolute; left: 0; top : 0;
            width : 100%; height : 100%;
            li{
                position: relative;
                float: left;
                margin: 4vw 0 0 0;
                width: 100%;height: 10vw;
                line-height: 10vw;
                text-indent: 11vw;
                text-align: left;
                color: azure;
                font-weight: bold;
                transition: all .1s;
                font-size: 3.5vw;
                background: url('../assets/ui/ui (55).png')center no-repeat;
                background-size: 100% 100%;
            }
            li.click{
                transform: translateX(-5vw);
            }
        }
    }
    //武将卡
    .warrior_card{
        float: left;position: relative;
        width: 17vw;height: 25.5vw;
        margin: 3vw 0 0 2.4vw;
        background: rgb(0, 0, 0);
        box-shadow: 0 0 1px black;
        transition: all .3s;
        .name{
            position: absolute; left: 0; top: 5%;
            width: 20%;
            height: 60%;
            line-height: 3.8vw;
            font-size: 100%;
            transform: scale(.6);
        }
        .star_list{
            left: 1.5%; top: 1.2%;
            width: 97%;height:3vw;
            background:linear-gradient(to bottom,black,rgba(0, 0, 0, 0));
            li{
                float: left;
                position: relative;
                width: 1.8vw;height: 1.8vw;
                margin: .5vw 0 0 0;
            }
            li:nth-of-type(1){
                margin: .5vw 0 0 6.2vw;
            }
        }
        .camp{
            left: 1%; top: 1%;
            width: 3vw;height: 3vw;
        }
        .cost_img{
            position: absolute; left: 50%; bottom : 2%;
            transform : translate(-50%,0);
            width: 26%;
            height: 8%;
        }
        .arm{
            right: 2%; bottom: 2%;
            width: 4.5vw;
            height: 4.5vw;
            .icon{
                position: absolute; left: 50%; top : 50%;
                transform : translate(-50%,-50%);
                width: 87%;height: 87%;
            }
            .arm_num{
                position: absolute; left: 0; top : 0;
                width : 100%; height : 100%;
                font-size: 1vw;
                text-align: right;
                line-height: 6vw;
                font-weight: 500;
                color: rgb(222, 222, 222);
            }
        }
        .up_mark{
            right: -1.9vw; top: -1.9vw;
            width: 4.5vw;height: 4.5vw;
        }
        .warrior_sign{
            left: 1%; bottom: 12%;
            width: 4vw;height: 4vw;
        }
        .black_mask{
            position: absolute; left: 0; top : 0;
            width : 100%; height : 100%;
            box-shadow: 0 0 5px black;
            background: url('../assets/ui/ui (110).png')rgba(0, 0, 0, 0.6) center no-repeat;
            background-size: 30% 10%;
        }
    }
    //顶部导航
    .top_navigation{
        position: absolute; left: 0; top: 0;
        width: 100vw;height: 12vh;
        // background:linear-gradient(to bottom right,black,rgb(86, 1, 1));
        background: rgb(32, 31, 31);
        // transition: all .2s;
        .logo2{
            left: 5vw; bottom: 3vw;
            width: 25vw;
            height: 8vw;
            background: url('../assets/background/logo2.webp')no-repeat;
            background-size: 100% 100%;
        }
        
        
        .back_home{
            right:7vw; bottom: 3vw;
            width: 7vw;
            height: 7vw;
            background: url('../assets/ui/ui\ \(7\).png')no-repeat;
            background-size: 100% 100%;
        }
        .back_home.click{
            transform: scale(.8);
        }
    
    }
    .down-enter,.down-leave-to{
        transform: translateY(-100%);
    }
    .down-enter-to,.down-leave{
        transform: translateY(0);
    }
    

    // 招募板块
    .main_recruit{
        right: 5vw; bottom: 8vw;
        width: 26vw; height: 26vw;
        background: url('../assets/ui/ui\ \(5\).png')no-repeat;
        background-size: 100% 100%;
        .logo1{
            position: absolute; left: 50%; top : 50%;
            transform : translate(-50%,-50%);
            width: 60%;
            height: 60%;
            background: url('../assets/ui/ui\ \(4\).png')no-repeat;
            background-size: 100% 100%;
        }
        .text{
            position: absolute; left: 50%; top : 80%;
            transform : translate(-50%,-50%);
            width: 16vw; height: 5vw;
            line-height: 5vw;
            opacity: .8;
            background: url('../assets/ui/ui\ \(6\).png')no-repeat;
            background-size: 100% 100%;
        }
    }
    .main_recruit.click{
        transform: scale(.9);
        .text{
            opacity: 1;
        }
    }

    //配置入口
    .settings_entrance,.account_btn{
        right: 5vw;top: 12vh;
        width: 8vw;height: 8vw;
        background: url('../assets/ui/ui (134).png')no-repeat;
        background-size: 100% 100%;
        box-shadow: 0 0 5px rgb(81, 81, 81);
    }
    .settings_entrance.click,.account_btn.click{
        transform: scale(.9);
    }
    .account_btn{
        margin: 13vw 0 0 0;
        background: url('../assets/ui/ui (135).png')no-repeat;
        background-size: 100% 100%;
    }
}
</style>