class TeamSkills {
    constructor(){
        this.id = 0;
        this.quality = '';
        this.name = "";
        this.type = "";//战法类型
        this.sign = "";
        this.use = 0;//表示战法的使用上限次数
        this.odd = "";
        this.info = "";
                    
    }
    getValue(i,type,use){
        this.id = i.id;
        this.quality = i.quality;
        this.name = i.name;
        this.sign = i.sign;
        this.odd =i.odd;
        this.type = type;
        this.use = use;
        this.info = i.info;

    }
}

export default TeamSkills