<template>
    <div class="my_warrior active" ref="my_warrior">
        <!-- 中部武将容器 -->
        <div class="warrior_container" ref="warrior_container">
            <!-- 武将卡 -->
            <div class="warrior_card" v-for="(w,index) in all_warriors" :key="w.index" @click="SelectThisWarrior(index)" v-show="SysFilterWarrior(w)">
                <img :src="w.img.normal" alt="" class="cover">
                <img src="../../assets/ui/ui (60).png" alt="" class="cover" >
                <ul class="star_list">
                    <li v-for="s in w.red" :key="s.index"><img src="../../assets/ui/ui (74).png" alt="" class="cover"></li>
                    <li v-for="s in (5-w.red)" :key="s.index"><img src="../../assets/ui/ui (76).png" alt="" class="cover"></li>
                </ul>
                <!-- 觉醒 -->
                <img src="../../assets/ui/ui (65).png" alt="" class="up_mark" v-show="w.isUp">
                <img src="../../assets/ui/ui (84).png" alt="" class="up_mark" v-show="w.isUpWork">
                <img src="../../assets/ui/ui (73).png" alt="" class="up_mark" v-show="w.isUp && w.isUpWork">
                <!-- 兵种 -->
                <div class="arm">
                    <img src="../../assets/ui/ui (81).png" alt="" v-show="!w.isUpArm" class="cover">
                    <img src="../../assets/ui/ui (82).png" alt="" v-show="w.isUpArms" class="cover">
                    <img src="../../assets/ui/ui (79).png" alt="" v-show="w.arms == '步' && !w.isUpArms" class="icon">
                    <img src="../../assets/ui/ui (70).png" alt="" v-show="w.arms == '骑' && !w.isUpArms" class="icon">
                    <img src="../../assets/ui/ui (62).png" alt="" v-show="w.arms == '弓' && !w.isUpArms" class="icon">

                    <img src="../../assets/ui/ui (71).png" alt="" v-show="w.arms == '骑' && w.isUpArms" class="icon">
                    <img src="../../assets/ui/ui (80).png" alt="" v-show="w.arms == '步' && w.isUpArms" class="icon">
                    <img src="../../assets/ui/ui (63).png" alt="" v-show="w.arms == '弓' && w.isUpArms" class="icon">
                    <div class="arm_num">{{w.att}}</div>
                </div>

                <div class="camp" v-show="w.camp == '蜀'"><img src="../../assets/ui/ui (75).png" alt="" class="cover"></div>
                <div class="camp" v-show="w.camp == '吴'"><img src="../../assets/ui/ui (78).png" alt="" class="cover"></div>
                <div class="camp" v-show="w.camp == '魏'"><img src="../../assets/ui/ui (77).png" alt="" class="cover"></div>
                <div class="camp" v-show="w.camp == '群'"><img src="../../assets/ui/ui (72).png" alt="" class="cover"></div>
                <div class="camp" v-show="w.camp == '汉'"><img src="../../assets/ui/ui (64).png" alt="" class="cover"></div>
                <div class="camp" v-show="w.camp == '晋'"><img src="../../assets/ui/ui (150).png" alt="" class="cover"></div>
                
                
                <img src="../../assets/ui/ui (61).png" alt="" class="cost_img">
                <div class="name">{{w.name}}</div>

                <!-- 武将标志 -->
                <div class="warrior_sign">
                    <img src="../../assets/ui/ui (66).png" alt="" v-show="w.sign == 's2'" class="cover">
                    <img src="../../assets/ui/ui (67).png" alt="" v-show="w.sign == 's3'" class="cover">
                    <img src="../../assets/ui/ui (68).png" alt="" v-show="w.sign == 'sp'" class="cover">
                    <img src="../../assets/ui/ui (69).png" alt="" v-show="w.sign == 'xp'" class="cover">
                </div>
                
                <div class="black_mask" v-show="!w.own"></div>
            </div>
        </div>

        <!-- 底部 -->
        <div class="my_warrior_bottom">
            <div class="filter_warrior_panel_btn" ref="filter_warrior_panel_btn"
            @touchstart="$refs.filter_warrior_panel_btn.classList.add('click')"
            @touchend="$refs.filter_warrior_panel_btn.classList.remove('click')"
            @click="status.filter = true">筛 选</div>
        </div>

        <!-- 底部武将筛选板 -->
        <div class="filter_warrior_panel cover" v-show="status.filter">
            <div class="mask cover" @click="status.filter = false"></div>
            <div class="container">
                <div class="item">
                    <div class="text">阵营筛选</div>
                    <div class="filter_btn" @click="OperaFilterWarrior('camp','群')">群
                        <div class="current cover bg_qun" v-show="FILTER_TABLE.camp.value == '群'">群</div>
                    </div>
                    <div class="filter_btn" @click="OperaFilterWarrior('camp','汉')">汉
                        <div class="current cover bg_han" v-show="FILTER_TABLE.camp.value == '汉'">汉</div>
                    </div>
                    <div class="filter_btn" @click="OperaFilterWarrior('camp','魏')">魏
                        <div class="current cover bg_wei" v-show="FILTER_TABLE.camp.value == '魏'">魏</div>
                    </div>
                    <div class="filter_btn" @click="OperaFilterWarrior('camp','蜀')">蜀
                        <div class="current cover bg_shu" v-show="FILTER_TABLE.camp.value == '蜀'">蜀</div>
                    </div>
                    <div class="filter_btn" @click="OperaFilterWarrior('camp','吴')">吴
                        <div class="current cover bg_wu" v-show="FILTER_TABLE.camp.value == '吴'">吴</div>
                    </div>
                    <div class="filter_btn" @click="OperaFilterWarrior('camp','晋')">晋
                        <div class="current cover bg_jin" v-show="FILTER_TABLE.camp.value == '晋'">晋</div>
                    </div>
                </div>
                <div class="item">
                    <div class="text">是否解锁</div>
                    <div class="filter_btn" @click="OperaFilterWarrior('own',true)">是
                        <div class="current cover bg_orange" v-show="FILTER_TABLE.own.value == true">是</div>
                    </div>
                    <div class="filter_btn" @click="OperaFilterWarrior('own',false)">否
                        <div class="current cover bg_orange" v-show="FILTER_TABLE.own.value == false && FILTER_TABLE.own.status">否</div>
                    </div>
                </div>
                <div class="item">
                    <div class="text">是否进阶</div>
                    <div class="filter_btn long" @click="OperaFilterWarrior('up','tactics')">觉醒
                        <div class="current cover bg_orange" v-show="FILTER_TABLE.up.value == 'tactics'">觉醒</div>
                    </div>
                    <div class="filter_btn long" @click="OperaFilterWarrior('up','work')">深造
                        <div class="current cover bg_orange" v-show="FILTER_TABLE.up.value == 'work'">深造</div>

                    </div>
                    <div class="filter_btn long" @click="OperaFilterWarrior('up','arms')">兵种
                        <div class="current cover bg_orange" v-show="FILTER_TABLE.up.value == 'arms'">兵种</div>

                    </div>

                </div>
                <div class="item">
                    <div class="text">战法品质</div>
                    <div class="filter_btn" @click="OperaFilterWarrior('quality','S')">S
                        <div class="current cover bg_orange" v-show="FILTER_TABLE.quality.value == 'S'">S</div>
                    </div>
                    <div class="filter_btn" @click="OperaFilterWarrior('quality','A')">A
                    <div class="current cover bg_orange" v-show="FILTER_TABLE.quality.value == 'A'">A</div>
                        <div class="current"></div>
                    </div>
                    <div class="filter_btn" @click="OperaFilterWarrior('quality','B')">B
                    <div class="current cover bg_orange" v-show="FILTER_TABLE.quality.value == 'B'">B</div>
                        <div class="current"></div>
                    </div>

                </div>
            </div>
        </div>
        
        
        
        <!-- 武将信息面板 -->
        <WarriorInfoVue :warrior_current="warrior_current" v-show="status.warrior_panel"/>
        <!-- 我的部队界面组件 -->
        <MyTeam :all_warriors="all_warriors"/>

        <!-- <div class="a" style="width: 100vw; height: 100vh; 
        font-size: 1px;
        position: fixed; text-align: left; left: 0; top: 0; color: black; background: white;"></div> -->
    </div>
</template>

<script>
import WarriorInfoVue from './WarriorInfo.vue'


import all_warriors from '../../mixin/warrior/warriors'//获取所有武将
import MyTeam from '../team/MyTeam.vue'

import MyWarrior from '../../mixin/structure/MyWarrior'
export default {
    name : 'MyWarrior',
    props:['InfiniteBorderApp'],
    components:{
        WarriorInfoVue,
        MyTeam
    },
    mixins:[all_warriors],
    data(){
        return{
            status:{
                filter:false,
                warrior_panel  :false,
                dirty : false,//脏位
            },

            user:{
                warriors: [],//我的武将队列
                
            },
            warrior_current: {
                id : 0,
                img : {big:'',head:''},
                isUpWork : false,
                isUp : false,
                isUpArms: false,
                red : 0,
                camp : '',
                arms : '',
                sign  :'',
                att : '',
                cost : 1,
                own : false,
                skill:{
                    name : '乱政',
                    type : 'active',
                    quality : 'A',
                    info : '使敌军单体获得如下10个负面效果中的1个：动摇、恐慌、火攻、妖术、混乱、犹豫、暴走、怯战、挑衅、围困，持续2回合，该效果执行3次，每次的目标和效果独立判定',
                },
            },

            //过滤表
            FILTER_TABLE:{
                //阵营
                camp:{
                    status : false,
                    value : '',
                },
                //是否拥有
                own : {
                    status : false,
                    value : -1,
                },
                //进阶
                up : {
                    status : false,
                    value : '',
                },
                //战法品质
                quality : {
                    status : false,
                    value : '',
                }
            },


            flag : false,
        }
    },
    methods:{
        

        //展示或关闭本界面
        OpenOrClose(flag){
            //flag为真，表示打开本界面
            if(flag){
                this.$refs.my_warrior.classList.remove('active');
            }else{
                this.$refs.my_warrior.classList.add('active');
            }
        },
        //过滤筛选武将
        SysFilterWarrior(w){
            if(this.flag){
                return false;
            }
            if(this.FILTER_TABLE.camp.status){
                //表示当前筛选为阵营
                if(w.camp != this.FILTER_TABLE.camp.value){
                    //不符合条件
                    return false;
                }
            }
            if(this.FILTER_TABLE.own.status){
                if(w.own != this.FILTER_TABLE.own.value){
                    return false;
                }
            }
            if(this.FILTER_TABLE.up.status){
                if(this.FILTER_TABLE.up.value == 'tactics'){
                    if(!w.isUp){
                        return false;
                    }
                }else if(this.FILTER_TABLE.up.value == 'work'){
                    if(!w.isUpWork){
                        return false;
                    }
                }else if(this.FILTER_TABLE.up.value == 'arms'){
                    if(!w.isUpArms){
                        return false;
                    }
                }
            }
            if(this.FILTER_TABLE.quality.status){
                if(w.skill.quality != this.FILTER_TABLE.quality.value){
                    return false;
                }
            }
            return true;
        },
        //操作过滤条件
        OperaFilterWarrior(type,value){
            if(type == 'camp'){
                if(this.FILTER_TABLE.camp.value == value){
                    this.FILTER_TABLE.camp.status = false;
                    this.FILTER_TABLE.camp.value = '';
                }else{
                    this.FILTER_TABLE.camp.status = true;
                    this.FILTER_TABLE.camp.value = value;
                }
            }else if(type == 'own'){
                if(this.FILTER_TABLE.own.value == value){
                    this.FILTER_TABLE.own.status = false;
                    this.FILTER_TABLE.own.value = -1;
                }else{
                    this.FILTER_TABLE.own.status = true;
                    this.FILTER_TABLE.own.value = value;
                }
            }else if(type == 'up'){
                if(this.FILTER_TABLE.up.value == value){
                    this.FILTER_TABLE.up.status = false;
                    this.FILTER_TABLE.up.value = '';
                }else{
                    this.FILTER_TABLE.up.status = true;
                    this.FILTER_TABLE.up.value = value;
                }
            }else if(type == 'quality'){
                if(this.FILTER_TABLE.quality.value == value){
                    this.FILTER_TABLE.quality.status = false;
                    this.FILTER_TABLE.quality.value = '';
                }else{
                    this.FILTER_TABLE.quality.status = true;
                    this.FILTER_TABLE.quality.value = value;
                }
            }
            
        },
        //选择这个武将打开详情
        SelectThisWarrior(i){
            this.warrior_current = this.all_warriors[i];
            this.status.warrior_panel = true;   
        },
        
        //关闭当前武将的信息面板
        CloseWarriorPanel(){
            this.status.warrior_panel = false;
        },
        //上锁解锁武将
        LockWarrior(type){
            //type为真表示上锁，反之表示解锁
            if(type){
                this.warrior_current.red = 0;
                this.warrior_current.isUp = false;
                this.warrior_current.isUpWork = false;
                this.warrior_current.isUpArms = false;
                for(let i=0;i < this.user.warriors.length;i++){
                    if(this.user.warriors[i].id == this.warrior_current.id){
                        //删除这个武将
                        this.user.warriors.splice(i,1);
                        break;
                    }
                }
                this.warrior_current.own = false;
                //上锁之后关闭该界面
                this.CloseWarriorPanel();
            }else{
                this.warrior_current.own = true;
                //把这个武将插入到我武将的单链表中
                //初始化一个武将
                let my_warrior = new MyWarrior(this.warrior_current);
                if(this.user.warriors.length == 0){
                    //如果我的武将为空，直接插入
                    this.user.warriors.push(my_warrior);
                }else{
                    let insert_index = 0;
                    for(let i = 0;i<this.user.warriors.length;i++){
                        if(my_warrior.id > this.user.warriors[i].id){
                            insert_index++;
                        }
                        else if(my_warrior.id < this.user.warriors[i].id){
                            break;
                        }
                    }
                    //插入到正确的位置
                    this.user.warriors.splice(insert_index,0,my_warrior);
                }
            }
            //修改脏位
            this.status.dirty = true;
        },
        //调整武将的红度
        AdjustStar(type){
            this.status.dirty = true;
            if(type){
                this.warrior_current.red ++;
            }else{
                this.warrior_current.red --;
            }
        },
        //觉醒深造和进阶武将
        UpWarrior(type,confirm){
            //confirm为真表示确定
            if(type == 'juexing'){
                if(confirm){
                    this.warrior_current.isUp = true;
                }else{
                    this.warrior_current.isUp = false;
                }
            }else if(type == 'shengzao'){
                if(confirm){
                    this.warrior_current.isUpWork = true;
                }else{
                    this.warrior_current.isUpWork = false;
                }
            }else{
                if(confirm){
                    this.warrior_current.isUpArms = true;
                }else{
                    this.warrior_current.isUpArms = false;
                }
            }
            this.status.dirty = true;
        },
        //把数据库的武将数据加载到面板上
        UpdateWarrior(){
            let account_index = 0;
            for(let i=0;i<this.InfiniteBorderApp.account.length;i++){
                if(this.InfiniteBorderApp.account[i].id == this.InfiniteBorderApp.current_id){
                    account_index = i;
                    break;
                }
            }
            //检查是否有我的武将数据
            if(this.InfiniteBorderApp.account[account_index].warriors.length != 0){
                //如果我有保存的数据
                this.user.warriors = this.InfiniteBorderApp.account[account_index].warriors;

                //并且更新展示表的数据
                if(this.user.warriors.length == 0){
                    return;
                }
                let index = 0;
                for(let i = 0;i<this.all_warriors.length;i++){
                    if(this.user.warriors[0].id == this.all_warriors[i].id){
                        index = i;
                    }
                }
                for (let i = 0; i < this.user.warriors.length; i++) {
                    for (let j = index; j < this.all_warriors.length; j++) {
                        if(this.all_warriors[j].id == this.user.warriors[i].id){
                            this.all_warriors[j].red = this.user.warriors[i].red;
                            this.all_warriors[j].isUp = this.user.warriors[i].isUp;
                            this.all_warriors[j].isUpWork = this.user.warriors[i].isUpWork;
                            this.all_warriors[j].isUpArms = this.user.warriors[i].isUpArms;
                            this.all_warriors[j].own = true;
                            break;
                        }
                    }
                }

                this.flag = true;
                this.flag = false;
            }
        },
        //保存武将数据到数据库中
        SaveWarriorsData(){
            if(!this.status.dirty){
                //武将没有被修改
                return;
            }
            //当前有武将可以保存
            if(this.user.warriors.length != 0){
                let index = 0;
                for(let j = 0;j<this.all_warriors.length;j++){
                    //找到第一个相同的武将
                    if(this.user.warriors[0].id == this.all_warriors[j].id){
                        index = j;//记录这个起始地址
                    }
                }
                for(let i = 0;i<this.user.warriors.length;i++){
                    for(let k = index;k<this.all_warriors.length;k++){
                        if(this.user.warriors[i].id == this.all_warriors[k].id){
                            index = k+1;//下一次循环查找从k+1开始找
                            if(index > this.all_warriors.length - 1){
                                //越界控制
                                index  = this.all_warriors.length-1;
                            }
                            //同步属性
                            this.user.warriors[i].red = this.all_warriors[k].red;
                            this.user.warriors[i].isUp = this.all_warriors[k].isUp;
                            this.user.warriors[i].isUpWork = this.all_warriors[k].isUpWork;
                            this.user.warriors[i].isUpArms = this.all_warriors[k].isUpArms;
                            break;
                        }
                    }
                }
            }
            //存储数据到local
            let temp_account = this.InfiniteBorderApp;
            for(let i =0;i<temp_account.account.length;i++){
                //找到要存储的账户
                if(temp_account.account[i].id == temp_account.current_id){
                    temp_account.account[i].warriors = this.user.warriors;
                }
            }
            localStorage.setItem('InfiniteBorderApp',JSON.stringify(temp_account));
            this.$bus.$emit('alert','保存成功!');
            //重置脏位
            this.status.dirty = false;
        },
        //返回武将数据
        ReturnWarriorData(){
            return this.all_warriors;
        },
        //清除所有武将
        ClearWarriors(){
            if(this.user.warriors.length == 0){
                return;
            }
            let last_id = this.user.warriors[this.user.warriors.length - 1].id;//获取我武将队列最后一个武将的id
            for(let i=0;i<this.all_warriors.length;i++){
                if(this.all_warriors[i].own){
                    this.all_warriors[i].red = 0;
                    this.all_warriors[i].isUp = false;
                    this.all_warriors[i].isUpArms = false;
                    this.all_warriors[i].isUpWork = false;
                    this.all_warriors[i].own = false;
                }
                if(this.all_warriors[i].id == last_id){
                    break;
                }
            }

            this.user.warriors = [];//清空我的武将队列数据
            // localStorage.setItem('stzb_my_warriors',JSON.stringify(this.user.warriors));
        },
        Printf(){
            let node = document.querySelector('.a');
            let all_str = "";
            let num = 1;
            for(let i in this.all_warriors){
                if(this.all_warriors[i].name != '侍卫' && this.all_warriors[i].advanced_arms){
                    let str  = '{' + "<br>" 
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"id" : ' + num + ',' + '<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"cost" : ' + this.all_warriors[i].cost + ',' + '<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"name" : "' + this.all_warriors[i].name + '",<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"gender" : "' + this.all_warriors[i].gender + '",<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"arms" : "' + this.all_warriors[i].arms + '",<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"range" : ' + this.all_warriors[i].att + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"up_arms" : ["' + this.Solve(this.all_warriors[i].advanced_arms[0]) + '","' + this.Solve(this.all_warriors[i].advanced_arms[1]) + '"],<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"tactics_name" : "' + this.all_warriors[i].skill.name + '",<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"tactics_type" : "' + this.all_warriors[i].skill.type + '",<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"tactics_odds" : "--' + '",<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"tactics_quality" : "' + this.all_warriors[i].skill.quality + '",<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"tactics_describe" : "' + this.all_warriors[i].skill.info + '",<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"sign" : ' + this.T(this.all_warriors[i].sign) + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"attr" : {' + "<br>"
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"ad" : ' + this.A(this.all_warriors[i]) + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"de" : ' + this.all_warriors[i].attr.de + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"tact" : ' + this.all_warriors[i].attr.tact + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"speed" : ' + this.all_warriors[i].attr.speed + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"demolition" : ' + this.all_warriors[i].attr.demolition + '<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;},' + "<br>"
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"grow" : {' + "<br>"
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"ad" : ' + this.all_warriors[i].grow.ad + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"de" : ' + this.all_warriors[i].grow.de + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"tact" : ' + this.all_warriors[i].grow.tact + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"speed" : ' + this.all_warriors[i].grow.speed + ',<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"demolition" : ' + this.all_warriors[i].grow.demolition + '<br>'
                    + '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;},' + "<br>"
                    + '&nbsp;&nbsp;&nbsp;&nbsp;"img" : "' + this.Im(this.all_warriors[i].img.normal) + '"<br>'
                    + "},<br>"
                    all_str += str;
                    num += 1;
                }
            }
            node.innerHTML = all_str;
        },
        Im(a){
            let url = a.toString();
            let index = url.search('jpg');
            console.log(index);
            let str = url.slice(4,index-10);
            return str + '.jpg';
        },
        A(target){
            if(target.attr){
                return target.attr.ad;
            }
            else{
                console.log(target.name + "没有攻击力");
                return 0;
            }
        },
        T(str){
            if(str == 'none') return "null";
            else return '"' + str + '"';
        },
        Solve(str){
            switch(str){
                case "g_n" : return "弩兵";
                case "g_c" : return "长弓兵";
                case "g_s" : return "死士";
                case "q_q" : return "轻骑兵";
                case "q_z" : return "重骑兵";
                case "q_t" : return "铁骑兵";
                case "q_s" : return "弓骑兵";
                case "q_x" : return "象兵";
                case "b_m" : return "蛮兵";
                case "b_j" : return "禁卫";
                case "b_c" : return "长枪兵";
                case "b_z" : return "重步兵";
            }
        }
        
    },
    mounted(){
        this.$bus.$on('EnterMyWarrior',this.OpenOrClose);
        this.$bus.$on('CloseWarriorPanel',this.CloseWarriorPanel);
        
        //武将操作的事件站
        this.$bus.$on('LockWarrior',this.LockWarrior);
        this.$bus.$on('AdjustStar',this.AdjustStar);
        this.$bus.$on('UpWarrior',this.UpWarrior);

        //保存数据
        this.$bus.$on('SaveWarriorData',this.SaveWarriorsData);

        //清空我的武将
        this.$bus.$on('ClearWarriors',this.ClearWarriors)

        // this.Printf();
        //把数据库的武将数据加载到面板上
        setTimeout(()=>{
            this.UpdateWarrior();
        },500);
        // this.warrior_current = this.all_warriors[5];
    }
}
</script>

