<template> 
    <div class="warrior_info" ref="warrior_info">
        <div class="mask cover" @click="Close"></div>


        <div class="container">
            <div class="close_btn" ref="close_btn" @touchstart="$refs.close_btn.classList.add('click')"
            @touchend="$refs.close_btn.classList.remove('click')" @click="Close"></div>
            <div class="texture"></div>
            <div class="warrior_nav">
                <div class="block current" ref="warrior_nav_settings" @click="ExchangePanelStatus(0)">配置</div>
                <div class="block" ref="warrior_nav_tactics" @click="ExchangePanelStatus(1)">战法</div>
                
            </div>

            <!-- 武将卡牌 -->
            <div class="warrior_head" v-show="warrior_current != '' && aspect == 0" >
                <div class="camp" v-show="warrior_current.camp == '蜀'"><img src="../../assets/ui/ui (75).png" alt="" class="cover"></div>
                <div class="camp" v-show="warrior_current.camp == '吴'"><img src="../../assets/ui/ui (78).png" alt="" class="cover"></div>
                <div class="camp" v-show="warrior_current.camp == '魏'"><img src="../../assets/ui/ui (77).png" alt="" class="cover"></div>
                <div class="camp" v-show="warrior_current.camp == '群'"><img src="../../assets/ui/ui (72).png" alt="" class="cover"></div>
                <div class="camp" v-show="warrior_current.camp == '汉'"><img src="../../assets/ui/ui (64).png" alt="" class="cover"></div>
                <div class="camp" v-show="warrior_current.camp == '晋'"><img src="../../assets/ui/ui (150).png" alt="" class="cover"></div>
                <img :src="warrior_current.img.head" alt="" class="cover">
                <!-- <img src="../../assets/ui/ui (60).png" alt="" class="cover" > -->
                
                <!-- 觉醒 -->
                <!-- <img src="../../assets/ui/ui (61).png" alt="" class="cost_img"> -->
                <div class="name">{{warrior_current.name}}</div>

                <!-- 武将标志 -->
                <div class="warrior_sign">
                    <img src="../../assets/ui/ui (66).png" alt="" v-show="warrior_current.sign == 's2'" class="cover">
                    <img src="../../assets/ui/ui (67).png" alt="" v-show="warrior_current.sign == 's3'" class="cover">
                    <img src="../../assets/ui/ui (68).png" alt="" v-show="warrior_current.sign == 'sp'" class="cover">
                    <img src="../../assets/ui/ui (69).png" alt="" v-show="warrior_current.sign == 'xp'" class="cover">
                </div>



            </div>


            <!-- 调整红星程度 -->
            <div class="change_star_panel" v-show="warrior_current.own && aspect == 0">
                <ul class="star_list">
                    <li v-for="s in warrior_current.red" :key="s.index"><img src="../../assets/ui/ui (74).png" alt="" class="cover"></li>
                    <li v-for="s in (5-warrior_current.red)" :key="s.index"><img src="../../assets/ui/ui (76).png" alt="" class="cover"></li>
                </ul>

                <div class="adjust_btn" ref="sub_star" @touchstart="$refs.sub_star.classList.add('click')"
                @touchend="$refs.sub_star.classList.remove('click')" @click="AdjustStar(false)">-</div>
                <div class="adjust_btn" ref="add_star" @touchstart="$refs.add_star.classList.add('click')"
                @touchend="$refs.add_star.classList.remove('click')" @click="AdjustStar(true)">+</div>
                
            </div>

            <!-- 武将攻击距离和统帅 -->
            <div class="base_info" v-show="warrior_current.own && aspect == 0">
                <div class="item">攻击距离:{{warrior_current.att}}</div>
                <div class="item">统帅:{{warrior_current.cost}}</div>
            </div>

            <!-- 觉醒和深造 -->
            <div class="up_mark" v-show="warrior_current.own && aspect == 0">
                <div class="text">觉醒深造</div>
                <div class="sign">
                    <img src="../../assets/ui/ui (65).png" alt="" class="cover" v-show="warrior_current.isUp">
                    <img src="../../assets/ui/ui (84).png" alt="" class="cover" v-show="warrior_current.isUpWork">
                    <img src="../../assets/ui/ui (73).png" alt="" class="cover" v-show="warrior_current.isUp && warrior_current.isUpWork">
                    <img src="../../assets/ui/ui (121).png" alt="" class="cover" v-show="!warrior_current.isUp && !warrior_current.isUpWork">
                </div>
                <div class="up">
                    <div class="up_btn" ref="up_btn1" @touchstart="$refs.up_btn1.classList.add('click')"
                    @touchend="$refs.up_btn1.classList.remove('click')">
                        <span class="cover" v-show="!warrior_current.isUp" @click="UpWarriorOpera('juexing',!warrior_current.isUp)">觉 醒</span>
                        <span class="cover" v-show="warrior_current.isUp" @click="UpWarriorOpera('juexing',!warrior_current.isUp)" style="background: darkred;">取消觉醒</span>
                    </div>
                    <div class="up_btn" ref="up_btn2" @touchstart="$refs.up_btn2.classList.add('click')"
                    @touchend="$refs.up_btn2.classList.remove('click')">
                        <span class="cover" v-show="!warrior_current.isUpWork" @click="UpWarriorOpera('shengzao',!warrior_current.isUpWork)">深 造</span>
                        <span class="cover" v-show="warrior_current.isUpWork" @click="UpWarriorOpera('shengzao',!warrior_current.isUpWork)" style="background: darkred;">取消深造</span>
                    </div>
                    <!-- <div class="up_btn" ref="up_btn3" @touchstart="$refs.up_btn3.classList.add('click')"
                @touchend="$refs.up_btn3.classList.remove('click')">兵种进阶</div> -->
                </div>
            </div>

            <!-- 进阶兵种 -->
            <div class="up_arms" v-show="warrior_current.own && aspect == 0">
                <div class="text">兵种进阶</div>
                <!-- 兵种 -->
                <div class="arm">
                    <!-- <img src="../../assets/ui/ui (81).png" alt="" v-show="!warrior_current.isUpArm" class="cover">
                    <img src="../../assets/ui/ui (82).png" alt="" v-show="warrior_current.isUpArms" class="cover"> -->
                    <img src="../../assets/ui/ui (79).png" alt="" v-show="warrior_current.arms == '步' && !warrior_current.isUpArms" class="icon">
                    <img src="../../assets/ui/ui (70).png" alt="" v-show="warrior_current.arms == '骑' && !warrior_current.isUpArms" class="icon">
                    <img src="../../assets/ui/ui (62).png" alt="" v-show="warrior_current.arms == '弓' && !warrior_current.isUpArms" class="icon">

                    <img src="../../assets/ui/ui (71).png" alt="" v-show="warrior_current.arms == '骑' && warrior_current.isUpArms" class="icon">
                    <img src="../../assets/ui/ui (80).png" alt="" v-show="warrior_current.arms == '步' && warrior_current.isUpArms" class="icon">
                    <img src="../../assets/ui/ui (63).png" alt="" v-show="warrior_current.arms == '弓' && warrior_current.isUpArms" class="icon">
                    
                </div>

                <div class="up_btn" ref="up_arms1" @touchstart="$refs.up_arms1.classList.add('click')"
                @touchend="$refs.up_arms1.classList.remove('click')" @click="UpWarriorOpera('arm',!warrior_current.isUpArms)" v-show="!warrior_current.isUpArms">进阶兵种</div>
                <div class="up_btn" ref="up_arms2" @touchstart="$refs.up_arms2.classList.add('click')"
                @touchend="$refs.up_arms2.classList.remove('click')" @click="UpWarriorOpera('arm',!warrior_current.isUpArms)" v-show="warrior_current.isUpArms" style="background: darkred;">取消进阶</div>
            </div>

            <!-- 解锁按钮 -->
            <div class="lock_btn" ref="lock_btn" @touchstart="$refs.lock_btn.classList.add('click')"
            @touchend="$refs.lock_btn.classList.remove('click')"  v-show="!warrior_current.own && aspect == 0" @click="Lock(false)"></div>
            <!-- 上锁按钮 -->
            <div class="unlock_btn" ref="unlock_btn" @touchstart="$refs.unlock_btn.classList.add('click')"
            @touchend="$refs.unlock_btn.classList.remove('click')"   v-show="warrior_current.own && aspect == 0" @click="Lock(true)"></div>
        

            <!-- 武将战法面板 -->
            <div class="tactics" v-show="aspect == 1">
                <div class="tactics_icon">

                    <img src="../../assets/ui/ui (91).png" alt="" class="cover" v-show="warrior_current.skill.type == 'passive'">
                    <img src="../../assets/ui/ui (92).png" alt="" class="cover" v-show="warrior_current.skill.type == 'command'">
                    <img src="../../assets/ui/ui (93).png" alt="" class="cover" v-show="warrior_current.skill.type == 'active'">
                    <img src="../../assets/ui/ui (94).png" alt="" class="cover" v-show="warrior_current.skill.type == 'combo'">


                    <img src="../../assets/ui/ui (89).png" alt="" class="icon" v-show="warrior_current.skill.quality == 'B'">
                    <img src="../../assets/ui/ui (88).png" alt="" class="icon" v-show="warrior_current.skill.quality == 'A'">
                    <img src="../../assets/ui/ui (90).png" alt="" class="icon" v-show="warrior_current.skill.quality == 'S'">
                    <div class="name">{{warrior_current.skill.name}}</div>
                
                
                </div>
                <div class="describe">{{warrior_current.skill.info}}</div>
            </div>
        
        </div>
    </div>
</template>

<script>
export default {
    name : 'WarriorInfo',
    props:['warrior_current'],
    data(){
        return{
            aspect : 0, 
        }
    },
    methods:{
        //关闭本界面
        Close(){
            this.$bus.$emit('CloseWarriorPanel');
        },
        //调整红星
        AdjustStar(type){
            if(type){
                if(this.warrior_current.red < 5){
                    this.$bus.$emit('AdjustStar',type);
                }
            }else{
                if(this.warrior_current.red > 0){
                    this.$bus.$emit('AdjustStar',type);
                }
            }
            
        },

        //上锁解锁武将
        Lock(type){
            //type为真表示上锁，反之表示解锁
            this.$bus.$emit('LockWarrior',type);
        },

        //武将觉醒，深造，进阶
        UpWarriorOpera(type,confirm){
            this.$bus.$emit('UpWarrior',type,confirm);
        },
        //切换武将配置视角和战法预览视角
        ExchangePanelStatus(value){
            if(value == 0){
                this.$refs.warrior_nav_tactics.classList.remove('current')
                this.$refs.warrior_nav_settings.classList.add('current')
                this.aspect = 0;
            }else{
                this.$refs.warrior_nav_settings.classList.remove('current')
                this.$refs.warrior_nav_tactics.classList.add('current')
                this.aspect = 1;
            }
        }
    },
    mounted(){
        
    }
}
</script>

<style lang="less">
.warrior_info{
    position: absolute; left: 0; top : 0;
    width : 100%; height : 100%;
    z-index: 1;
    
    .mask{
        background: rgba(0, 0, 0, 0.8);
    }

    //武将容器
    .container{
        left: 0; top: 50%;
        width: 100vw;height: 80vw;
        margin: -40vw 0 0 0;
        background: url('../../assets/background/bg\ \(7\).png')#393b3e bottom right no-repeat;
        background-size: cover;
        color: rgb(235, 235, 235);
        animation: emerge .1s ease-out 1;
        .close_btn{
            right: 3vw; top: 3vw;
            height: 8vw; width: 8vw;
            background: url('../../assets/ui/ui (85).png')no-repeat;
            background-size: cover;
        }
        .close_btn.click{
            transform: scale(.8);
        }
        .texture{
            left: 0; top: 2vw; width: 50vw; height: 10vw;
            background: url('../../assets/background/bg (5).png')no-repeat;
            background-size: 100% 100%;
        }
        .warrior_nav{
            left: 0; top: 0;
            width: 8vw;
            height: 100%;
            background: rgba(0, 0, 0, 0.264);
            // background: url('../../assets/background/bg\ \(20\).png')center no-repeat;
            background-size: 105% 105%;
            .block{
                float: left;position: relative;
                // margin: 0 0 0 3vw;
                padding: 15vw 2vw;
                width: 4vw;
                height: 10vw;
                // background: #000;
                line-height: 5vw;
                font-size: 3vw;
            }
            .block.current{
                color: rgb(207, 0, 0);
                border-right: 1px solid rgb(193, 0, 0);
            }
        }
        .warrior_head{
            width: 25vw;height: 25vw;
            animation: fadeIn .4s ease-out 1;
            // margin: 30vw 0 0 3vw;
            margin: 10vw 0 0 15vw;
            background: rgb(39, 38, 38);
            box-shadow: 0 0 5px rgb(34, 34, 34);
            border-radius: 50%;
            img{
                position: absolute; left: 50%; top : 50%;
                transform : translate(-50%,-50%);
                width: 95%;height: 92%;
                border-radius: 50%;
            }
            .name{
                position: absolute; left: 50%; top : 100%;
                transform : translate(-50%,-50%);
                width: 26vw;height: 6vw;
                background: rgb(31, 30, 30);
                box-shadow: 0 0 5px rgb(34, 34, 34);
                line-height: 6vw;
                border-radius: 1vw;
            }
            .camp{
                right: -15vw; top: -10vw;
                width: 30vw;height: 30vw;
                opacity: .1;
            }
            
            .warrior_sign{
                left: 1%; bottom: 12%;
                width: 8vw;height: 8vw;
            }
        }
        .change_star_panel,.base_info,.up_mark,.up_arms{
            left: 46vw; top: 20vw;
            width: 50vw;height: 8vw;
            background: rgba(43, 43, 43, 0.6);
            line-height: 8vw;
            animation: box_float .4s ease-out 1;
            .star_list{
                left: 15vw;
                width: 20vw;height: 8vw;
                li{
                    float: left;
                    position: relative;
                    width: 4vw;height: 4vw;
                    margin: 2vw 0 0 0;
                }
                
            }
            .adjust_btn{
                left: 2vw;top: 2vw;height: 4vw;width: 8vw;
                background: rgb(107, 105, 105);
                line-height: 4vw;
            }
            .adjust_btn:nth-of-type(2){
                left: 40vw;
            }
            .adjust_btn.click{
                transform: scale(.8);
            }
        }
        .change_star_panel{
            animation-delay: -.17s;
        }
        .base_info{
            top: 30vw;
            line-height: 8vw;
            animation-delay: -.13s;
            .item{
                position: relative;
                float: left;height: 6vw;width: 24vw;
                margin: 1vw 0 0 0;
                line-height: 6vw;
                
                text-align: center;
            }
            .item:nth-of-type(1){
                border-right: 2px solid rgba(255, 255, 255, 0.155);
            }
        }
        .up_mark{
            top: 40vw;left: 14vw;
            width: 82vw;
            animation-delay: -.09s;
            .text{
                left: 0;width: 20vw;
            }
            .sign{
                left: 20vw;
                width: 8vw;height: 8vw;
                // background: #000;
            }
            .up{
                left: 30vw;
                .up_btn{
                    position: relative;float: left;
                    width: 16vw;height: 4vw;
                    background: rgb(107, 105, 105);
                    line-height: 4vw;
                    // font-size: 2vw;
                    margin:2vw 0 0 4vw;
                    transition: all .1s;
                }
                .up_btn:nth-of-type(2){
                    margin: 2vw  0 0 14vw;
                }
                .up_btn.click{
                    transform: scale(.7);
                }
                span{
                    display: block;
                }
            }
            
        }
        .up_arms{
            top: 50vw;left: 14vw;
            width: 82vw;
            animation-delay: -0.05s;
            .text{
                left: 0;width: 20vw;
            }
            .arm{
                left: 20vw;
                width: 8vw;height: 8vw;
                img{
                    position: absolute; left: 0; top : 0;
                    width : 100%; height : 100%;
                }
            }
            .up_btn{
                right: 2vw;top: 2vw;
                height: 4vw; width:16vw;
                line-height: 4vw;
                transition: all .1s;
                background: rgb(107, 105, 105);
            }
            .up_btn.click{
                transform: scale(.7);
            }
        }
        .lock_btn,.unlock_btn{
            top: 65vw; left: 50%;
            width: 30vw;height: 6vw;
            animation: box_float .3s ease-out 1;
            margin: 0 0 0 -15vw;
            color: black;
            line-height: 6vw;
            font-weight: bold;
            transition: all .1s;
            box-shadow: 0 0 1px rgb(48, 48, 48);
            background: url('../../assets/ui/ui\ \(87\).png')rgb(220, 217, 217) center no-repeat;
            background-size: 30% 80%;
        }
        
        .unlock_btn{
            background: url('../../assets/ui/ui\ \(86\).png')rgb(220, 217, 217) center no-repeat;
            background-size: 30% 80%;
        }
        .lock_btn.click,.unlock_btn.click{
            transform: scale(.8);
        }
        .tactics{
            left: 12vw;top: 18vw;
            width: 82vw;height: 45vw;
            background: rgba(0, 0, 0, 0.2);
            .tactics_icon{
                left: 5vw; top: 5vw;
                width: 15vw;
                height: 15vw;
                border-radius: 50%;
                .icon{
                    left: -2vw; top: -.3vw;
                    width: 18.9vw;
                    height: 18.9vw;
                    
                }
                .name{
                    left: 0; top: 100%;
                    width: 15vw;height: 3vw;
                    line-height: 3vw;
                }
            }
            .describe{
                left: 28vw; top: 5vw;
                width: 48vw;
                height: 36vw;
                overflow: scroll;
                // background: #000;
            }
            .describe::-webkit-scrollbar{
                display: none;
            }
        }
    }
    
}
.warrior_info.active{
    left: -100vw;
    
}
</style>