<template>
    <div class="setup_panel" ref="setup_panel">
        <!-- 数据处理 -->
        <div class="data_deal" ref="data_deal">
            <div class="title">数据清理</div>
            <div class="clear_warrior_data_btn" ref="clear_warrior_data_btn" @touchstart='BtnAnimation(0,true)' @touchend='ClearData(0,"warrior")'>清除武将数据</div>
            <div class="clear_team_data_btn" ref="clear_team_data_btn"  @touchstart='BtnAnimation(1,true)' @touchend="ClearData(1,'team')">清除队伍数据</div>
            <div class="clear_skill_data_btn" ref="clear_skill_data_btn" @touchstart='BtnAnimation(2,true)' @touchend='ClearData(2,"skill")'>清除战法数据</div>
            <!-- 账号管理 -->
            <div class="user_title">账号管理</div>
            <div class="user_management" ref="user_management" @touchstart="$refs.user_management.classList.add('active')" 
            @touchend="$refs.user_management.classList.remove('active')" @click="UserPanel">账号管理</div>
        
        </div>

        <div class="user_management_panel remove" ref="user_management_panel">
            <div class="user_container">
                <div class="user_tape" v-for="(u,index) in all_users" :key="u.user_id" ref="user_tape">
                    <div class="name">{{u.user_name}}</div>
                    <div class="warrior_number">武将个数: {{u.user_warriors.length}}</div>
                    <div class="skills_number">战法个数: {{u.user_skills.length}}</div>
                    <div class="detail_btn" ref="detail_btn" @touchstart="$refs.detail_btn[index].classList.add('click')"
                    @touchend="$refs.detail_btn[index].classList.remove('click')" @click="OpenThisDetail(index)">操作</div>
                    <div class="time">创建时间: {{u.date_year}}/{{u.date_month}}/{{u.date_day}}</div>
                    <div class="user_info">{{u.user_info}}</div>

                    <div class="import_data_btn" ref="import_data_btn" @touchstart="$refs.import_data_btn[index].classList.add('click')"
                    @touchend="$refs.import_data_btn[index].classList.remove('click')"
                    @click="ImportData">导入该存档</div>
                    <div class="delete_data_btn" ref="delete_data_btn" @touchstart="$refs.delete_data_btn[index].classList.add('click')"
                    @touchend="$refs.delete_data_btn[index].classList.remove('click')"
                    @click="DeleteThisSave">删除该存档</div>
                    <div class="cover_data_btn" ref="cover_data_btn" @touchstart="$refs.cover_data_btn[index].classList.add('click')"
                    @touchend="$refs.cover_data_btn[index].classList.remove('click')" @click="IsUpdateSave" >更新存档</div>
                    <div class="cancel_btn" ref="cancel_data_btn" @touchstart="$refs.cancel_data_btn[index].classList.add('click')"
                    @touchend="$refs.cancel_data_btn[index].classList.remove('click')" @click="OpenThisDetail(index)">取消</div>
                </div>
            </div>
            <div class="add_user_btn" ref="add_user_btn" @touchstart="$refs.add_user_btn.classList.add('click')" @touchend="$refs.add_user_btn.classList.remove('click')"
            @click="AddUser">添 加 新 存 档</div>
            <div class="return_settings_btn" ref="return_settings_btn"
            @touchstart="$refs.return_settings_btn.classList.add('click')" @touchend="$refs.return_settings_btn.classList.remove('click')"
            @click="ClosePanel">返&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;回</div>
        </div>

        <!-- 新存档面板 -->
        <div class="new_save_panel" ref="new_save_panel">
            <div class="head">存档</div>

            <input type="text" class="user_name" placeholder="存档名字" ref="user_name">
            <input type="text" class="user_info" placeholder="备注" ref="user_info">

            <!-- 存档 -->
            <div class="info">是否将当前<span> {{user_warriors.length}}</span> 个武将数据和<span> {{user_skills.length}}</span> 战法数据存储为新的存档?</div>





            <!-- 确定按钮 -->
            <div class="confirm_cancel_btn" ref="confirm_btn1" @touchstart="$refs.confirm_btn1.classList.add('click')"
            @touchend="$refs.confirm_btn1.classList.remove('click')"
            @click="ConfirmSaveThis">确定</div>
            <!-- 取消按钮 -->
            <div class="confirm_cancel_btn" ref="cancel_btn1" @touchstart="$refs.cancel_btn1.classList.add('click')"
            @touchend="$refs.cancel_btn1.classList.remove('click')"
            @click="CancelSaveThis">取消</div>
        </div>

        <!-- 删除存档提示面板 -->
        <div class="import_or_delete_mask" v-show="isAlert">
            <div class="alert_container">
                <div class="info">确认删除该存档吗</div>
                <div class="confirm_btn" ref="confirm_select_save" 
                @click="DeleteThisSaveConfirm"  @touchstart="$refs.confirm_select_save.classList.add('click')" @touchend="$refs.confirm_select_save.classList.remove('click')">确 定</div>
                <div class="cancel_btn" ref="cancel_delete_save"
                @click="CancelDeleteSave" @touchstart="$refs.cancel_delete_save.classList.add('click')" @touchend="$refs.cancel_delete_save.classList.remove('click')">取消</div>
            </div>
            
        </div>
        <!-- 更新存档提示面板 -->
        <div class="import_or_delete_mask" v-show="isUpDataAlert">
            <div class="alert_container">
                <div class="info">确认将当前账号数据覆盖该存档吗</div>
                <div class="confirm_btn" ref="confirm_select_save1" 
                @click="CoverThisSave"  @touchstart="$refs.confirm_select_save1.classList.add('click')" @touchend="$refs.confirm_select_save1.classList.remove('click')">确 定</div>
                <div class="cancel_btn" ref="cancel_delete_save1"
                @click="CancelCover" @touchstart="$refs.cancel_delete_save1.classList.add('click')" @touchend="$refs.cancel_delete_save1.classList.remove('click')">取消</div>
            </div>
            
        </div>
        
        
    </div>
      
</template>

<script>
export default {
    name:'Settings',
    data(){
        return{
            USER_TABLE:[],
            all_users:[],
            user_warriors:[],
            user_skills:[],

            current_index : -1,
            isAlert :false,
            isUpDataAlert:false,//更新面板
        }
    },
    methods:{
        //按钮动画
        BtnAnimation(item,status){
            if(status){
                if(item == 0){
                    this.$refs.clear_warrior_data_btn.classList.add('active');
                }else if(item == 1){
                    this.$refs.clear_team_data_btn.classList.add('active');
                }else{
                    this.$refs.clear_skill_data_btn.classList.add('active');
                }
            }else{
                if(item == 0){
                    this.$refs.clear_warrior_data_btn.classList.remove('active');
                }else if(item == 1){
                    this.$refs.clear_team_data_btn.classList.remove('active');
                }else{
                    this.$refs.clear_skill_data_btn.classList.remove('active');
                }
            }
        },
        //清除数据
        ClearData(item,type){
            if(type == 'warrior'){
                //清理武将数据
                if(localStorage.getItem('stzb_my_warriors')){
                    //存在数据,则删除
                    //console.log(localStorage.removeItem);
                    localStorage.removeItem('stzb_my_warriors');
                    //同时更新武将表状态
                    this.$bus.$emit('ClearWarriors')
                    //状态反馈
                    this.$bus.$emit('alert','武将数据已清除');
                }else{
                    //状态反馈
                    this.$bus.$emit('alert','没有数据');
                }
            }else if(type == 'skill'){
                if(localStorage.getItem('stzb_my_skill')){
                    localStorage.removeItem('stzb_my_skill');
                    //更新战法界面
                    this.$bus.$emit('UpDataSkillsInterface');
                    //状态反馈
                    this.$bus.$emit('alert','战法数据已清除');
                }else{
                    //状态反馈
                    this.$bus.$emit('alert','没有数据');
                }
            }else if(type == 'team'){
                if(localStorage.getItem('stzb_my_team')){
                    localStorage.removeItem('stzb_my_team');
                    localStorage.removeItem('stzb_my_skill_model');
                    this.$bus.$emit('UpdateTeamInterface');

                    this.$bus.$emit('alert','队伍数据已清除');
                }
            }
            
            this.BtnAnimation(item,false);
            
        },
        //打开用户面板
        UserPanel(){
            this.$refs.data_deal.classList.add('remove');
            this.$refs.user_management_panel.classList.remove('remove');
        },
        //关闭用户面板
        ClosePanel(){
            this.$refs.data_deal.classList.remove('remove');
            this.$refs.user_management_panel.classList.add('remove')
        },
        //添加用户存档
        AddUser(){
            this.$refs.new_save_panel.classList.add('show');
            //检查是否有我的武将数据
            if(localStorage.getItem('stzb_my_warriors')){
                //如果我有保存的数据
                this.user_warriors = JSON.parse(localStorage.getItem('stzb_my_warriors'));//获得数据
                //并且更新展示表的数据
            }
            if(localStorage.getItem('stzb_my_skill')){
                //如果有被保存的技能
                this.user_skills = JSON.parse(localStorage.getItem('stzb_my_skill'));
            }
        },
        //保存当前用户
        ConfirmSaveThis(){
            if(this.$refs.user_name.value == ''){
                this.$bus.$emit('alert','名字不能为空！');
                return;
            }
            if(this.$refs.user_info.value == ''){
                this.$bus.$emit('alert','备注不能为空！');
                return;
            }
            let time = new Date();
            
            let user = {
                user_name : this.$refs.user_name.value,
                user_info : this.$refs.user_info.value,
                date_year : time.getFullYear(),
                date_month : time.getMonth() + 1,
                date_day : time.getDate(),
                user_id : Math.floor(Math.random()*99999),
                user_warriors : this.user_warriors,//武将
                user_skills : this.user_skills,
            }
            this.all_users.push(user);
            localStorage.setItem('stzb_all_users_data',JSON.stringify(this.all_users));
            this.$refs.user_name.value = '';
            this.$refs.user_info.value = '';
            this.$bus.$emit('alert','保存成功')
            setTimeout(()=>{
                this.$refs.new_save_panel.classList.remove('show');
            },100)
        },
        //取消保存该账户
        CancelSaveThis(){
            setTimeout(()=>{
                this.$refs.new_save_panel.classList.remove('show');
            },100)
        },
        //打开详情
        OpenThisDetail(index){
            if(this.current_index == index){
                this.$refs.user_tape[index].classList.remove('extend');
                this.current_index = -1;
            }else{
                for(let i=0;i<this.all_users.length;i++){
                    this.$refs.user_tape[i].classList.remove('extend');
                }
                this.current_index = index;
                this.$refs.user_tape[index].classList.add('extend');
            }
            
        },
        //导入存档数据到当前账号
        ImportData(){
            setTimeout(()=>{
                //先清除旧武将
                this.$bus.$emit('ClearWarriors');
                localStorage.setItem('stzb_my_warriors',JSON.stringify(this.all_users[this.current_index].user_warriors));
                localStorage.setItem('stzb_my_skill',JSON.stringify(this.all_users[this.current_index].user_skills));
                //除去队伍
                // if(localStorage.getItem('stzb_my_team')){
                //     localStorage.removeItem('stzb_my_team');
                //     localStorage.removeItem('stzb_my_skill_model');
                // }
                //同时更新武将表状态
                this.$bus.$emit('UpdateWarriors')
                //更新战法界面
                this.$bus.$emit('UpDataSkillsInterface');
                
                this.$bus.$emit('alert','存档覆盖成功')
            },100)
        },
        //是否删除该存档
        DeleteThisSave(){
            this.isAlert = true;
        },
        //确定删除
        DeleteThisSaveConfirm(){
            this.all_users.splice(this.current_index,1);
            localStorage.setItem('stzb_all_users_data',JSON.stringify(this.all_users));
            this.current_index = -1;
            this.$bus.$emit('alert','成功删除存档')
            setTimeout(()=>{
                this.isAlert = false;
            },50)
        },
        //取消删除
        CancelDeleteSave(){
            setTimeout(()=>{
                this.isAlert = false;
            },50)
        },
        //是否更新存档
        IsUpdateSave(){
            this.isUpDataAlert = true;
        },
        //覆盖该存档数据
        CoverThisSave(){
            if(localStorage.getItem('stzb_my_warriors')){
                //如果我有保存的数据
                this.user_warriors = JSON.parse(localStorage.getItem('stzb_my_warriors'));//获得数据
                //并且更新展示表的数据
            }
            if(localStorage.getItem('stzb_my_skill')){
                //如果有被保存的技能
                this.user_skills = JSON.parse(localStorage.getItem('stzb_my_skill'));
            }
            this.all_users[this.current_index].user_warriors = this.user_warriors//武将
            this.all_users[this.current_index].user_skills = this.user_skills//武将
            localStorage.setItem('stzb_all_users_data',JSON.stringify(this.all_users));
            this.current_index = -1;
            this.$bus.$emit('alert','存档数据更新成功')
            setTimeout(()=>{
                this.isUpDataAlert = false;
            },50)
        },
        //取消覆盖存档
        CancelCover(){
            setTimeout(()=>{
                this.isUpDataAlert = false;
            },50)
        }
    },
    mounted(){
        //this.$refs.setup_panel.classList.add('active');
        if(localStorage.getItem('stzb_all_users_data')){
            //如果我有保存的数据
            this.all_users = JSON.parse(localStorage.getItem('stzb_all_users_data'));//获得数据
            //并且更新展示表的数据
        }
    },
    beforeDestroy(){
        //this.$refs.setup_panel.classList.remove('active');
    }
}
</script>

<style lang='less'>
.setup_panel{
    position: absolute; left: 0; top : 0;
    width : 100%; height : 100%;
    background: url('../../assets/background/bg (1).jpg')no-repeat;
    background-size: cover;
    // background: url('../assets/main/bg19.png')no-repeat;
    background-size: 100% 100%;
    // animation:setup_panel .6s ease-out 1;
    @keyframes setup_panel {
       0%{
          transform: scale(2);
       }
       20%{
          transform: scale(2);
       }
       100%{
          transform: scale(1);
       }
    }
    .import_or_delete_mask{
        position: absolute; left: 0; top : 0;
        width : 100%; height : 100%;
        background: rgba(221, 221, 221, 0.6);
        z-index: 5;
        .alert_container{
            position: absolute; left: 0; top : 50%;
            margin-top: -30vw;
            width : 100%; height : 60vw;
            background: url('../../assets/background/bg (3).png')no-repeat;
            background-size: 100% 100%;
            animation: alert_container .1s ease-out 1;
            @keyframes alert_container {
               0%{
                  transform: scale(.9);
               }
               100%{
                  transform: scale(1);
               }
            }
            .info{
                position: absolute; left: 50%; top : 50%;
                transform : translate(-50%,-50%);
                width: 60vw;
                height: 30vw;
                font-size: 4vw;
                text-align: center;
                line-height: 30vw;
                color: rgb(222, 222, 222);
            }
            .confirm_btn,.cancel_btn{
                position: absolute; left: 50%; bottom : 10%;
                margin: 0 0 0 -35vw;
                width: 30vw;
                height: 6vw;
                background: rgb(215, 215, 215);
                line-height: 6vw;
                text-align: center;
                font-size: 2.5vw;
                color: black;
                transition: all .1s;
            }
            .confirm_btn.click,.cancel_btn.click{
                transform: scale(.9);
            }
            .cancel_btn{
                margin: 0 0 0 5vw;
            }
        }
    }
    .data_deal{
        position: absolute; left:0; top : 0;
        width: 100vw;
        height: 160vw;
        //box-shadow: 3px 3px 10px black;
        transition: all .3s;
        overflow-y: scroll;
        overflow-x: hidden;

        
        .title,.user_title{
            position: relative;
            width: 100%;
            height: 15vw;
            margin: 30vw 0 0 0;
            line-height: 15vw;
            text-align: center;
            font-size: 5vw;
            font-weight: bold;
            color: rgb(209, 204, 204);
        }
        .user_title{
            margin: 10vw 0 0 0;
        }
        .clear_warrior_data_btn,.clear_team_data_btn,.clear_skill_data_btn,.user_management{
            position: relative;
            width: 50vw;height: 8vw;
            margin: 7vw 0 0 25vw;
            background: linear-gradient(to right bottom,rgb(111, 104, 104),rgb(95, 89, 89));
            text-align: center;
            font-size: 3vw;
            line-height: 8vw;
            color: rgb(237, 237, 237);
            animation: set_btn_move .2s linear 1;
            @keyframes set_btn_move {
               0%{
                  margin: 10vw 0 0 25vw;
               }
               100%{
                  margin: 7vw 0 0 25vw;
               }
            }
            transition: all .1s;
        }
        // .clear_team_data_btn{
        //     background: rgb(0, 28, 98);
        // }
        // .clear_skill_data_btn{
        //     background: rgb(0, 101, 81);
        // }
        .clear_warrior_data_btn.active,.clear_team_data_btn.active,.clear_skill_data_btn.active,.user_management.active{
            opacity: .8;
            transform: scale(.9);
        }
    }
    .data_deal.remove{
        transform: translateX(100vw);
    }
    .user_management_panel{
        position: absolute; left: 0; top : 15vh;
        width : 100%; height : 120vw;
        transition: all .2s;
        .user_container{
            position: absolute; left: 0vw; top : 0;
            width : 100vw; height : 100%;
            overflow-y: scroll;
            overflow-x: hidden;
            .user_tape{
                position: relative;
                width: 90vw;
                height: 20vw;
                background: rgb(68, 68, 68);
                background-size: 100% 100%;
                margin: 0 0 5vw 5vw;
                transition: all .2s;
                overflow: hidden;
                .name{
                    position: absolute; left: 2.2vw; top: 1.7vw;
                    width: 40vw;
                    height: 7vw;
                    text-align: center;
                    line-height: 7vw;
                    color: white;
                    font-size: 3vw;
                    background: rgba(0, 0, 0, 0.788);
                }
                .warrior_number,.skills_number,.user_info{
                    position: absolute; left: 2.2vw; top: 12.5vw;
                    width: 30vw;
                    height: 6vw;
                    text-align: center;
                    line-height: 6vw;
                    color: white;
                    font-size: 3vw;
                    background: rgb(67, 2, 2);
                }
                .user_info{
                    background: rgba(67, 2, 2, 0);
                    width: 80vw;
                    top: 33vw;
                    line-height: 4vw;
                }
                .skills_number{
                    left: 34.2vw;
                    background: rgb(111, 5, 70);
                }
                .detail_btn,.cancel_btn{
                    position: absolute; right: 3vw; bottom: 1.5vw;
                    width: 17vw;
                    height: 6vw;
                    text-align: center;
                    line-height: 6vw;
                    font-size: 2vw;
                    color: rgb(46, 43, 43);
                    background: rgb(235, 226, 226);
                    transition: all .1s;
                }
                .detail_btn.click,.cancel_btn.click,.import_data_btn.click,.delete_data_btn.click,.cover_data_btn.click{
                    transform: scale(.9);
                }
                .time{
                    position: absolute; left: 2.2vw; top: 22vw;
                    width: 50vw;
                    height: 6vw;
                    text-align: center;
                    line-height: 6vw;
                    color: white;
                    font-size: 3vw;
                    background: rgb(5, 99, 106);
                }
                .import_data_btn,.delete_data_btn,.cover_data_btn{
                    position: absolute; left: 2.2vw; top: 46.5vw;
                    width: 30vw;
                    height: 6vw;
                    text-align: center;
                    line-height: 6vw;
                    color: rgb(52, 52, 52);
                    font-size: 3vw;
                    background: rgb(208, 208, 208);
                    transition: all .1s;
                }
                .delete_data_btn{
                    left: 34.2vw;
                }
                .cover_data_btn{
                    margin: -15vw 0 0 0;
                    top: 2vw;
                    left: 68vw;
                    width: 20vw;

                }
                .cancel_btn{
                    display: none;

                }
            }
            .user_tape.extend{
                height:54vw;
                .detail_btn{
                    display: none;
                }
                .cancel_btn{
                    display: block;
                }
                .cover_data_btn{
                    margin: 0 0 0 0;
                }
                
            }
            .user_tape:nth-of-type(1){
                margin: 5vw 0 5vw 5vw;
                
            }
        }
        .add_user_btn,.return_settings_btn{
            position: absolute; left: 30vw; bottom: -15vw;
            width: 40vw;
            height: 8vw;
            line-height: 8vw;
            font-size: 3vw;
            color: white;
            text-align: center;
            background: rgb(45, 43, 43);
        }
        .add_user_btn.click,.return_settings_btn.click{
            transform: scale(.9);
        }
        .return_settings_btn{
            bottom: -26vw;
        }

    }
    .user_management_panel.remove{
        transform: translateX(100vw);
    }

    .new_save_panel{
        
        position: fixed;
        left: 0; top: 0;
        width: 100vw;
        height: 100vh;
        min-height: 200vw;
        background: url('../../assets/background/bg (1).jpg')no-repeat;
        background-size: 100% 100%;
        z-index: 10;
        display: none;
        .head{
            position: absolute; left: 0; top: 0;
            width: 100vw;
            height: 25vw;
            background:linear-gradient(to bottom,black,rgb(72, 1, 1));
            text-align: center;
            line-height: 33vw;
            font-size: 5vw;
            color: rgb(175, 167, 167);
            font-weight: bold;
        }
        .user_name,.user_info{
            position: absolute; left: 10vw; top: 35vw;
            width: 80vw;
            height: 8vw;
            background: rgb(67, 63, 63);
            color: white;
            font-size: 4vw;
            border: 0;
        }
        .user_info{
            top: 50vw;
            font-size: 4vw;
            
        }
        .info{
            position: absolute; left: 10vw; top: 65vw;
            width: 80vw;
            height: 8vw;
            font-size: 4vw;
            color: rgb(174, 170, 170);
            span{
                font-size: 4vw;
                color: aqua;
            }
        }
        .confirm_cancel_btn{
            position: absolute; left: 10vw; top: 95vw;
            width: 30vw;
            height: 6vw;
            text-align: center;
            font-size: 4vw;
            font-weight: bold;
            line-height: 6vw;
            background: rgb(234, 234, 234);
            color: rgb(51, 48, 48);
            transition: all .05s;
        }
        .confirm_cancel_btn.click{
            background: rgb(255, 255, 255);
            transform: scale(.9);
