    //武将等级修改
    AdjustWarriorLevel(level){
        this.level = level;
        //level为要修改成的等级
        this.current_attr = {
            ad : Math.floor(this.base_attr.ad + level * this.grow.ad),
            tact : Math.floor(this.base_attr.tact + level * this.grow.tact),
            de : Math.floor(this.base_attr.de + level * this.grow.de),
            speed : Math.floor(this.base_attr.speed + level * this.grow.speed),
            demolition:Math.floor(this.base_attr.demolition + level * this.grow.demolition),
            hp : 100*level,
            att : this.att,
        }
    }
    //计算属性点分配总点数
    CountAttrIndex(){
        //每次总属性点变动的时候需要重新分配
        this.plus_attr = {
            all : 0,
            avail: 0,//可分配点数
            ad : 0 ,
            de  :0,
            tact : 0,
            speed : 0,
        }
        if(this.gender == '女'){
            this.plus_attr.all = Math.floor(this.level/10)*15 + this.red*10;
        }else{
            this.plus_attr.all = Math.floor(this.level/10)*10 + this.red*10;
        }
        
    }
    //载入主战法,将武将安装到配置面板上时候载入
    LoadingMainTactics(){
        if(this.camp == '群'){
            for(let i=0;i<qun.length;i++){
                if(qun[i].id == this.id){
                    //匹配到了。开始植入
                    this.TACTICS_STACK.push(qun[i].tactics);
                }
            }
        }else if(this.camp == '魏'){
            for(let i=0;i<wei.length;i++){
                if(wei[i].id == this.id){
                    //匹配到了。开始植入
                    this.TACTICS_STACK.push(wei[i].tactics);
                }
            }
        }else if(this.camp == '蜀'){
            for(let i=0;i<shu.length;i++){
                if(shu[i].id == this.id){
                    //匹配到了。开始植入
                    this.TACTICS_STACK.push(shu[i].tactics);
                }
            }
        }else if(this.camp == '吴'){
            for(let i=0;i<wu.length;i++){
                if(wu[i].id == this.id){
                    //匹配到了。开始植入
                    this.TACTICS_STACK.push(wu[i].tactics);
                }
            }
        }else if(this.camp == '汉'){
            for(let i=0;i<han.length;i++){
                if(han[i].id == this.id){
                    //匹配到了。开始植入
                    this.TACTICS_STACK.push(han[i].tactics);
                }
            }
        }
        else if(this.camp == '晋'){
            for(let i=0;i<jin.length;i++){
                if(jin[i].id == this.id){
                    //匹配到了。开始植入
                    this.TACTICS_STACK.push(jin[i].tactics);
                }
            }
        }
    }
    //分配属性点
    DistributeAttrIndex(type,value,empty){
        //对某个属性项进行清除属性点操作
        if(empty){
            if(type == 'ad'){
                this.plus_attr.avail = this.plus_attr.ad + this.plus_attr.avail;
                this.plus_attr.ad  = 0;

            }else if(type == 'de'){
                this.plus_attr.avail = this.plus_attr.de + this.plus_attr.avail;
                this.plus_attr.de = 0;
            }else if(type == 'tact'){
                this.plus_attr.avail = this.plus_attr.tact + this.plus_attr.avail;
                this.plus_attr.tact = 0;
            }else{
                this.plus_attr.avail = this.plus_attr.speed + this.plus_attr.avail;
                this.plus_attr.speed = 0;
            }
            return true;
        }
        this.plus_attr.avail = this.plus_attr.all - (this.plus_attr.ad + this.plus_attr.de + this.plus_attr.tact + this.plus_attr.speed)
        //如果值是负数表示返还属性点
        if(value > 0){
            if(this.plus_attr.avail < value){
                //没有可以分配的点数了
                return false;
            }
        }
        if(type == 'ad'){
            if(value < 0 && this.plus_attr.ad <= 0){
                return;
            }
            this.plus_attr.ad += value;
        }else if(type == 'de'){
            if(value < 0 && this.plus_attr.de <= 0){
                return;
            }
            this.plus_attr.de += value;
        }else if(type == 'tact'){
            if(value < 0 && this.plus_attr.tact <= 0){
                return;
            }
            this.plus_attr.tact += value;
        }else{
            if(value < 0 && this.plus_attr.speed <= 0){
                return;
            }
            this.plus_attr.speed += value;
        }
        //可分配的点数减少对应值
        this.plus_attr.avail -= value;
        // console.log(this.plus_attr.avail);
    }
    //智能分配属性点
    AutoDistinctAttr(){
        let max = 0;
        max = this.current_attr.ad;
        
    }
