    //-----------------------------------------------状态判断(start)-----------------------------------------------
    //造成伤害后立即判定目标是否死亡，且是否是大营单位,返回true表示大营阵亡，游戏结束
    IsGeneralDead(target){
        if(target.current_attr.hp <= 0){
            //死亡之后把所有自己来执行的执行掉

            // target.ClearDAMAGE_VAR_HEAP();

            // target.ClearSTATUS();
 
            // target.ConsumeATTR_VAR_HEAP();

            // target.ConsumeACTIVE_VAR_HEAP();

            //生成战报
            let report = new Report();
            report.InitVersaReport(target,1,`${target.name}兵力不足，无法再战！`);
            this.vue.REPORT_HEAP.push(report);
            
            target.current_attr.hp = 0;
            target.exist = false;
            
            if(target.position == 2){
                //生成战报
                let report = new Report();
                report.InitVersaReport(target,1,`大营兵力不足，战斗结束`);
                this.vue.REPORT_HEAP.push(report);
                return true;
            }
        }
        return false;
    }
