    //-----------------------------------------------伤害计算(end)-----------------------------------------------
    //计算普通攻击的伤害
    CountAttackDamage(initiator,receiver,mis_arms,mis_dodge){
        if(receiver.current_attr.hp <= 0){
            return 0;
        }
        //initiator:攻击者
        //receiver接受者
        //mis_arms：是否无视兵种克制
        //mis_dodge：是否无视规避
        //基础数值
        let dodge = receiver.IsDodge();
        if(dodge){
            if(!mis_dodge){
                let report = new Report();
                report.InitVersaReport(receiver,2,`处于规避状态`)
                this.vue.REPORT_HEAP.push(report);
                //消耗一次性的规避
                receiver.ConsumeDodge();
                return 0;
            }
        }
        let damage_num = 4.5;
        //攻击者兵力影响调整
        damage_num = Math.floor(Math.sqrt(initiator.current_attr.hp) * damage_num);
        //防御计算,防御系数：攻击者的当前攻击力 + 200/接受者的当前防御力 + 200

        let ignore_defense = initiator.ReturnIgnoreDefense('防御');
        //有无视防御的存在
        if(ignore_defense > 0){
            if(ignore_defense > 90){
                ignore_defense = 90;
            }
            damage_num = ((initiator.current_attr.ad + 200) / ((receiver.current_attr.de * ( 1- ignore_defense/100)) + 200)) * damage_num;
        }
        else{
            damage_num = ((initiator.current_attr.ad + 200) / (receiver.current_attr.de + 200)) * damage_num;
        }
        
        //加上兵种克制
        if(!mis_arms){
            let arms_str = initiator.current_advanced.substring(0,1);
            let receiver_arm_str = receiver.current_advanced.substring(0,1);
            if(arms_str == 'b'){
                if(receiver_arm_str == 'q'){
                    if(initiator.current_advanced != 'b_c'){
                        if(receiver.current_advanced == 'q_z'){
                            damage_num = damage_num * 0.75;
                        }else{
                            damage_num = damage_num * 0.8;
                        }
                    }
                }else if(receiver.current_advanced == "g" && initiator.current_advanced == 'b_c'){
                    damage_num = damage_num * 0.8;
                }
            }else if(arms_str == 'q'){
                if(receiver_arm_str == 'g'){
                    if(initiator.current_advanced == 'q_z'){
                        if(receiver.current_advanced == 'g_n'){
                            damage_num = damage_num * 0.7;
                        }else{
                            damage_num = damage_num * 0.75;
                        }
                    }else{
                        if(receiver.current_advanced == 'g_n'){
                            damage_num = damage_num * 0.75;
                        }else{
                            damage_num = damage_num * 0.8;
                        }
                    }
                }
            }else if(arms_str == 'g'){
                if(receiver_arm_str == 'b'){
                    if(initiator.current_advanced == 'g_n'){
                        damage_num = damage_num * 0.75;
                    }else{
                        damage_num = damage_num * 0.8;
                    }
                }
            }
        }
        //增减伤数值计算
        let var_damage_factor = this.ReturnVarDamageCoefficient(initiator,receiver,'ad')
        //普通攻击的增减伤系数
        let var_common_attack_factor = this.ReturnCommonAttackCoefficient();
        
        damage_num = damage_num * (1 + var_damage_factor + var_common_attack_factor);

       

        //设置伤害浮动因子,将最终伤害的1/15作为伤害浮动
        let damage_float_factor = Math.floor(damage_num/15);
        //最后，造成伤害取整
        damage_num = Math.floor(damage_num + Math.random()*damage_float_factor);
        if(damage_num > receiver.current_attr.hp){
            damage_num = receiver.current_attr.hp
            receiver.exist = false;
        }
        //返回伤害
        return damage_num;
    }
