    //查重函数，传入需要查重的数组，和需要查重的字符串或者值，返回true表示重复，false表示没有重复
    DuplicateCheck(arr,target){
        for(let i=0;i<arr.length;i++){
            if(arr == target){
                return true;
            }
        }
        return false;
        
    }
    
    
