    //增伤减伤块的生命周期函数
    ConsumeDAMAGE_VAR_HEAP(){
        for(let i=0;i<2;i++){
            for(let j=0;j<3;j++){
                let temp = [];
                for(let k=0;k<this.vue.teams[i][j].DAMAGE_VAR_HEAP.length;k++){
                    //处理自己施加的块的生命周期
                    if(this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].origin_team == this.belong && this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].origin_warrior == Math.abs(this.position - 2)){
                        this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].remain_time -= 1;
                        
                        if(this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].remain_time > 0){
                            //如果此时块的时间<=0，块销毁
                            temp.push(this.vue.teams[i][j].DAMAGE_VAR_HEAP[k]);
                            // this.DAMAGE_VAR_HEAP.splice(k,1);
                        }
                        else{
                            let initiator = this.vue.teams[this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].origin_team][this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].origin_warrior]
                            let damage_type_str = "攻击伤害";
                            if(this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].damage_type == 'tact'){
                                damage_type_str = "策略攻击伤害"
                            }
                            else if(this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].damage_type == 'active'){
                                damage_type_str = "主动战法伤害"
                            }
                            else if(this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].damage_type == 'all'){
                                damage_type_str = "所有伤害"
                            }
                            
                            let report = new Report();
                            report.InitVersaReport(initiator,1,`【${this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].name}】的${this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].attack == true?"":"受到"}${damage_type_str}${this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].addition == true?"提高":"降低"}${this.vue.teams[i][j].DAMAGE_VAR_HEAP[k].value}%消失了`)
                            this.vue.REPORT_HEAP.push(report)
                        }
                    }else{
                        temp.push(this.vue.teams[i][j].DAMAGE_VAR_HEAP[k]);
                    }
                }

                this.vue.teams[i][j].DAMAGE_VAR_HEAP = temp;
                
            }
        }

    }
    //基础状态块的生命周期
    ConsumeSTATUS(){
        for(let i=0;i<2;i++){
            for(let j=0;j<3;j++){
                let temp = [];
                for(let k=0;k<this.vue.teams[i][j].STATUS.length;k++){
                    if(this.vue.teams[i][j].STATUS[k].origin_team == this.belong && this.vue.teams[i][j].STATUS[k].origin_warrior == Math.abs(this.position - 2)){
                        this.vue.teams[i][j].STATUS[k].remain_time -= 1;
                        if(this.vue.teams[i][j].STATUS[k].remain_time <= 0){
                            let report = new Report();
                            report.InitVersaReport(this,1,`来自【${this.vue.teams[i][j].STATUS[k].name}】的${this.vue.teams[i][j].STATUS[k].status_type}效果消失了`);
                            this.vue.REPORT_HEAP.push(report);
                        }else{
                            temp.push(this.vue.teams[i][j].STATUS[k]);
                        }
                        
                    }else{
                        //不属于自己该执行的块要带过来
                        temp.push(this.vue.teams[i][j].STATUS[k]);
                    }
                }

                this.vue.teams[i][j].STATUS = temp;
            }
        }
        
    }
    //消耗堆区的属性控制块
    ConsumeATTR_VAR_HEAP(){
        
        for(let i=0;i<2;i++){
            for(let j=0;j<3;j++){
                let temp = [];
                for(let k=0;k<this.vue.teams[i][j].ATTR_VAR_HEAP.length;k++){
                    if(this.vue.teams[i][j].ATTR_VAR_HEAP[k].origin_team == this.belong && this.vue.teams[i][j].ATTR_VAR_HEAP[k].origin_warrior == Math.abs(this.position - 2)){
                        this.vue.teams[i][j].ATTR_VAR_HEAP[k].remain_time -= 1;
                        if(this.vue.teams[i][j].ATTR_VAR_HEAP[k].remain_time <= 0){
                            this.vue.teams[i][j].ATTR_VAR_HEAP[k].back(this);
                        }else{
                            temp.push(this.vue.teams[i][j].ATTR_VAR_HEAP[k]);
                        }
                    }else{
                        temp.push(this.vue.teams[i][j].ATTR_VAR_HEAP[k]);
                    }
                }

                this.vue.teams[i][j].ATTR_VAR_HEAP = temp;
            }
        }
        
    }
