                        <transition name="small_to_big_fade">
                        <div class="skill_mask" v-show="!c.acquired"></div>
                        </transition>
                    </div>
                </div>
                <!-- 追击战法 -->
                <div class="title">
                    <div style="float:left;background:rgb(107, 0, 153);width:1.5vw;height:100%;"></div>
                    <div class="text" style="float:left;font-size:2.5vw;black:white;font-weight:bold;line-height:7vw;text-align:center;width:15vw;height:100%;">追击战法</div>
                </div>
                <div class="combo_skill_container">
                    <div class="skill_box" v-for="(c,c_index) in combo_skills" :key="c.index" @click="LoadThisSkill('combo',c_index)" v-show="FilterTactics(c)">
                        <img src="../../assets/ui/ui (94).png" alt="" class="skill_image">
                        <img src="../../assets/ui/ui (90).png" alt="" class="skill_frame" v-show="c.quality == 'S'">
                        <img src="../../assets/ui/ui (88).png" alt="" class="skill_frame" v-show="c.quality == 'A'">
                        <img src="../../assets/ui/ui (89).png" alt="" class="skill_frame" v-show="c.quality == 'B'">
                        <div class="name">{{c.name}}</div>
                        <!-- 未拥有的遮罩层 -->
                        <transition name="small_to_big_fade">
                        <div class="skill_mask" v-show="!c.acquired"></div>
                        </transition>
                    </div>
                </div>
                <!-- 被动战法 -->
                <div class="title">
                    <div style="float:left;background:rgb(0, 64, 153);width:1.5vw;height:100%;"></div>
                    <div class="text" style="float:left;font-size:2.5vw;black:white;font-weight:bold;line-height:7vw;text-align:center;width:15vw;height:100%;">被动战法</div>
                </div>
                <div class="passive_skill_container">
                    <div class="skill_box" v-for="(c,c_index) in passive_skills" :key="c.index" @click="LoadThisSkill('passive',c_index)" v-show="FilterTactics(c)">
                        <img src="../../assets/ui/ui (91).png" alt="" class="skill_image">
                        <img src="../../assets/ui/ui (90).png" alt="" class="skill_frame" v-show="c.quality == 'S'">
                        <img src="../../assets/ui/ui (88).png" alt="" class="skill_frame" v-show="c.quality == 'A'">
                        <img src="../../assets/ui/ui (89).png" alt="" class="skill_frame" v-show="c.quality == 'B'">
                        <div class="name">{{c.name}}</div>
                        <!-- 未拥有的遮罩层 -->
                        <transition name="small_to_big_fade">
                        <div class="skill_mask" v-show="!c.acquired"></div>
                        </transition>
                    </div>
                </div>
            </div>

            <!-- 卸载战法 -->
            <div class="unload_skill_btn" ref="unload_skill_btn" @touchstart="$refs.unload_skill_btn.classList.add('click')"
            @touchend="$refs.unload_skill_btn.classList.remove('click')"
            @click="UnloadThisSkill">卸下战法</div>
        </div>

        <!-- 卸载武将提示面板 -->
        <div class="alert_panel" v-show="status.isUnloadWarrior">
            <div class="mask cover" @click="status.isUnloadWarrior = false"></div>
            <transition name="bubble">
            <div class="container" v-show="status.isUnloadWarrior">
                <div class="close_btn" ref="close_btn0909" @touchstart="$refs.close_btn0909.classList.add('click')" 
                @touchend="$refs.close_btn0909.classList.remove('click')" @click="status.isUnloadWarrior = false"></div>
                <div class="info">
                    <span>是否将</span>
                    <span style="color: rgb(238, 0, 255);">【{{team[current_team].warriors[current_warrior].camp}}-{{team[current_team].warriors[current_warrior].name}}-{{team[current_team].warriors[current_warrior].arms}}】</span>
                    
                    <span>移除出队伍？</span>
                </div>
                <div class="confirm_btn" ref="confirm_btn" @touchstart="$refs.confirm_btn.classList.add('click')" 
                @touchend="$refs.confirm_btn.classList.remove('click')" @click="UnloadWarrior">确定</div>
            </div>
            </transition>
        </div>
        <!-- 删除队伍提示面板 -->
        <div class="alert_panel" v-show="status.isDeleteThisTeam">
            <div class="mask cover" @click="status.isDeleteThisTeam = false"></div>
            <transition name="bubble">
            <div class="container" v-show="status.isDeleteThisTeam">
                <div class="close_btn" ref="close_btn0901" @touchstart="$refs.close_btn0901.classList.add('click')" 
                @touchend="$refs.close_btn0901.classList.remove('click')" @click="status.isDeleteThisTeam = false"></div>
                <div class="info">
                    移除这支队伍？
                </div>
                <div class="confirm_btn" ref="confirm_btn2" @touchstart="$refs.confirm_btn2.classList.add('click')" 
                @touchend="$refs.confirm_btn2.classList.remove('click')" @click="DeleteThisTeam">确定</div>
            </div>
            </transition>
        </div>
        

    </div>
</template>

<script>

import TeamWarrior from '../../mixin/structure/TeamWarrior'//导入创建空武将的类
import TeamSkills from '../../mixin/structure/TeamSkills'//导入创建空武将的类

import TeamWarriorNode from '../../mixin/structure/TeamWarriorNode'//导入用于保存队伍数据的结点
import TeamInfoVue from './TeamInfo.vue'


import skills from '../../mixin/warrior/skills'
export default {
    name : 'MyTeam',
    props:['all_warriors'],
    components:{
        TeamInfoVue
    },
    mixins:[skills],
    data(){
        return{
            // all_warriors : [],
            refresh_skill:true,//用于战法数据安装刷新页面使得界面正常显示最新的战法安转
            fresh_flag : true,//同上
            status:{
                //打开编辑武将面板
                isShowEditTeam : false,
                //选择武将
                isSelectWarriors:false,
                //切换安转战法或者武将
                switchWarriorOrSkill : false,
                //卸下武将的提示框
                isUnloadWarrior:false,
                //删除队伍提示框
                isDeleteThisTeam : false,
                
            },
            team:[
                {
                    warriors:[
                        {} = new TeamWarrior(),
                        {} = new TeamWarrior(),
                        {} = new TeamWarrior(),
                    ],//武将集合（前锋，中军，大营）
                    skills:[
                        [
                            {} = new TeamSkills(),
                            {} = new TeamSkills()
                        ],
                        [
                            {} = new TeamSkills(),
                            {} = new TeamSkills()
                        ],
                        [
                            {} = new TeamSkills(),
                            {} = new TeamSkills()
                        ]
                    ]
                }
            ],

            current_team : 0,//当前队伍序号
            current_warrior:0,//当前武将在队伍中的序号


            dirty:false,//脏位
            skill_current_warrior : 0,
            skill_current_position : 0,
            
        }
    },
    methods:{
        //过滤战法
        FilterTactics(target){
            if(!target.acquired){
                return false;
            }
            for(let i=0;i<this.team.length;i++){
                for(let j=0;j<3;j++){
                    for(let k=0;k<2;k++){
                        if(this.team[i].skills[j][k].id == target.id && target.learned.have != 2){
                            return false;
                        }
                    }
                }
            }
            return true;
            
        },
        //过滤武将
        FilterWarrior(target){
            for(let i = 0;i<this.team.length;i++){
                for(let j= 0;j<3;j++){
                    if(this.team[i].warriors[j].id == target.id || this.team[i].warriors[j].id + 1 == target.id || this.team[i].warriors[j].id -1  == target.id){
                        //找到相同武将，不能放入
                        return false;
                    }
                }
            }
            return true;
        },
        //进入或离开界面
        OpenOrClose(flag){
            //flag为真，表示打开本界面
            if(flag){
                this.$refs.my_team.classList.remove('active');
            }else{
                this.$refs.my_team.classList.add('active');
            }
        },
        //添加一个队伍实例
        NewTeam(){
            let new_team = {
                warriors:[
                    {} = new TeamWarrior(),
                    {} = new TeamWarrior(),
                    {} = new TeamWarrior(),
                ],//武将集合（前锋，中军，大营）
                skills:[
                        [
                            {} = new TeamSkills(),
                            {} = new TeamSkills()
                        ],
                        [
                            {} = new TeamSkills(),
                            {} = new TeamSkills()
                        ],
                        [
                            {} = new TeamSkills(),
                            {} = new TeamSkills()
                        ]
                    ]
                
            };
            return new_team
        },
        //添加一个队伍按钮
        AddTeam(){
            let new_team = this.NewTeam();
            //修改脏位
            this.dirty = true;
            this.team.push(new_team);
            let distance = (window.innerWidth/5) + 10;
            setTimeout(()=>{
                this.$refs.team_container.scrollTo(0,distance*(this.team.length-5));
            },10)
        },
        //把local的数据加载到界面
        UpdateIntoInterface(){
            let queue ;
            if(localStorage.getItem('stzb_my_team')){
                queue =  JSON.parse(localStorage.getItem('stzb_my_team'))
            }else{
                return;
            }
            let skill_queue;
            if(localStorage.getItem('stzb_my_skill_model')){
                skill_queue = JSON.parse(localStorage.getItem('stzb_my_skill_model'))
            }else{
                return
            }
            let p = 0;
            let queue_index = 0;
            for(let i=0;i<queue.length;i++){
                if(queue[i].empty){
                    queue_index += 1;
                }
            }
            for(let i=0;i<this.all_warriors.length;i++){
                //匹配到对应武将
                if(queue[queue_index].warrior_id == this.all_warriors[i].id){
                    if(queue[queue_index].team_index + 1 > this.team.length){
                        //如果说匹配到的武将在第n队，但是现在team数组没有n队，那么需要拓展队伍
                        let length = this.team.length;
                        for(let j=0;j<(queue[queue_index].team_index + 1) - length;j++){
                            let new_team = this.NewTeam();
                            this.team.push(new_team);
                        }
                    }
                    //把对应武将赋值给team模板里
                    this.team[queue[queue_index].team_index].warriors[queue[queue_index].warrior_index] = this.all_warriors[i];
                    queue_index++;
                    
                }
                if(queue_index == queue.length){
                    for(let i=0;i<this.team.length;i++){
                        for(let j =0;j<3;j++){
                            for(let k=0;k<2;k++){
                                this.team[i].skills[j][k] = new TeamSkills();
                                this.team[i].skills[j][k].getValue(skill_queue[p],skill_queue[p].type,skill_queue[p].use)
                                // console.log(skill_queue[p]);
                                p++;
                            }
                        }
                    }
                    return;
                }
            }
        },
        //编辑队伍
        EditTeam(index){
            this.current_team = index;
            this.$refs.team_info.classList.remove('active');
        },
        //关闭编辑界面
        CloseEditPanel(){
            this.status.isShowEditTeam = false;
        },
        //配置这个位置格子上的武将
        ConfigWarrior(w,id){
            this.current_warrior = id;
            if(w.name == ''){
                //这个位置上没有武将，则弹出武将池
                this.$refs.select_warrior_container.classList.add('active');
            }else{
                this.status.isUnloadWarrior = true;
            }
        },
        //把这个武将放到武将槽里
        IptThisWarrior(target,index){
            //team_index为当前队伍在全部队伍中的索引
            //warrior_index为放置武将的位置
            //target为要放置的目标武将
            //检查武将是否重复
            let repeat = false;
            for(let i = 0;i<this.team.length;i++){
                for(let j= 0;j<3;j++){
                    if(this.team[i].warriors[j].id == target.id || this.team[i].warriors[j].id + 1 == target.id || this.team[i].warriors[j].id -1  == target.id){
                        //找到相同武将，不能放入
                        repeat = true;
                    }
                }
            }
            //检查武将的cost值是否超过上限
            if(target.cost + this.team[this.current_team].warriors[(this.current_warrior + 1)%3].cost + this.team[this.current_team].warriors[(this.current_warrior + 2)%3].cost > 10){
                this.$bus.$emit('alert','队伍统帅值过高');
                return;
            }
            if(!repeat){
                this.team[this.current_team].warriors[this.current_warrior] = this.all_warriors[index];
                // this.current_team.warriors[warrior_index] = target;
                

                //修改脏位
                this.dirty = true;
                //关闭遮罩层
                this.$refs.select_warrior_container.classList.remove('active');

                //是dom页面更新
                this.fresh_flag = false;
                this.fresh_flag = true;
                
            }else{
                // this.$bus.$emit('alert','存在相同武将');
            }
            
            
            
        },
        //卸载武将
        UnloadWarrior(){
            this.dirty = true;
            this.status.isUnloadWarrior = false;
            this.team[this.current_team].warriors[this.current_warrior] = {
                            id:'',
                            name:'',
                            red:0,
                            camp:'',
                            att:"",
                            arms:'',
                            isUp:false,
                            isUpWork:false,
                            isUpArms:false,
                            img:{
                                normal:"",
                                middle:"",
                                head:"",
                            },
                            skill:[],
            };
            this.team[this.current_team].skills[this.current_warrior][0] = new TeamSkills();
            this.team[this.current_team].skills[this.current_warrior][1] = new TeamSkills();
            //修改脏位
            this.dirty = true;
        },
        //加载界面时更新战法数据
        UpDataSkills(){
            if(localStorage.getItem('stzb_my_skill')){
                //如果有被保存的技能
                let get = JSON.parse(localStorage.getItem('stzb_my_skill'));
                if(get.length > 0){
                    //只有有数据的时候才执行
                    let index = 0;
                    for(let i = 0;i<this.command_skills.length;i++){
                        if(this.command_skills[i].id == get[index]){
                            index ++;//匹配到一个，index指针后移
                            this.command_skills[i].acquired = true;
                            if(index == get.length){
                                //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
                                return;
                            }
                        }
                    }
                    for(let i = 0;i<this.active_skills.length;i++){
                        if(get[index]>= 7000){
                            //说明没有主动战法
                            i = this.active_skills.length-1;
                            break;
                        }
                        if(this.active_skills[i].id == get[index]){
                            index ++;//匹配到一个，index指针后移
                            this.active_skills[i].acquired = true;
                            if(index == get.length){
                                //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
                                return;
                            }
                        }
                    }
                    for(let i = 0;i<this.passive_skills.length;i++){
                        if(get[index]>= 10000){
                            //说明没有被动战法
                            i = this.passive_skills.length-1;
                            break;
                        }
                        if(this.passive_skills[i].id == get[index]){
                            index ++;//匹配到一个，index指针后移
                            this.passive_skills[i].acquired = true;
                            if(index == get.length){
                                //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
                                return;
                            }
                        }
                    }
                    for(let i = 0;i<this.combo_skills.length;i++){
                        if(this.combo_skills[i].id == get[index]){
                            index ++;//匹配到一个，index指针后移
                            this.combo_skills[i].acquired = true;
                            if(index == get.length){
                                //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
                                return;
                            }
                        }
                    }
                }
                
            }
        },
         //插入队列
        InsertNodeIntoTeamQueue(item,queue){
            if(queue.length == 0){
                queue.push(item);
                return;
            }else{
                for(let i =0 ;i< queue.length;i++){
                    if(item.warrior_id <= queue[i].warrior_id){
                        queue.splice(i,0,item);
                        return;
                        //插入元素
                    }
                    if(i == queue.length - 1){
                        queue.push(item);
                        return;
                    }
                }
            }
        },
        //保存队伍数据
        SaveTeamData(){
            if(!this.dirty){
                //这个页面没有被修改过，不需要保存数据
                return;
            }
            let queue = [];//准备一个空队列
            //保存队伍数据
            for(let i=0;i<this.team.length;i++){
                for(let j=0;j<this.team[i].warriors.length;j++){
                    if(this.team[i].warriors[j].id){
                        //说明该位置有武将
                        let item = new TeamWarriorNode(false,i,j,this.team[i].warriors[j].id)
                        this.InsertNodeIntoTeamQueue(item,queue)
                    }else{
                        let item = new TeamWarriorNode(true,i,j,this.team[i].warriors[j].id)
                        this.InsertNodeIntoTeamQueue(item,queue)
                    }
                    
                }
            }
            let skill_arr = [];
            for(let i=0;i<this.team.length;i++){
                for(let j=0;j<3;j++){
                    for(let k=0;k<2;k++){
                        let phrase = this.team[i].skills[j][k];
                        skill_arr.push(phrase);
                    }
                }
            }
            
            localStorage.setItem('stzb_my_team',JSON.stringify(queue));
            localStorage.setItem('stzb_my_skill_model',JSON.stringify(skill_arr));
            this.$bus.$emit('alert','保存成功');
            this.dirty = false;
        },
        //打开战法安装面板
        LoadSkillIntoWarrior(l_index,s_index){
            this.skill_current_warrior = l_index;
            this.skill_current_position = s_index;
            this.$refs.skill_container.classList.add('emerge');
            this.UpDataSkills();
        },
        //卸下战法
        UnloadThisSkill(){
            this.refresh_skill = false;
            this.team[this.current_team].skills[this.skill_current_warrior][this.skill_current_position] = new TeamSkills();
            this.refresh_skill = true;
            this.$refs.skill_container.classList.remove('emerge');
        },
        //加载界面时更新数据
        UpDataSkills(){
            if(localStorage.getItem('InfiniteBorderApp')){
                //如果有被保存的技能
                let iba = JSON.parse(localStorage.getItem('InfiniteBorderApp'));
                let iba_index = 0;
                for(let i=0;i<iba.account.length;i++){
                    if(iba.account[i].id == iba.current_id){
                        iba_index = i;
                    }
                }
                let get = iba.account[iba_index].tactics;
                if(get.length > 0){
                    //只有有数据的时候才执行
                    let index = 0;
                    for(let i = 0;i<this.command_skills.length;i++){
                        if(this.command_skills[i].id == get[index]){
                            index ++;//匹配到一个，index指针后移
                            this.command_skills[i].acquired = true;
                            if(index == get.length){
                                //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
                                return;
                            }
                        }
                    }
                    for(let i = 0;i<this.active_skills.length;i++){
                        if(get[index]>= 7000){
                            //说明没有主动战法
                            i = this.active_skills.length-1;
                            break;
                        }
                        if(this.active_skills[i].id == get[index]){
                            index ++;//匹配到一个，index指针后移
                            this.active_skills[i].acquired = true;
                            if(index == get.length){
                                //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
                                return;
                            }
                        }
                    }
                    for(let i = 0;i<this.passive_skills.length;i++){
                        if(get[index]>= 10000){
                            //说明没有被动战法
                            i = this.passive_skills.length-1;
                            break;
                        }
                        if(this.passive_skills[i].id == get[index]){
                            index ++;//匹配到一个，index指针后移
                            this.passive_skills[i].acquired = true;
                            if(index == get.length){
                                //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
                                return;
                            }
                        }
                    }
                    for(let i = 0;i<this.combo_skills.length;i++){
                        if(this.combo_skills[i].id == get[index]){
                            index ++;//匹配到一个，index指针后移
                            this.combo_skills[i].acquired = true;
                            if(index == get.length){
                                //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
                                return;
                            }
                        }
                    }
                }
            }
            else{
                for(let i=0;i<this.command_skills.length;i++){
                    this.command_skills[i].acquired = false;
                }
                for(let i=0;i<this.active_skills.length;i++){
                    this.active_skills[i].acquired = false;
                }
                for(let i=0;i<this.combo_skills.length;i++){
                    this.combo_skills[i].acquired = false;
                }
                for(let i=0;i<this.passive_skills.length;i++){
                    this.passive_skills[i].acquired = false;
                }
                this.dirty = false;
            }
            // if(localStorage.getItem('stzb_my_skill')){
            //     //如果有被保存的技能
            //     let get = JSON.parse(localStorage.getItem('stzb_my_skill'));
            //     if(get.length > 0){
            //         //只有有数据的时候才执行
            //         let index = 0;
            //         for(let i = 0;i<this.command_skills.length;i++){
            //             if(this.command_skills[i].id == get[index]){
            //                 index ++;//匹配到一个，index指针后移
            //                 this.command_skills[i].acquired = true;
            //                 if(index == get.length){
            //                     //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
            //                     return;
            //                 }
            //             }
            //         }
            //         for(let i = 0;i<this.active_skills.length;i++){
            //             if(get[index]>= 7000){
            //                 //说明没有主动战法
            //                 i = this.active_skills.length-1;
            //                 break;
            //             }
            //             if(this.active_skills[i].id == get[index]){
            //                 index ++;//匹配到一个，index指针后移
            //                 this.active_skills[i].acquired = true;
            //                 if(index == get.length){
            //                     //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
            //                     return;
            //                 }
            //             }
            //         }
            //         for(let i = 0;i<this.passive_skills.length;i++){
            //             if(get[index]>= 10000){
            //                 //说明没有被动战法
            //                 i = this.passive_skills.length-1;
            //                 break;
            //             }
            //             if(this.passive_skills[i].id == get[index]){
            //                 index ++;//匹配到一个，index指针后移
            //                 this.passive_skills[i].acquired = true;
            //                 if(index == get.length){
            //                     //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
            //                     return;
            //                 }
            //             }
            //         }
            //         for(let i = 0;i<this.combo_skills.length;i++){
            //             if(this.combo_skills[i].id == get[index]){
            //                 index ++;//匹配到一个，index指针后移
            //                 this.combo_skills[i].acquired = true;
            //                 if(index == get.length){
            //                     //表示说get数组的最后一个数据已经匹配完成，那么就可以终止运行
            //                     return;
            //                 }
            //             }
            //         }
            //     }
                
            // }
        },
        //确定安装这个战法
        LoadThisSkill(type,index){
            this.dirty = true;
            let item;
            if(type == 'passive'){
                item = this.passive_skills[index];
            }else if(type == 'command'){
                item = this.command_skills[index];
            }else if(type == 'combo'){
                item = this.combo_skills[index];
            }else{
                item = this.active_skills[index];
            }
            if(!item.acquired){
                //表示该战法还没有被学习
                this.$bus.$emit('alert','该战法未学习');
                return false;
            }
            for(let i=0;i<this.team.length;i++){
                for(let j=0;j<3;j++){
                    for(let k=0;k<2;k++){
                        if(this.team[i].skills[j][k].id == item.id && item.learned.have != 2){
                            this.$bus.$emit('alert','已存在相同战法');
                            return;
                        }
                    }
                }
            }
            this.refresh_skill = false;
            this.team[this.current_team].skills[this.skill_current_warrior][this.skill_current_position].getValue(item,type,item.learned.have);
            this.refresh_skill = true;
            this.$refs.skill_container.classList.remove('emerge');

        },
        //删除队伍,打开删除提示面板
        IsDeleteThisTeam(){
            this.status.isDeleteThisTeam = true;
        },
        //删除队伍
        DeleteThisTeam(){
            //先关闭武将编辑界面
            this.$refs.team_info.classList.add('active');
            //删除队伍
            if(this.team.length != 1){
                this.team.splice(this.current_team,1);
                this.current_team = 0;
                this.dirty = true;
            }else{
                //队伍不足一队，无法删除
                this.$bus.$emit('alert','队伍至少留一队');
            }
            
            //关闭提示框
            this.status.isDeleteThisTeam = false;
        },
        TurnToSimBattle(){
            let camp = [];
            let red_arr = [];
            for(let i=0;i<3;i++){
                //存在空武将
                if(this.team[this.current_team].warriors[i].name == "" || this.team[this.current_team].warriors[i].name == null){
                    this.$bus.$emit('alert','武将配置不全')
                    return ;
                }
                let empty = {
                    TACTICS_STACK: [],
                    arms: this.team[this.current_team].warriors[i].arms,
                    camp : this.team[this.current_team].warriors[i].camp,
                    current_advanced : '',
                    id : this.team[this.current_team].warriors[i].id,
                    img : this.team[this.current_team].warriors[i].img,
                    name : this.team[this.current_team].warriors[i].name,
                    sign : this.team[this.current_team].warriors[i].sign
                }
                red_arr.push(this.team[this.current_team].warriors[i].red);
                //填充一个空的
                let skill1 = {
                    name : "",
                    quality : "",
                    sign : "",
                    type : ""
                }
                empty.TACTICS_STACK.push(skill1);
                //放入武将的三个战法
                for(let j=0;j<2;j++){
                    if(this.team[this.current_team].skills[i][j].name == "" || this.team[this.current_team].skills[i][j].name == null){
                        this.$bus.$emit('alert','战法配置不全')
                        return ;
                    }
                    let skill = {
                        name : this.team[this.current_team].skills[i][j].name,
                        quality : this.team[this.current_team].skills[i][j].quality,
                        sign : this.team[this.current_team].skills[i][j].sign,
                        type : this.team[this.current_team].skills[i][j].type
                    }
                    empty.TACTICS_STACK.push(skill);
                }
                camp.push(empty);
            }
            // console.log('camp',this.team[this.current_team]);
            this.$refs.team_info.classList.add('active');
            this.OpenOrClose(false);
            this.$bus.$emit('EnterSimBattle');
            this.$bus.$emit('LoadMyTeam',camp,red_arr);
        }

    },
    mounted(){
        this.$bus.$on('EnterMyTeam',this.OpenOrClose);

        //关闭编辑页面
        this.$bus.$on('CloseEditPanel',this.CloseEditPanel);
        //保存队伍数据
        this.$bus.$on('SaveTeamData',this.SaveTeamData);
        //更新页面
        this.$bus.$on('UpdateTeamInterface',this.UpdateIntoInterface);

        this.UpdateIntoInterface();
    }
}
</script>

