<template>
    <div class="tactics_info_panel cover">
        <div class="mask cover"></div>
        <div class="container">
            <div class="close" ref="close"
            @touchstart="$refs.close.classList.add('click')"
            @touchend="$refs.close.classList.remove('click')" @click="CloseThisPanel"></div>
            <div class="texture"></div>
            <div class="tactics_img">
                <img src="../../assets/ui/ui (92).png" alt="" v-if="current_tactics.main_type == 'command'" class="tactics_img_icon">
                <img src="../../assets/ui/ui (93).png" alt="" v-if="current_tactics.main_type == 'active'" class="tactics_img_icon">
                <img src="../../assets/ui/ui (94).png" alt="" v-if="current_tactics.main_type == 'combo'" class="tactics_img_icon">
                <img src="../../assets/ui/ui (91).png" alt="" v-if="current_tactics.main_type == 'passive'" class="tactics_img_icon">
            </div>
            <div class="tactics_name">战法名称&nbsp;&nbsp;<span>{{current_tactics.name}}</span></div>
            <div class="tactics_quality">战法品质&nbsp;&nbsp;<span>{{current_tactics.quality}}级</span></div>
            <div class="tactics_odd">发动几率&nbsp;&nbsp;<span>{{current_tactics.odd}}%</span></div>
            <div class="tactics_type" v-if="current_tactics.main_type == 'command'">战法类型&nbsp;&nbsp;<span>指挥</span></div>
            <div class="tactics_type" v-if="current_tactics.main_type == 'active'">战法类型&nbsp;&nbsp;<span>主动</span></div>
            <div class="tactics_type" v-if="current_tactics.main_type == 'combo'">战法类型&nbsp;&nbsp;<span>追击</span></div>
            <div class="tactics_type" v-if="current_tactics.main_type == 'passive'">战法类型&nbsp;&nbsp;<span>被动</span></div>
            <div class="tactics_att">有效距离&nbsp;&nbsp;<span>{{current_tactics.att}}</span></div>
            <div class="tactics_introduce">{{current_tactics.info}}</div>

            <!-- 卸载战法按钮 -->
            <div class="unload_tactics_btn" ref="unload_tactics_btn"
            @touchstart="$refs.unload_tactics_btn.classList.add('click')"
            @touchend="$refs.unload_tactics_btn.classList.remove('click')"
            @click="UnloadThisTactics" v-if="current_tactics_id != 0">卸载战法</div>
        </div>
    </div>
</template>

<script>
export default {
    name : 'TacticsInfoVue',
    props : ['current_tactics','current_tactics_id'],
    data(){
        return{

        }
    },
    methods:{
        CloseThisPanel(){
            this.$bus.$emit('TacticsInfoPanel',false);
        },
        //卸载战法
        UnloadThisTactics(){
            this.$bus.$emit("UnloadTactics");
            this.CloseThisPanel();
        }
    },
    mounted(){

    }
}
</script>

<style lang="less">
.tactics_info_panel{
    z-index: 10;
    .mask{
        background: rgba(0, 0, 0, 0.601);
    }
    .container{
        top: 50%;
        margin: -45vw 0 0 0;
        width: 100vw;
        height: 90vw;
        background: url('../../assets/background/bg (7).png')rgb(43, 49, 52) no-repeat;
        background-size: 100% 100%;
        .texture{
            left: 0; top: 5vw;
            width: 50vw;height: 10vw;
            background: url('../../assets/background/bg (5).png')no-repeat;
            background-size: 100% 100%;
        }
        .close{
            right: 6vw; top: 7vw;
            width: 7vw;height: 7vw;
            background: url('../../assets/ui/ui (85).png')no-repeat;
            background-size: 100% 100%;
        }
        .tactics_img{
            left: 10vw; top: 17vw;
            width: 16vw;height: 16vw;
            background: rgb(75, 75, 75);
            border-radius: 50%;
            box-shadow: 0 0 5px black;
            .tactics_img_icon{
                position: absolute; left: 50%; top : 50%;
                transform : translate(-50%,-50%);
                width: 90%;height: 90%;
            }
        }
        .tactics_name,.tactics_quality,.tactics_odd,.tactics_type,.tactics_att{
            left: 30vw; top: 19vw;
            height: 5vw; width: 28vw;
            // background: #000;
            font-size: 2.8vw;
            text-align: left;
            line-height: 5vw;
            span{
                font-size: 2.8vw;
                color: rgb(234, 234, 234);
                line-height: 5vw;
            }
        }
        .tactics_type{
            top: 25vw;
        }
        .tactics_quality{
            left: 60vw;
        }
        .tactics_odd{
            top: 25vw;left: 60vw;
        }
        .tactics_att{
            top: 31vw;
        }
        .tactics_introduce{
            left: 10vw; top: 40vw;
            width: 60vw;height: 25vw;
            color: rgb(234, 234, 234);
            font-size: 2.8vw;
            padding: 5vw 10vw;
            overflow: scroll;
            background: rgba(0, 0, 0, 0.158);
        }
        .unload_tactics_btn{
            left: 50%; bottom: 5vw;
            width: 24vw;height: 6vw;
            line-height: 6vw;
            font-weight: bold;
            color: black;
            margin: 0 0 0 -12vw;
            background: url('../../assets/ui/ui (10).png')no-repeat;
            background-size: 100% 100%;
        }
        .close.click,.unload_tactics_btn.click{
            transform: scale(.8);
        }
        
    }
}
</style>