class BattleFormation{
    constructor(){
        this.id = 0;
        this.name = "";
        this.sign = "";
        this.camp = "";
        this.arms = "";
        this.current_advanced = "";
        this.img = "";
        this.TACTICS_STACK = [
            {
                name : "",
                quality : "",
                sign : "",
                type : "",

            },
            {
                name : "",
                quality : "",
                sign : "",
                type : "",
            },
            {
                name : "",
                quality : "",
                sign : "",
                type : "",
            },
        ]
    }

    GetInfo(item){
        this.id = item.id;
        this.name = item.name;
        this.sign = item.sign;
        this.camp = item.camp;
        this.arms = item.arms;
        this.current_advanced = item.current_advanced;
        this.img = item.img;
        this.TACTICS_STACK = [
            {
                name : item.TACTICS_STACK[0].name,
                quality : item.TACTICS_STACK[0].quality,
                sign : item.TACTICS_STACK[0].target_type,
                type : item.TACTICS_STACK[0].main_type,

            },
            {
                name : item.TACTICS_STACK[1].name,
                quality : item.TACTICS_STACK[1].quality,
                sign : item.TACTICS_STACK[1].target_type,
                type : item.TACTICS_STACK[1].main_type,
            },
            {
                name : item.TACTICS_STACK[2].name,
                quality : item.TACTICS_STACK[2].quality,
                sign : item.TACTICS_STACK[2].target_type,
                type : item.TACTICS_STACK[2].main_type,
            },
        ]
    }
    CopyInfo(item){
        this.id = item.id;
        this.name = item.name;
        this.sign = item.sign;
        this.camp = item.camp;
        this.arms = item.arms;
        this.current_advanced = item.current_advanced;
        this.img = item.img;
        this.TACTICS_STACK = [
            {
                name : item.TACTICS_STACK[0].name,
                quality : item.TACTICS_STACK[0].quality,
                sign : item.TACTICS_STACK[0].sign,
                type : item.TACTICS_STACK[0].type,

            },
            {
                name : item.TACTICS_STACK[1].name,
                quality : item.TACTICS_STACK[1].quality,
                sign : item.TACTICS_STACK[1].sign,
                type : item.TACTICS_STACK[1].type,
            },
            {
                name : item.TACTICS_STACK[2].name,
                quality : item.TACTICS_STACK[2].quality,
                sign : item.TACTICS_STACK[2].sign,
                type : item.TACTICS_STACK[2].type,
            },
        ]
    }
}

export default BattleFormation;