import Vue from "vue";
import Vuex from 'vuex';

import Warrior from '../mixin/structure/WarriorBattle'

//Vue实例使用Vuex
Vue.use(Vuex);

const store = new Vuex.Store({
    //公共数据存储区
    state(){
        return{
            teams : [
                //我的队伍
                {} =  [
                    {} = new Warrior(2,0),
                    {} = new Warrior(1,0),
                    {} = new Warrior(0,0),//前锋
                ],
                //敌方的队伍
                {} = [
                    {} = new Warrior(2,1),//前锋
                    {} = new Warrior(1,1),
                    {} = new Warrior(0,1),
                ]
            ],
        }
    },
    getters:{

    },
    //定义方法
    mutations:{
        CommitSimBattleResultTeams(state,arr){
            state.teams = arr;
        }
    },
    //操作异步
    actions:{

    },
    modules:{

    }
})


export default store;

