<template>
  <!-- app应用 -->
  <div class="app">
    <!-- 主组件 -->
    <Main/>

    <!-- <div class="click_animation" ref="clickImg" v-show="isClick"></div> -->
    
        <!-- 状态提示 -->
        <!-- <transition-group name="bottom_to_top"> -->
        <div class="message_alert" v-for="a in alert_arr" :key="a.id">
          <div class="bg cover"></div>
          <div class="text cover">{{a.msg}}</div>
        </div>
        <!-- </transition-group> -->
    <!-- </div> -->

    
  </div> 
</template>

<script>
import arr from './mixin/warrior/test.js'
import Main from './components/Main.vue'
export default {
  name : 'App',
  components : {
    Main,
  },
  data(){
    return{
      alert_arr : [],
    }
  },
  methods:{
    alertPanel(info){
        let alert_item = {
          id : Math.floor(Math.random()*9999),
          msg :info,
        }
        this.alert_arr.push(alert_item);
        setTimeout(()=>{
            if(this.alert_arr.length > 0){
              this.alert_arr.shift();
            }
        },1500)
        
    },
    Count(){
      let node = document.querySelector('.a');
      let all_str = "";
      let num = 1;
      for(let i in arr){
          if(arr[i]){
              let str  = '{' + "<br>" 
              + `&nbsp;&nbsp;&nbsp;&nbsp;"id" : ${i},` + '<br>'
              + `&nbsp;&nbsp;&nbsp;&nbsp;"name" : "${arr[i].name}",` + '<br>'
              + `&nbsp;&nbsp;&nbsp;&nbsp;"quality" : "${arr[i].quality}",` + '<br>'
              + `&nbsp;&nbsp;&nbsp;&nbsp;"sign" : ` + this.M(arr[i]) + ',<br>'
              + `&nbsp;&nbsp;&nbsp;&nbsp;"describe" : "${arr[i].tactics ? arr[i].tactics.info : ""}",` + '<br>'
              + `&nbsp;&nbsp;&nbsp;&nbsp;"odds" : ${arr[i].tactics ? arr[i].tactics.odd : 0},` + '<br>'
              + `&nbsp;&nbsp;&nbsp;&nbsp;"range" : ${arr[i].tactics ? arr[i].tactics.att : 3},` + '<br>'
              + `&nbsp;&nbsp;&nbsp;&nbsp;"arms" : [` + this.Arms(arr[i]) + '],<br>'
              + `&nbsp;&nbsp;&nbsp;&nbsp;"tgt_number" : ${arr[i].tactics ? arr[i].tactics.target_number : "0"}`  + '<br>'
              + "},<br>"
              all_str += str;
              num += 1;
          }
      }
      node.innerHTML = all_str;
    },
    M(target){
      if(target.sign == 'none') return null;
      else return '"' + target.sign + '"';
    },
    Arms(target){
      if(target.tactics){
        let str = '';
        for(let i in target.tactics.arms){
          str += '"' + target.tactics.arms[i] + '"'
          if(i != target.tactics.arms.length-1) str += ','
        }
        return str;
      }
      else return ''
    }
  },
  mounted(){
    //提示框的事件总线
    this.$bus.$on('alert',this.alertPanel)
    // this.Count();
  }
}
</script>

